<template>
    <div>
        <router-view/>
        <van-nav-bar
                title="实名认证"
                left-text="返回"
                class="header_title"
                @click-left="onClickLeft"
                safe-area-inset-top
                placeholder
                :fixed="fixed"
        />
        <div class="warp-loading" v-if="upImgLoading">
            <van-loading class="upFile" size="24px" vertical color="#0094ff">
                <span style="color:#0094ff;font-size:12px">图片上传中...</span>
            </van-loading>
        </div>
        <div style="height: 1rem;"></div>
        <van-steps :active="active" active-color="#38f">
            <van-step>填写资料</van-step>
            <van-step>上传照片</van-step>
            <van-step>系统审核</van-step>
        </van-steps>
        <van-form @submit="nextStep" v-if="active===0">
            <van-field
                    v-model="validatorRules.sSurname"
                    name="姓氏"
                    label="姓氏"
                    placeholder="姓氏"
                    :maxlength="20"
                    :rules="[{ required: true, message: '请填写姓氏' }]"
            />
            <van-field
                    v-model="validatorRules.sName"
                    name="名字"
                    label="名字"
                    placeholder="名字"
                    :maxlength="20"
                    :rules="[{ required: true, message: '请填写名字' }]"
            />
            <van-field
                    v-model="validatorRules.sCertificateNo"
                    name="证件号码"
                    label="证件号码"
                    placeholder="证件号码"
                    :rules="[{ validator:idCard, message: '请填写证件号码' }]"
            />

            <div class="cert-foot-bnt">
                <van-button round block type="info" native-type="submit">
                    下一步
                </van-button>
            </div>
        </van-form>
        <div v-if="active===1">
            <div class="cert-box">
                <van-row type="flex" justify="space-around">
                    <van-col>
                        <div class="imgs-box">
                            <p v-if="imageUrlFront==null">
                                <van-image width="200" :src="require('../../assets/img/sfz1.png')">
                                    <template v-slot:loading>
                                        <van-loading type="spinner" size="20" />
                                    </template>
                                </van-image>
                            </p>
                            <p v-else><van-image style="height:200px" :src="imageUrlFront"/></p>
                        </div>
                        <van-button icon="back-top" type="info" size="mini" style="width:50%;margin:0 auto;padding:15px 0" round block @click="showTakePhoto(1)" native-type="button">{{imageUrlFront?'重新上传正面照':'上传身份证正面照'}}</van-button>
                    </van-col>
                </van-row>
            </div>
            <div class="cert-box">
                <van-row type="flex" justify="space-around">
                    <van-col>
                        <div class="imgs-box">
                            <p v-if="imageUrlBack==null">
                                <van-image width="200" :src="require('../../assets/img/sfz2.png')">
                                    <template v-slot:loading>
                                        <van-loading type="spinner" size="20" />
                                    </template>
                                </van-image>
                            </p>
                            <p v-else><van-image style="height:200px" :src="imageUrlBack"/></p>
                        </div>
                        <van-button icon="back-top" type="info" size="mini" style="width:50%;margin:0 auto;padding:15px 0" round block @click="showTakePhoto(2)" native-type="button">{{imageUrlFront?'重新上传反面照':'上传身份证反面照'}}</van-button>
                    </van-col>
                </van-row>
            </div>
            <div class="cert-box clearfix">
                <van-row type="flex" justify="space-around">
                    <van-col>
                        <div class="imgs-box">
                            <p v-if="imageUrlHand==null">
                                <van-image width="200" :src="require('../../assets/img/sfz3.png')">
                                    <template v-slot:loading>
                                        <van-loading type="spinner" size="20" />
                                    </template>
                                </van-image>
                            </p>
                            <p v-else><van-image style="height:200px" :src="imageUrlHand"/></p>
                        </div>
                        <van-button icon="back-top" type="info" size="mini" style="width:50%;margin:0 auto;padding:15px 0" round block @click="showTakePhoto(3)" native-type="button">{{imageUrlHand?'重新上传手持身份证':'上传手持身份证'}}</van-button>
                    </van-col>
                </van-row>
            </div>
            <br>
            <div class="cert-foot-bnt clearfix">
                <van-row type="flex" justify="space-around">
                    <van-col style="width: 45%;">
                        <van-button round block type="default" v-if="active>0" @click="upStep">
                            上一步
                        </van-button>
                    </van-col>
                    <van-col style="width: 45%;">
                        <van-button round block type="info" @click="nextStep">
                            提交审核
                        </van-button>
                    </van-col>
                </van-row>
                <div style="height:3em;"></div>
            </div>
        </div>

        <div v-if="active===2">
            <div class="cert-box p-mt">
                <van-row type="flex" justify="space-around">
                    <van-col>
                        <van-row type="flex" class="card-box" justify="center">
                            <van-col>
                                <van-image width="40" :src="require('../../assets/img/ic_transaction_success.png')"/>
                            </van-col>
                        </van-row>
                        <br>
                        <van-row type="flex" justify="center">
                            <van-col>系统审核中，2个工作日内完成审核（节假日顺延）</van-col>
                        </van-row>
                    </van-col>
                </van-row>
            </div>
            <div class="cert-foot-bnt">
                <van-button round block type="info" @click="success">
                    完成
                </van-button>
            </div>
        </div>

        <van-action-sheet
            v-model="show"
            cancel-text="取消"
            close-on-click-action
            :actions="actions"
            @select="takePhoto"
        />
    </div>
</template>

<script>
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {ossUrl,getSignedUrl} from '@/api/oss'
    import {addCertificate} from '@/api/udc'
    import moment from 'dayjs'
    import { identityCodeValid } from '@/utils/validate'

    const file_url = process.env.VUE_APP_FILE_API_URL
    export default {
        name: 'authsetup',
        components: {
            [Toast.name]: Toast,
        },
        data() {
            return {
                ossUrl,
                type: '',
                selKey: '1',
                active: 0,
                showPicker:false,
                fixed: true,
                show: false,
                countryColumns:[],
                showPhotoType: 1,
                validatorRules: {
                    sSurname: '',
                    sName: '',
                    sCertificateNo: '',
                    iCountryId: ''
                },
                actions: [
                    {name: '从相册选择'},
                    {name: '拍照'},
                ],
                imageUrlFront: null,
                imageUrlBack: null,
                imageUrlHand: null,
                sFrontImg: '',
                sBackImg: '',
                sHandImg: '',
                ssk: '',
                upImgLoading: false
            }
        },
        mounted() {

        },
        created() {
            this.ssk = this.$ls.get('SSK')
            let that = this;
            this.type = this.$route.query.type;
            if (this.$route.query.type === '1') this.selKey = '1';
            if (this.$route.query.type === '2') this.selKey = '2';
            if (this.$route.query.type === '3') this.selKey = '3';

           let clist =  this.userCountry()||[];
           clist.forEach(item => {
               that.countryColumns.push( item.sCountryName);
            })
        },
        methods: {
            moment,
            ...mapGetters(['isLogin', 'userName', 'cellphone', 'email','userCountry']),
            ...mapActions(['GetUserExten']),
            idCard(value,rule){
                if (!value) {
                    rule.message = '请填写证件号码'
                    return false
                }else if(!identityCodeValid(value)){
                    rule.message = '请输正确格式的证件号码'
                    return false
                }
            },
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            nextStep() {
                if (this.active === 1) {
                    //上传身份证
                    if (this.imageUrlFront == null || this.imageUrlBack == null || this.imageUrlHand == null) {
                        this.$toast.fail("请正确上传身份证照片");
                        return;
                    }
                    if (this.sFrontImg === '') {
                        this.$toast.fail('请上传证件正面照！');
                        return;
                    }
                    if (this.sBackImg === '') {
                        this.$toast.fail('请上传证件反面照！');
                        return;
                    }
                    if (this.sHandImg === '') {
                        this.$toast.fail('请上传手持证件照！');
                        return;
                    }
                    this.validatorRules.iCountryId = 39;
                    const params = {
                        sFrontImg: this.sFrontImg,
                        sBackImg: this.sBackImg,
                        sHandImg: this.sHandImg,
                        sEmail: this.email(),
                        sCellphone: this.cellphone(),
                        ...this.validatorRules,
                    }
                    //alert(JSON.stringify(params))
                    addCertificate(params)
                        .then(() => {
                            //去到
                            this.active = this.active + 1;
                            //   this.$router.replace({name:"authsetupsucess",query:{message: '系统审核中，2个工作日内完成审核（节假日顺延）'}});
                        })
                        .catch((e) => {
                            //alert(JSON.stringify(e))
                        })
                }else{
                    this.active = this.active + 1;
                }

            },
            upStep() {
                this.active = this.active - 1;
            },
            showTakePhoto(type) {
                this.show = true;
                this.showPhotoType = type;
            },
            takePhoto(action, index) {
                let that = this;

                function setOptions(srcType) {
                    let options = {
                        // Some common settings are 20, 50, and 100
                        quality: 80,
                        destinationType: Camera.DestinationType.FILE_URI,
                        // In this app, dynamically set the picture source, Camera or photo gallery
                        sourceType: srcType,
                        encodingType: Camera.EncodingType.JPEG,
                        mediaType: Camera.MediaType.PICTURE,
                        correctOrientation: true  //Corrects Android orientation quirks
                    }
                    return options;
                }

                if (index === 0) {
                    let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
                    let options = setOptions(srcType);
                    navigator.camera.getPicture(function cameraSuccess(imageUri) {
                        that.fileUpload(imageUri)

                    }, function cameraError(error) {
                        console.debug("Unable to obtain picture: " + error, "app");

                    }, options);
                }
                if (index === 1) {
                    let srcType = Camera.PictureSourceType.CAMERA;//拍照
                    let options = setOptions(srcType);
                    navigator.camera.getPicture(function cameraSuccess(imageUri) {
                        that.fileUpload(imageUri)

                    }, function cameraError(error) {
                        console.debug("Unable to obtain picture: " + error, "app");

                    }, options);
                }
            },
            // 使用cordova FileUpload上传图片
            fileUpload: function (imageURI) {
                function getCaption(obj, state) {
                    let index = obj.lastIndexOf("?");
                    if(index>0){
                        if (state == 0) {
                            obj = obj.substring(0, index);
                        } else {
                            obj = obj.substring(index + 1, obj.length);
                        }
                        return obj;
                    }else{
                        return obj;
                    }
                }

                imageURI = getCaption(imageURI, 0)

                let that = this
                that.upImgLoading = true
                let FileUploadOptions = window.FileUploadOptions
                let FileTransfer = window.FileTransfer
                let options = new FileUploadOptions()
                options.fileKey = 'file'
                let path = imageURI.substr(imageURI.lastIndexOf('/') + 1);
                options.fileName = path

                options.mimeType = 'image/jpeg'
                let headers = {'Authorization': ' ' + this.ssk}
                options.headers = headers;
                let ft = new FileTransfer()

                ft.upload(imageURI, encodeURI(this.ossUrl.PrivateUpload), function (data) {
                    //alert(resString)
                    let resString = data.response
                    console.log(resString)
                    let resObject = JSON.parse(resString) // 字符串转对象
                    if (resObject.iRspCode === 0) {
                        if (that.showPhotoType === 1) {
                            getSignedUrl(resObject.data.vUrlPath[0]).then(res=>that.imageUrlFront = res)
                            that.sFrontImg = resObject.data.vUrlPath[0];
                            that.upImgLoading = false
                        }
                        if (that.showPhotoType === 2) {
                            getSignedUrl(resObject.data.vUrlPath[0]).then(res=>that.imageUrlBack = res)
                            that.sBackImg = resObject.data.vUrlPath[0];
                            that.upImgLoading = false
                        }
                        if (that.showPhotoType === 3) {
                            getSignedUrl(resObject.data.vUrlPath[0]).then(res=>that.imageUrlHand = res)
                            that.sHandImg = resObject.data.vUrlPath[0];
                            that.upImgLoading = false
                        }
                        //alert(JSON.stringify(resObject.data.vUrlPath[0]))
                    } else {
                        //alert(resObject.msg)
                        that.upImgLoading = false
                        that.$toast.fail('上传出错稍后重试！')
                    }
                }, function (err) {
                    console.log(err)
                    //alert(JSON.stringify(err))
                    that.upImgLoading = false
                    that.$toast.fail('上传出错稍后重试！')
                }, options)
            },
            success() {
                this.$router.replace({name: "security"});
            }
        }
    }
</script>

<style lang="less" scoped>
    .f_header {
        font-weight: 500;
        font-size: 16px;
        margin-bottom: 5px;
        color: darkblue;
    }

    .card-box {
        margin-top: 20px;
        vertical-align: middle;
    }

    .user_header {
        position: relative;
        background-repeat: no-repeat;
        background-size: cover;
        height: 145px;
        box-sizing: border-box;
        text-align: left;
        color: #fff;
        padding-top: 30px;

        .text {
            margin-left: 20px;
        }

        .flex-type {
            margin-bottom: 20px;
        }
    }

    .fin_list {
        position: relative;
        text-align: left;
        background-color: white;
        margin-left: 20px;
        margin-bottom: 20px;
    }

    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }

    .user_avatar {
        margin-bottom: 10px;

        img {
            border: 0;
            border-radius: 50%;
        }
    }
    .clearfix:after{content:””; display:block; clear:both;}
    .cert-box{
        margin: 0 1px;
        margin-bottom:30px
        //background-color: #F7F6FB
    }
    .p-mt{
        padding-top: 30px;
    }
    .cert-foot-bnt{
        margin: 30px;
    }
    .imgs-box{
        margin-top: 18px;
        p{
            //border: 1px rgb(233, 238, 245) dashed;
            border-radius: 5px;
            width: 310px;
            height: 220px;
            color: #c5c9d0;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            .van-image__img{
                height: 200px;
            }
        }
    }
</style>
