<template>
    <div>
        <van-nav-bar
            title="广告详情"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :fixed="true"
        />
        <div class="ads-list-cont">
            <div class="tinfo">
                <div class="ads-list" v-if="dataSource.length>0">
                    <div class="info-top gflex border-bm border-top">
                        <div class="box__hd">
                            <!-- <img class="avatar-img" v-if="dataSource[0].sAvatar" :src="dataSource[0].sAvatar" alt=""> -->
                            <van-image class="avatar-img" v-if="dataSource[0].sAvatar" :src="dataSource[0].sAvatar" :error-icon="require('../../assets/img/avatar_default.png')">
                                <template v-slot:loading>
                                    <van-loading type="spinner" size="20" />
                                </template>
                            </van-image>
                            <span v-else class="avatar-icon" :class="bgidCladd">{{dataSource[0].sNickName.split('')[0]}}</span>
                        </div>
                        <div class="box__bd">
                            <div class="box__title">{{dataSource[0].sNickName}}<span class="qtright"></span></div>
                        </div>
                    </div>
                    <div class="auth-box border-bm">
                        <span class="auth-item">邮箱认证 <van-icon name="checked" class="nstyle icon-active" /></span>
                        <span class="auth-item">手机认证 <van-icon name="checked" class="nstyle icon-active" /></span>
                        <span class="auth-item">初级认证 <van-icon name="checked" class="nstyle icon-active" /></span>
                        <span class="auth-item">高级认证 <van-icon name="checked" class="nstyle icon-active" /></span>
                        <span class="auth-item">广告认证 <van-icon name="checked" class="nstyle icon-active" /></span>
                    </div>
                </div>
                <div class="css-deal1">
                    <div class="css-cnt">
                        <div class="css-font-left">广告方保证资产：</div>
                        <div class="css-font-right">{{tradeData.iDepositAmount}} <span style="font-size:12px">USDT</span></div>
                    </div>
                    <div class="css-cnt">
                        <div class="css-font-left">总成单：</div>
                        <div class="css-font-right">{{tradeData.iTotalSuccessOrderCount}}<span style="font-size:12px">次</span></div>
                    </div>
                    <div class="css-cnt">
                        <div class="css-font-left">近30日成单：</div>
                        <div class="css-font-right">{{tradeData.iMonthSuccessOrderCount}}<span style="font-size:12px">次</span></div>
                    </div>
                    <div class="css-cnt">
                        <div class="css-font-left">近30日成交率：</div>
                        <div class="css-font-right">{{tradeData.iMonthOrderCount>0? ((tradeData.iMonthSuccessOrderCount /tradeData.iMonthOrderCount) * 100).toFixed(4) +'':'-'}}<span style="font-size:12px">%</span></div>
                    </div>
                    <div class="css-cnt">
                        <div class="css-font-left">平均放币时间：</div>
                        <div class="css-font-right">{{initReleaseCoinTime[0]}}<span style="font-size:12px">分</span>{{initReleaseCoinTime[1]}}<span style="font-size:12px">秒</span></div>
                    </div>
                </div>
            </div>
            <van-pull-refresh v-model="refreshing" style="min-height: 38vh;" @refresh="refreshData">
                <van-list
                    v-model="loading"
                    :finished="loadedall"
                    finished-text="没有更多了"
                    @load="loadData"
                >
                    <van-empty description="没有数据" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length==0" />
                    <div class="warp-box" v-else>
                        <div v-for="(items,index) in handlerDatas" :key="index">
                            <p class="title font16">{{items.iSideType==1?'在线购买':'在线出售'}}</p>
                            <div class="ads-list" v-for="(item,index) in items.children" :key="index">
                                <div class="info-cont gflex">
                                    <div style="flex:1">
                                        <div class="box__title">币种 <span>{{ item.sToken}}</span></div>
                                    </div>
                                    <div class="rbox">
                                        <div v-if="uid() === item.iUserId">我的广告</div>
                                        
                                        <div v-else>
                                            <!-- <a class="mc-bnt" v-if="item.iSideType ==1" @click="showModal('buy',item)">购买</a>
                                            <a class="mc-bnt" v-if="item.iSideType ==2" @click="showModal('sell',item)">卖出</a> -->
                                            <div v-if="Number(item.dTradeQuantity) >0">
                                                <a class="mc-bnt" v-if="item.iSideType ==1" @click="showModal('buy',item)">购买</a>
                                                <a class="mc-bnt" v-if="item.iSideType ==2" @click="showModal('sell',item)">卖出</a>
                                            </div>
                                            <div v-else>
                                                <a class="mc-bnt hs" v-if="item.iSideType ==1">购买</a>
                                                <a class="mc-bnt hs" v-if="item.iSideType ==2">卖出</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="info-cont gflex" :class="(index+1)<items.children.length?'mbmt':''">
                                    <div style="flex:1">
                                        <div class="box__title">数量 <span>{{Number(item.dTradeQuantity)}}</span></div>
                                        <div class="box__title">单价 <i>￥{{Number(item.dPrice).toFixed(2)+ ' '+item.sUnit}}</i></div>
                                        <div class="box__title">限额 <span>{{ Number(item.dMinAmount).toFixed(2) + ' - ' + Number(item.dMaxAmount).toFixed(2)  + ' '+item.sUnit}}</span></div>
                                        <div class="box__title">上架状态 <span>{{ item.iAdsState ===1 ? '上架中' : '已下架' }}</span></div>
                                    </div>
                                    <div class="pay-type rbox">
                                        <span v-for="sp in returnPayModes(item.sPayMode)" :key="sp">
                                            <img v-if="sp==13" class="pay-icon" src="../../assets/img/zfb.png">
                                            <img v-if="sp==14" class="pay-icon" src="../../assets/img/wx.png">
                                            <img v-if="sp==15" class="pay-icon" src="../../assets/img/yhk.png">
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </van-list>
            </van-pull-refresh>
        </div>
        <buy-sell-modal ref="buy-sell-modal" />
    </div>
</template>

<script>
    import {NavBar} from 'vant';
    import {otcUrl, publishAds, getPayMode, getPayDictionary, getMyAdsList,getUserOrderStat} from '@/api/otc'
    import {mapGetters} from 'vuex'
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import BuySellModal from "./BuySellModal";

    export default {
        name: "Ads",
        mixins: [GpayListMixin],
        components: {
            [NavBar.name]: NavBar,
            BuySellModal
        },
        data() {
            return {
                myAdsList: [],
                myAdsParam: {},
                payDictionarys: [],
                iSideType: 1,
                iBlockType: 1,
                activeSCoinCode: 'USDT',
                resultName: 'vAdsData',
                queryParam: {
                    // iAdsId: 0,
                    // sCoinCode: 'USDT',
                    // iSideType: 2,
                    //iBlockType: 1,
                    iPageNo: 0,
                    iPageSize: 10,
                    //iSideType: 1,
                    iUserId: Number(this.$route.query.usid)
                },
                url: {
                    list: otcUrl.GetAdsList,
                },
                tradeData:{},
            };
        },
        created() {
            //查询数据
            //this.initParam();
            this.refreshData();
            getPayDictionary({iDictType: 3})
                .then((res) => {
                    this.payDictionarys = res.data.vPayDictionary
                })
            getUserOrderStat({iUserId:Number(this.$route.query.usid)})
                .then((res)=>{
                    this.tradeData = res.data.stStat
                })
        },
        computed:{
            handlerDatas(){
                let obj = {},arr = this.dataSource;
                arr.forEach((item, index) => {
                    let { iSideType } = item;
                    if (!obj[iSideType]) {
                        obj[iSideType] = {
                            iSideType,
                            children: []
                        }
                    }
                    obj[iSideType].children.push(item);
                });
                let data = Object.values(obj); // 最终输出
                //this.myAdsList = data
                return data
            },
            bgidCladd(){
                let id = this.$route.query.coid
                return id?'user-avatar-bg'+id:'user-avatar-bg'+1
            },
            initReleaseCoinTime(){
                if(this.tradeData && this.tradeData.iTotalSuccessOrderCount >0){
                    const avTime = parseInt(this.tradeData.iTotalReleaseCoinTime / this.tradeData.iTotalSuccessOrderCount / 1000)
                    return [parseInt(avTime/60),avTime % 60]//parseInt(avTime/60) + '分' + avTime % 60 + '秒'
                }else{
                    return [0,0]
                }
            },
        },
        methods: {
            ...mapGetters(['uid','iCertificateStatus','email','cellphone','iLiveCertificateStatus','iAdsStatus']),
            returnPayModes(ids) {
                const tmpIds = ids.split(',')
                const modes = []
                if (tmpIds.length > 0 && this.payDictionarys.length > 0) {
                    tmpIds.forEach(id => {
                        modes.push(this.payDictionarys.find(i => i.iId == id).iId)
                    })
                }
                return modes//modes.join(',')
            },
            showModal(type,record){
                this.$refs['buy-sell-modal'].init()
                this.$refs['buy-sell-modal'].record=record
                this.$refs['buy-sell-modal'].type=type
                return
                if(this.iCertificateStatus() !==2){
                    this.$confirm({
                    content:'您还没有实名认证，请先进行认证！',
                    onOk:()=>{
                        this.$router.push({name:'auth',params:{type:'1'}})
                    },
                    okText:'前往认证',
                    cancelText:'取消'
                    })
                }else{
                    this.$refs['buy-sell-modal'].type=type
                    this.$refs['buy-sell-modal'].record=record
                    this.$refs['buy-sell-modal'].visible=true
                    this.$refs['buy-sell-modal'].init()
                }
            },
            onClickLeft() {
                this.$router.push('/otc');
            },
        },
    }
</script>

<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
