        }
        .van-tab--disabled{
            color: #c8c9cc;
        }
    }
    /deep/.ads-two-mun .van-tab--active{
        span{
            font-family: PingFang SC;
            font-weight: 400;
            color: #fff;
            background: @gpay-bg3D7EFF;
            border-radius: 15px;
            padding: 1px 14px;
        }
    }
    /deep/ .ads-nav-name .van-tab{
        font-size: .3rem;
        line-height: normal;
    }
    /deep/ .van-image__img{
        border-radius: 50%;
    }
    .title {
        padding: 0 .3rem;
        padding-top: .1rem;
        margin-bottom: .2rem;
    }
    .font16 {
        font-size: .27rem;
    }
    .css-deal1 {
        box-sizing: border-box;
        margin: 0;
        min-width: 0;
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        width: 100%;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        margin-top: 15px;
    }
    .css-cnt {
        box-sizing: border-box;
        margin: 0;
