<template>
    <div class="tabbar-user">
        <user-header />
        <user-menu />
        <div class="exit">
            <a class="exit-login" color="#7232dd" v-if="isLogin" @click="quit">退出当前账户</a>
        </div>
    </div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex'
    import userHeader from './user_header';
    import userMenu from './user_menu';
    export default {
        name: "My",
        computed:{
            name(){
                return this.userName() !== '' ?  this.userName() : this.cellphone()
            }
        },
        components: {
            [userHeader.name]: userHeader,
            [userMenu.name]: userMenu,
        }, data() {
            return {
                active: 0,
            };
        },
        methods: {
            ...mapGetters(['isLogin','userName','cellphone']),
            ...mapActions(['Logout']),
            quit(){
                this.$dialog.confirm({
                    message: '您确定退出当前账户',
                    //messageAlign: 'left',
                    confirmButtonText:'退出',
                    confirmButtonColor: '#ee0a24',
                    cancelButtonText:'取消'
                }).then(() =>{
                    this.Logout();
                })
            }
        },
    }
</script>

<style lang="less" scoped>
    .home-page {
        text-align: center;
        margin-top: 60px;
    }
    .tabbar-user {
        // > div {
        //     margin-bottom: 10px;
        // }
        &__quit {
            border: 0;
            border-radius: 0;
        }
    }
    .exit{
        text-align: center;
        padding-top: 30px;
        padding-bottom: 2rem;
        .exit-login{
            color: #f7714b;
            font-size: 12px;
            border: 1px solid #fc7a56;
            border-radius: 25px;
            padding: 7px 50px;
        }
    }
    
</style>
