<template>
    <div>
        <van-popup v-model="showPay"
            :closeable="true"
            round
            title=""
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            @click-close-icon="close"
            position="bottom" style="padding-top:12px">
            <van-form @submit="onSubmit" ref="forms" validate-trigger="onChange">
                <van-field
                    label="支付方式"
                    value="银行卡"
                >
<!--                <template #input>-->
<!--                    <span :class="{'font-c8c':!sItemDesc}" @click="show=true">{{sItemDesc?sItemDesc:'选择支付方式'}}</span>-->
<!--                </template>-->
                </van-field>
                <van-field
                    v-model="sName"
                    name="pattern"
                    label="账户名"
                    placeholder="请填写银行卡账户名"
                />
               <van-field
                    v-if="sItemDesc"
                    v-model="payItem.sAccount"
                    name="pattern"
                    :maxLength="22"
                    autocomplete="off"
                    :label="sItemDesc +'账号'"
                    :placeholder="'请输入'+sItemDesc+'账号'"
                    :rules="[{ required: true, message: ' ' }]"
                />
                <van-field
                    v-model="payItem.sBankName"
                    v-if="payItem.iPayDictId==15"
                    name="pattern"
                    label="开户银行"
                    :maxLength="20"
                    autocomplete="off"
                    placeholder="请输入开户银行"
                    :rules="[{ required: true, message: ' ' }]"
                />
                <van-field
                    v-model="payItem.sSubbranchBankName"
                    v-if="payItem.iPayDictId==15"
                    name="pattern"
                    label="开户支行"
                    :maxLength="20"
                    autocomplete="off"
                    placeholder="请输入开户支行"
                    :rules="[{ required: true, message: ' ' }]"
                />
                <!-- :rules="[{ validator:numbVal, message: errMsg,type:'numb' }]" -->
                 <van-field
                    v-if="payItem.iPayDictId==13||payItem.iPayDictId==14"
                    name="pattern"
                    label="二维码"
                >
                    <template #input>
                        <van-image width="100" v-if="payItem.imageUrl" @click="photo" height="100" :src="payItem.imageUrl" />
                        <van-image width="100" v-else @click="photo" height="100" />
                    </template>
                </van-field>
                <van-field
                    name="pattern"
                    label="是否启用"
                >
                <template #input>
                    <van-switch v-model="payItem.iIsDisplay" size="24px" />
                </template>
                </van-field>
                <van-field
                    v-model="payItem.sDrawalPassword"
                    label="资金密码"
                    :maxLength="20"
                    type="password"
                    placeholder="请输入您设置的资金密码"
                    :rules="[{ required: true, message: '' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" :loading="loading" loading-text="数据处理中..." native-type="submit">
                        确认添加
                    </van-button>
                </div>
            </van-form>
        </van-popup>
        <van-action-sheet v-model="show" title="支付方式">
            <van-radio-group v-model="payItem.iPayDictId" direction="horizontal">
                <van-cell class="v-pay" v-for="item in payList" :key="item.iId" :title="item.sItemDesc" @click="radios(item)">
                    <van-radio style="float:right;" :name="item.iId"></van-radio>
                </van-cell>
            </van-radio-group>
        </van-action-sheet>
        <!--上传图片-->
        <van-action-sheet
            v-model="photoshow"
            cancel-text="取消"
            close-on-click-action
            :actions="actions"
            @select="takePhoto"
        />
    </div>
</template>

<script>
    import {getPayDictionary,addPayMode} from '@/api/otc'
    import {getCertificate} from '@/api/udc'
    import {ossUrl,getSignedUrl} from '@/api/oss'
    import { mapGetters } from 'vuex'

    export default {
        name: "payAdd",
        components: {

        },
        data() {
            return {
                showPay: false,
                photoshow: false,
                show: false,
                sItemDesc:'银行卡',
                payList:[],
                firstName: '',
                lastName:'',
                sName:'',
                payItem:{
                    iPayDictId:15,
                    iIsDisplay:true,
                    sSubbranchBankName:'',
                    sBankName:'',
                    sAccount:'',
                    sDrawalPassword:'',
                    imageUrl:''
                },
                actions: [
                    {name: '从相册选择'},
                    {name: '拍照'},
                ],
                loading: false
            };
        },
        computed:{
            // sName(){
            //     return this.firstName + this.lastName
            // }
        },
        created() {
        },
        methods: {
            ...mapGetters(['iDrawalStatus']),
            init(){
                if(this.iDrawalStatus() !==1){
                    this.$message.warning('请先设置资金密码！')
                    // this.$router.replace('/my/security');
                    this.$router.replace('/my');
                    return
                }
                // 支付方式
                getPayDictionary({iDictType:3})
                .then((res)=>{
                    this.payList = res.data.vPayDictionary
                })
                // getCertificate({iIdle:0})
                // .then(res=>{
                //     // debugger
                //     this.firstName = res.data.sSurname
                //     this.lastName = res.data.sName
                // })
                this.showPay = true
                this.$nextTick(() =>{
                    this.$refs.forms.resetValidation()//重置验证
                });
            },
            photo(){
                this.photoshow = true;
            },
            minVal(value,rule){
                let prices = this.dPrice
                this.minAmount = value
                if (!value) {
                    rule.message = '请输入单笔最低限额'
                    return false
                }
            },
            takePhoto(action, index) {
                let that = this;
                function setOptions(srcType) {
                    let options = {
                        // Some common settings are 20, 50, and 100
                        quality: 80,
                        destinationType: Camera.DestinationType.DATA_URL,
                        // In this app, dynamically set the picture source, Camera or photo gallery
                        sourceType: srcType,
                        encodingType: Camera.EncodingType.JPEG,
                        mediaType: Camera.MediaType.PICTURE,
                        correctOrientation: true  //Corrects Android orientation quirks
                    }
                    return options;
                }

                if (index === 0) {
                    let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
                    let options = setOptions(srcType);
                    navigator.camera.getPicture(function cameraSuccess(imageData) {
                        that.imageDataFunc(imageData)
                        // that.fileUpload(imageUri)
                    }, function cameraError(error) {
                        console.debug("Unable to obtain picture: " + error, "app");
                    }, options);
                }
                if (index === 1) {
                    let srcType = Camera.PictureSourceType.CAMERA;//拍照
                    let options = setOptions(srcType);
                    navigator.camera.getPicture(function cameraSuccess(imageData) {
                        that.imageDataFunc(imageData)

                    }, function cameraError(error) {
                        console.debug("Unable to obtain picture: " + error, "app");
                    }, options);
                }
            },
            imageDataFunc (imageData) {
                let that = this;
                var src  = "data:image/jpeg;base64," + imageData;
                that.payItem.imageUrl = src;
            },
            onSubmit(values){
                if(!this.payItem.iPayDictId){
                    this.$toast('请选择支付方式')
                    return
                }
                this.loading = true
                const params = {
                    sDrawalPassword:this.payItem.sDrawalPassword,
                    iPayDictId:this.payItem.iPayDictId,
                    sAccount:this.payItem.sAccount,
                    sName:this.sName,
                    sBankName:this.payItem.sBankName,
                    sSubbranchBankName:this.payItem.sSubbranchBankName,
                    sAttachmentUrl:this.payItem.imageUrl,
                    iIsDisplay:this.payItem.iIsDisplay ? 1:0,
                    iDefault:0,
                }
                if(params.iPayDictId !=15){
                    params.sSubbranchBankName = ''
                    params.sBankName =''
                }
                this.loading = true
                console.log(params)
                addPayMode(params)
                .then(()=>{
                    this.$message.success('添加成功！')
                    this.showPay = false
                    this.payItem = {
                        iPayDictId:15,
                        iIsDisplay:true,
                        sSubbranchBankName:'',
                        sBankName:'',
                        sAccount:'',
                        sDrawalPassword:'',
                        imageUrl:''
                    }
                    this.loading = false
                    this.$emit('refPay');
                })
                .finally(()=>{
                    this.loading = false
                })
            },
            togglePay(index) {
                console.log(this.$refs.checks[index])
                this.$refs.checks[index].toggle();
            },
            radios(item){
                this.show = false
                this.sItemDesc = item.sItemDesc
                this.payItem.iPayDictId = item.iId
            },
            close(){
                this.payItem = {
                  iPayDictId:15,
                  iIsDisplay:true,
                  sSubbranchBankName:'',
                  sBankName:'',
                  sAccount:'',
                  sDrawalPassword:'',
                  imageUrl:''
                }
            }
        },
    }
</script>

<style lang="less" scoped>
    .f-bnt{
        width:47%;
        display: inline-block;
        background: #FFFFFF;
        border: 1px solid #C8CBCE;
        color: #919DAF;
    }
    .f-bnt-true{
        width:47%;
        display: inline-block;
        margin-left:.4rem;
    }
    .v-pay.van-cell{
        font-size: 15px;
        padding: 14px 16px;
    }
</style>
