<template>
    <div class="user_header">
        <van-icon name="set" class="user_set" @click="toSetting" />
        <div class="user_avatar">
            <van-image v-if="avatar" @click.native="upAvatar" :src="avatar" width="55" height="55" :error-icon="require('../../assets/img/avatar_default.png')">
                <template v-slot:loading>
                    <van-loading type="spinner" size="20" />
                </template>
            </van-image>
            <img v-else src="../../assets/img/avatar_default.png" @click="upAvatar" alt="头像" width="55" height="55">
        </div>
        <div class="nick_name">{{nick_name}}</div>
        <pub-up-img ref="PubUpImgs"></pub-up-img>
    </div>
</template>

<script>
    import {gueryFinanceList} from '@/api/otc'
    import {mapGetters,mapActions} from 'vuex'
    import PubUpImg from "@/components/fileAvatar";
    export default {
        name: 'user-header',
        components:{
          PubUpImg
        },
        computed:{
            name(){
                return this.userName() !== '' ?  this.userName() : this.cellphone()
            },
            avatar(){
                return this.sAvatar()
            }
        },
        data() {
            return {
                nick_name:this.userName() !== '' ?  this.userName() : this.cellphone()
            };
        },
        methods: {
            ...mapGetters(['isLogin','userName','cellphone','sAvatar']),
            toSetting() {

            },
            upAvatar(){
              this.$refs.PubUpImgs.init()
            },
        }
    };

</script>

<style lang="less" scoped>
    .user_header {
        position: relative;
        background-repeat: no-repeat;
        background-size: cover;
        height: 2.5rem;
        box-sizing: border-box;
        text-align: center;
        color: #fff;
        padding-top: .52rem;
        background: @gpay-bg3D7EFF;
        .nick_name{
            font-size: .3rem;
            width: auto;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin: 0 .4rem;
        }
    }

    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }
    .user_avatar {
        margin-bottom: .1rem;
        img {
            border: 0;
            border-radius: 50%;
        }
    }
    /deep/ .user_avatar {
        .van-image img{
            border-radius: 50%;
        }
        .van-image__loading{
            background: none;
            border: 0;
        }
    }
    /deep/.van-image__error{
            border-radius: 50%;
            background: none;
            .van-icon__image{
                width: 55px;
                height: 55px;
            }
        }
</style>
