<template>
  <div>
    <div class="warp-loading" v-if="upImgLoading">
      <van-loading class="upFile" size="24px" vertical color="#0094ff">
        <span style="color:#0094ff;font-size:12px">图片上传中...</span>
      </van-loading>
    </div>
    <div class="warp-loading" v-if="upVideoLoading">
      <van-loading class="upFile" size="24px" vertical color="#0094ff">
        <span style="color:#0094ff;font-size:12px">视频上传中...</span>
      </van-loading>
    </div>
    <div style="height: 1rem;"></div>
    <van-steps :active="active" active-color="#38f">
      <van-step>填写资料</van-step>
      <van-step>上传照片</van-step>
      <van-step>上传视频</van-step>
      <van-step>系统审核</van-step>
    </van-steps>

    <van-form @submit="nextStep" v-if="active===0">
      <van-field
          name="姓名"
          label="姓名"
          placeholder="请填写您的真实姓名"
          v-model="validatorRules.sCertificateName"
          :rules="[{ required: true, message: '请填写您的真实姓名' }]"
      />
      <van-field
          v-model="validatorRules.sCertificateNo"
          name="证件号码"
          label="证件号码"
          placeholder="证件号码"
          :rules="[{ validator:idCard, message: '请填写证件号码' }]"
      />
      <van-field
          v-model="validatorRules.sContactPhone"
          name="联系电话"
          label="联系电话"
          placeholder="请填写您的联系电话" type="tel"
          :maxLength="20"
          :rules="[{ validator, message: '请填写正确格式联系电话' }]"
      />
      <van-field
          v-model="validatorRules.sContactWechat"
          name="微信"
          label="微信"
          placeholder="请填写您的微信号"
          :maxLength="20"
          :rules="[{ required: false, message: '请填写您的微信号' }]"
      />
      <van-field
          v-model="validatorRules.sContactEmail"
          name="联系邮箱"
          label="联系邮箱"
          placeholder="请填写您常用的联系邮箱"
          :maxLength="30"
          :rules="[{ validator:emails, message: '请填写您常用的联系邮箱' }]"
      />
      <van-field
          v-model="validatorRules.sEmergentPerson"
          name="紧急联系人"
          label="紧急联系人"
          placeholder="紧急联系人"
          :maxLength="20"
          :rules="[{ required: false, message: '请填写紧急联系人' }]"
      />
      <van-field
          v-model="validatorRules.sEmergentPhone"
          name="紧急联系人电话"
          label="紧急联系人电话"
          placeholder="紧急联系人电话" type="tel"
          :maxLength="20"
          :rules="[{ validator:sTell, message: '请填写紧急联系人电话' }]"
      />
      <van-field
          v-model="validatorRules.sEmergentRelation"
          name="与本人关系"
          label="与本人关系"
          placeholder="请选择紧急联系人与本人关系"
          readonly
          clickable
          :rules="[{ required: false, message: '请选择紧急联系人与本人关系' }]"
          @click="showPicker = true"
      />
      <van-popup v-model="showPicker" round position="bottom">
        <van-picker
            show-toolbar
            :columns="relColumns"
            @cancel="showPicker = false"
            @confirm="onRelConfirm"
        />
      </van-popup>
      <div class="cert-foot-bnt">
        <van-button round block type="info" native-type="submit">
          下一步
        </van-button>
      </div>
    </van-form>
    <div v-if="active===1">
      <div class="cert-box">
        <van-row type="flex" justify="space-around">
          <van-col>
            <div class="imgs-box">
              <p v-if="imageUrlFront==null">
                <van-image width="200" :src="require('../../assets/img/sfz1.png')">
                  <template v-slot:loading>
                    <van-loading type="spinner" size="20"/>
                  </template>
                </van-image>
              </p>
              <p v-else>
                <van-image style="height:200px" :src="imageUrlFront"/>
              </p>
            </div>
            <van-button icon="back-top" type="info" size="mini"
                        style="width:50%;margin:0 auto;padding:15px 0" round block @click="showTakePhoto(1)"
                        native-type="button">{{imageUrlFront?'重新上传正面照':'上传身份证正面照'}}
            </van-button>
          </van-col>
        </van-row>
      </div>
      <div class="cert-box">
        <van-row type="flex" justify="space-around">
          <van-col>
            <div class="imgs-box">
              <p v-if="imageUrlBack==null">
                <van-image width="200" :src="require('../../assets/img/sfz2.png')">
                  <template v-slot:loading>
                    <van-loading type="spinner" size="20"/>
                  </template>
                </van-image>
              </p>
              <p v-else>
                <van-image style="height:200px" :src="imageUrlBack"/>
              </p>
            </div>
            <van-button icon="back-top" type="info" size="mini"
                        style="width:50%;margin:0 auto;padding:15px 0" round block @click="showTakePhoto(2)"
                        native-type="button">{{imageUrlBack?'重新上传反面照':'上传身份证反面照'}}
            </van-button>
          </van-col>
        </van-row>
      </div>
      <div class="cert-box clearfix">
        <van-row type="flex" justify="space-around">
          <van-col>
            <div class="imgs-box">
              <p v-if="imageUrlHand==null">
                <van-image width="200" :src="require('../../assets/img/sfz3.png')">
                  <template v-slot:loading>
                    <van-loading type="spinner" size="20"/>
                  </template>
                </van-image>
              </p>
              <p v-else>
                <van-image style="height:200px" :src="imageUrlHand"/>
              </p>
            </div>
            <van-button icon="back-top" type="info" size="mini"
                        style="width:50%;margin:0 auto;padding:15px 0" round block @click="showTakePhoto(3)"
                        native-type="button">{{imageUrlHand?'重新上传手持身份证':'上传手持身份证'}}
            </van-button>
          </van-col>
        </van-row>
      </div>
      <br>
      <div class="cert-foot-bnt clearfix">
        <van-row type="flex" justify="space-around">
          <van-col style="width: 45%;">
            <van-button round block type="default" v-if="active>0" @click="upStep">
              上一步
            </van-button>
          </van-col>
          <van-col style="width: 45%;">
            <van-button round block type="info" @click="nextStep">
              下一步
            </van-button>
          </van-col>
        </van-row>
        <div style="height:3em;"></div>
      </div>
    </div>
    <div v-if="active===2">
      <van-cell value="视频认证" class="f_header"/>
      <van-notice-bar style="margin:16px; font-size:12px" color="#979A9F" background="#E9EEF5" left-icon="info-o"
                      wrapable text="请手持身份证正面，朗读以下范本并进行视频录制（保证声音和影像清晰）
本人（姓名），身份证号（身份证号码），我的资金来源合法可靠，自愿交易比特币等数字资产，本人充分了解 数字货币及潜在风险，本人具有抗风险的能力并愿意承担一切风险。另外，我确定此账号所有操作均为本人操作，该账号所产生的一切法律责任，由我个人承担。。"/>
      <div style="margin:16px; margin-bottom:30px">
        <van-row type="flex" justify="space-around">
          <van-col>
            <van-row type="flex" class="card-box" justify="center">
              <van-col>
                <!-- <van-image v-if="videoUrl == ''||videoUrl == null" :src="require('../../assets/img/vedio.png')"/> -->
                <div class="video-box">
                  <div v-if="videoUrl == ''||videoUrl == null">
                    <van-image :src="require('../../assets/img/video_default.png')"/>
                  </div>
                  <video width="285" height="195" v-if="videoUrl !== ''" :src="videoFile" alt="avatar"
                         controls="controls"/>
                </div>
              </van-col>
            </van-row>
            <van-row type="flex" class="card-box" justify="center">
              <van-col>
                <van-button round icon="play-circle-o" type="info" @click="showVideo=true">
                  {{videoUrl?'更换视频':'点击选择视频'}}
                </van-button>
              </van-col>
            </van-row>
          </van-col>
        </van-row>
      </div>
      <div style="margin: 16px 26px;">
        <van-form @submit="handleCommit" :scroll-to-error="true">
          <div style="height: 1rem;"></div>
          <!--            <van-field name="checkbox" label-width="270">-->
          <!--              <template #input>-->
          <!--      p-mt          <van-checkbox v-model="agree" shape="square"/>-->
          <!--                同意冻结 {{lv1DepositQuantity}} USDT作为广告方保证资产-->
          <!--              </template>-->
          <!--            </van-field>-->
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit">
              提交审核
            </van-button>
          </div>
        </van-form>
      </div>
    </div>
    <div v-if="active===3">
      <div v-if="status < 3 ">
        <div class="cert-box p-mt">
          <a-result title="您的资料已提交，请等待审核结果！"></a-result>
        </div>
      </div>
      <div v-if="status === 3">
        <div class="cert-box p-mt">
          <a-result
              status="error"
              title="审核不通过！">
            <template #extra>
              <a-button key="console" type="primary" @click="active = 0 ">
                重新提交
              </a-button>
            </template>
          </a-result>
        </div>
      </div>
    </div>
    <loading-modal
        ref="loadingModal"
        :message='loadmsg'
    />
    <van-action-sheet
        v-model="show"
        cancel-text="取消"
        close-on-click-action
        :actions="actions"
        @select="takePhoto"
    />
    <van-action-sheet
        v-model="showVideo"
        cancel-text="取消"
        close-on-click-action
        :actions="videoactions"
        @select="takeVideo"
    />
    <div style="height: 1rem;"></div>
  </div>
</template>

<script>
import LoadingModal from "@/components/LoadingModal/index";
import {Result,Button} from "ant-design-vue"
import {ossUrl,getSignedUrl} from "@/api/oss";
import {identityCodeValid, isEmail} from "@/utils/validate";
import {applyAdsExtCertificate} from "@/api/udc";

export default {
  name: "step_two",
  components:{
    LoadingModal,
    [Result.name]:Result,
    [Button.name]:Button
  },
  props:{
    status:Number
  },
  data(){
    return {
      ossUrl,
      type: '',
      selKey: '1',
      active: 0,
      sOssVideo: '',
      videoFile: null,
      show: false,
      showVideo: false,
      showPicker: false,
      agree: false,
      showPhotoType: 1,
      relColumns: [
        "朋友", "同事", "亲人"
      ],
      certificateInfo: {
        sName: '',
        sSurname: ''
      },
      validatorRules: {
        sCertificateName: '',
        sContactPhone: "",
        sContactWechat: "",
        sContactEmail: "",
        // sHomeAddress: "",
        sEmergentPerson: "",
        sEmergentPhone: "",
        sEmergentRelation: "",
        sCertificateNo: '',
      },
      actions: [
        {name: '从相册选择'},
        {name: '拍照'},
      ],
      videoactions: [
        {name: '从相册选择'},
        {name: '录制视频'},
      ],
      imageUrlFamilyRegister: null,
      imageUrlAddress: null,
      sFamilyRegister: null,
      sHomeAddressProof: null,
      imageUrlFront: null,
      imageUrlBack: null,
      imageUrlHand: null,
      sFrontImg: '',
      sBackImg: '',
      sHandImg: '',
      ssk: '',
      fixed: true,
      showLoading: false,
      liveCertificateStatus: 0,
      adsCertificateStatus: 0,
      certificateStatus: 0,
      lv1DepositQuantity: 0,
      loadmsg: '加载中',
      upImgLoading: false,
      upVideoLoading: false,
    }
  },
  computed:{
    videoUrl() {
      return this.sOssVideo === '' ? '' : process.env.VUE_APP_FILE_API_URL + this.sOssVideo
    },
    loadMsg() {
      return this.loadmsg;
    },
  },
  created(){
    this.ssk = this.$ls.get('SSK')
    if(this.status !=0){
      this.active =3
    }
  },
  methods:{

    // 校验函数返回 true 表示校验通过，false 表示不通过
    idCard(value, rule) {
      if (!value) {
        rule.message = '请填写证件号码'
        return false
      } else if (!identityCodeValid(value)) {
        rule.message = '请输正确格式的证件号码'
        return false
      }
    },
    validator(val) {
      return /1\d{10}/.test(val);
    },
    emails(value, rule) {
      if(!value || value =='') return true
      return isEmail(value)
    },
    onClickLeft() {
      if (this.$route.query.goindex === 'true') {
        this.$router.push('/');
      } else {
        this.$router.back(-1);
      }
    },
    nextStep() {
      if (this.active === 1) {
        //上传身份证
        // if (this.imageUrlFront == null || this.imageUrlBack == null || this.imageUrlHand == null) {
        //   this.$toast.fail("请正确上传身份证照片");
        //   return;
        // }
        // if (this.sFrontImg === '') {
        //   this.$toast.fail('请上传证件正面照！');
        //   return;
        // }
        // if (this.sBackImg === '') {
        //   this.$toast.fail('请上传证件反面照！');
        //   return;
        // }
        // if (this.sHandImg === '') {
        //   this.$toast.fail('请上传手持证件照！');
        //   return;
        // }
        this.active = this.active + 1;
      } else {
        this.active = this.active + 1;
      }

    },
    upStep() {
      this.active = this.active - 1;
    },
    sTell(value, rule) {
      if (!value) {
        // rule.message = '请填写紧急联系人电话'
        return true
      }
      if (this.validatorRules.sContactPhone == this.validatorRules.sEmergentPhone) {
        rule.message = '紧急联系人电话和联系电话不能一样'
        return false
      }
    },
    success() {
      this.$router.back(-1);
    },
    showTakePhoto(type) {
      this.show = true;
      this.showPhotoType = type;
    },
    onRelConfirm(value) {
      this.validatorRules.sEmergentRelation = value;
      this.showPicker = false;
    },
    takePhoto(action, index) {
      var that = this;

      function setOptions(srcType) {
        var options = {
          // Some common settings are 20, 50, and 100
          quality: 80,
          destinationType: Camera.DestinationType.FILE_URI,
          // In this app, dynamically set the picture source, Camera or photo gallery
          sourceType: srcType,
          encodingType: Camera.EncodingType.JPEG,
          mediaType: Camera.MediaType.PICTURE,
          correctOrientation: true  //Corrects Android orientation quirks
        }
        return options;
      }

      if (index === 0) {
        let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
        let options = setOptions(srcType);
        navigator.camera.getPicture(function cameraSuccess(imageUri) {
          that.fileUpload(imageUri)

        }, function cameraError(error) {
          console.debug("Unable to obtain picture: " + error, "app");

        }, options);
      }
      if (index === 1) {
        let srcType = Camera.PictureSourceType.CAMERA;//拍照
        let options = setOptions(srcType);
        navigator.camera.getPicture(function cameraSuccess(imageUri) {
          that.fileUpload(imageUri)

        }, function cameraError(error) {
          console.debug("Unable to obtain picture: " + error, "app");

        }, options);
      }
    },
    recording() {
      let that = this;
      let permissions = cordova.plugins.permissions;
      permissions.checkPermission(
          permissions.READ_EXTERNAL_STORAGE,
          function (s) {
            if (!s.hasPermission) {
              permissions.requestPermission(
                  permissions.READ_EXTERNAL_STORAGE,
                  function (s) {
                    if (s.hasPermission) {
                      //申请成功
                      vedio();
                    } else {
                      this.$toast('写入权限申请失败');
                    }
                  },
                  function (error) {
                    this.$toast.fail(error)
                  }
              )
            } else {
              //
              vedio();
            }
          },
          null
      )

      let vedio = function () {
        let options = {
          limit: 1,
          duration: 30 // 限制录制的视频时间长度，单位：/秒
        }
        //插件提供的拍视函数
        navigator.device.capture.captureVideo(
            function (mediaFiles) {
              let file = mediaFiles[0];
              // Toast(file.fullPath )
              that.videoFile = file;
              //alert(JSON.stringify(file))
              window.resolveLocalFileSystemURL(file.fullPath, function (entry) {
                let fileurl = entry.toURL()//如果此步转换失败检查app文件读取权限
                let videoPath = fileurl;
                that.$refs.loadingModal.visible = true
                that.loadmsg = "视频压缩中...";
                VideoEditor.transcodeVideo(
                    function (result) {
                      that.$refs.loadingModal.visible = false;
                      let videoPath = result;
                      // Toast(videoPath)
                      that.fileUploadVideo(videoPath, 1);
                    }, function (err) {
                      that.$refs.loadingModal.visible = false;
                    },
                    {
                      fileUri: videoPath, // the path to the video on the device
                      outputFileName: 'ReportVideo', // the file name for the transcoded video
                      outputFileType: VideoEditorOptions.OutputFileType.mp4, // android is always mp4
                      saveToLibrary: true, // optional, defaults to true
                      maintainAspectRatio: true,
                      deleteInputFile: false, // optional (android only), defaults to false
                      width: 640, // optional, see note below on width and height
                      height: 640, // optional, see notes below on width and height
                      videoBitrate: 1000000, // optional, bitrate in bits, defaults to 1 megabit (1000000)
                      fps: 24, // optional (android only), defaults to 24
                      audioChannels: 2, // optional (ios only), number of audio channels, defaults to 2
                      progress: function (info) {

                        console.log('transcodeVideo progress callback, info: ' + info)

                      } // info will be a number from 0 to 100
                    }
                )

              }, function (err) {
                that.$refs.loadingModal.visible = false;
              });
            },
            error => {
              that.$refs.loadingModal.visible = false;
            },
            options
        )
      }
    },
    takeVideo(action, index) {
      let that = this;

      function setOptions(srcType) {
        let options = {
          // Some common settings are 20, 50, and 100
          quality: 80,
          destinationType: Camera.DestinationType.FILE_URI,
          // In this app, dynamically set the picture source, Camera or photo gallery
          sourceType: srcType,
          //encodingType: Camera.EncodingType.JPEG,
          mediaType: Camera.MediaType.VIDEO,
          correctOrientation: true  //Corrects Android orientation quirks
        }
        return options;
      }

      if (index === 0) {
        let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
        let options = setOptions(srcType);
        navigator.camera.getPicture(function cameraSuccess(imageUri) {
          that.fileUploadVideo(imageUri, 2)
        }, function cameraError(error) {
          console.debug("Unable to obtain picture: " + error, "app");
        }, options);
      } else {
        this.recording()
      }
    },
    // 使用cordova FileUpload上传图片
    fileUpload: function (imageURI) {
      function getCaption(obj, state) {
        var index = obj.lastIndexOf("?");
        if (index > 0) {
          if (state == 0) {
            obj = obj.substring(0, index);
          } else {
            obj = obj.substring(index + 1, obj.length);
          }
          return obj;
        } else {
          return obj;
        }
      }

      imageURI = getCaption(imageURI, 0)

      let that = this
      that.upImgLoading = true
      let FileUploadOptions = window.FileUploadOptions
      let FileTransfer = window.FileTransfer
      let options = new FileUploadOptions()
      options.fileKey = 'file'
      let path = imageURI.substr(imageURI.lastIndexOf('/') + 1);
      options.fileName = path
      options.mimeType = 'image/jpeg'
      let headers = {'Authorization': ' ' + that.ssk}
      options.headers = headers;
      let ft = new FileTransfer()
      ft.upload(imageURI, encodeURI(this.ossUrl.PrivateUpload), async function (data) {

        function wait(ms) {
          return new Promise(resolve => setTimeout(() => resolve(), ms));
        };

        let resString = data.response
        let resObject = JSON.parse(resString) // 字符串转对象
        if (resObject.iRspCode === 0) {
          let vUrlPath = resObject.data.vUrlPath[0];
          if (that.showPhotoType === 1) {
            that.sFamilyRegister = vUrlPath;
            //that.imageUrlFamilyRegister = file_url + vUrlPath;
            getSignedUrl(vUrlPath).then(res => that.imageUrlFront = res)
            that.sFrontImg = resObject.data.vUrlPath[0];
            that.upImgLoading = false
            //alert(that.sFamilyRegister)
          }
          if (that.showPhotoType === 2) {
            that.sHomeAddressProof = vUrlPath;
            //that.imageUrlAddress = file_url + vUrlPath;
            getSignedUrl(vUrlPath).then(res => that.imageUrlBack = res)
            that.sBackImg = resObject.data.vUrlPath[0];
            that.upImgLoading = false
            //alert(that.sHomeAddressProof)
          }
          if (that.showPhotoType === 3) {
            that.sHomeAddressProof = vUrlPath;
            //that.imageUrlAddress = file_url + vUrlPath;
            getSignedUrl(vUrlPath).then(res => that.imageUrlHand = res)
            that.sHandImg = resObject.data.vUrlPath[0];
            that.upImgLoading = false
            //alert(that.sHomeAddressProof)
          }
        } else {
          //that.$toast(resObject.msg)
          that.$toast.fail('上传出错稍后重试！')
          that.upImgLoading = false
        }
      }, function (err) {
        console.log(err)
        alert(JSON.stringify(err))
        that.upImgLoading = false
        that.$toast.fail('上传出错稍后重试！')
      }, options)
    },
    fileUploadVideo(imageURI, ftype) {
      let that = this
      let FileUploadOptions = window.FileUploadOptions
      let FileTransfer = window.FileTransfer
      let options = new FileUploadOptions()
      options.fileKey = 'video'

      let path = imageURI.substr(imageURI.lastIndexOf('/') + 1);

      if (ftype === 2) {
        options.fileName = path
      } else {
        options.fileName = that.videoFile.name
        options.mimeType = that.videoFile.type
      }

      //alert(JSON.stringify(options.fileName));

      let headers = {'Authorization': ' ' + this.ssk}
      options.headers = headers;

      let ft = new FileTransfer()
      that.$refs.loadingModal.visible = true
      that.loadmsg = "视频上传中...";
      ft.upload(imageURI, encodeURI(this.ossUrl.PrivateUpload), function (data) {
        let resString = data.response
        console.log("=========0==============" + resString)
        that.$refs.loadingModal.visible = false
        let resObject = JSON.parse(resString) // 字符串转对象
        if (resObject.iRspCode === 0) {
          that.sOssVideo = resObject.data.vUrlPath[0];
          // alert(that.sOssVideo)
          getSignedUrl(that.sOssVideo).then(res => {
            // let video = document.createElement('video');
            // video.setAttribute('crossOrigin', 'Anonymous')
            // video.src = res
            that.videoFile = res;
            // alert(that.videoFile)
            // alert(  video.src )
            // console.log( "=========2=============="+ video.src)
          })

          // video.src = file_url + that.sOssVideo;
          // video.onloadeddata = that.loadedmetadata;
        } else {
          //this.$toast(resObject.msg)
          that.$toast.fail('上传出错稍后重试！')
        }
      }, function (err) {
        that.$refs.loadingModal.visible = false
        //alert(JSON.stringify(err))
        that.$toast.fail('上传出错稍后重试！')
      }, options)
    },
    handleCommit() {
      // if (this.validatorRules.sEmergentRelation === '') {
      //     this.$toast.fail('请选择与本人关系！')
      //     return
      // }
      // if (!this.sFamilyRegister) {
      //     this.$toast.fail('请上传户口证明文件！')
      //     return
      // }
      // if (!this.sHomeAddressProof) {
      //     this.$toast.fail('请上传地址证明文件！')
      //     return
      // }
      // if(this.sOssVideo === ''){
      //   this.$toast.fail('请上传认证视频！')
      //   return
      // }
      const params = {
        // sFamilyRegister: this.sFamilyRegister,
        // sHomeAddressProof: this.sHomeAddressProof,
        // sFrontImg: this.sFrontImg,
        // sBackImg: this.sBackImg,
        // sHandImg: this.sHandImg,
        // sOssVideo:this.sOssVideo,
        sFrontImg: '/user/20210322/f4c2f1dce9722c0cf681cab9c1751793.png',
        sBackImg: '/user/20210322/f4c2f1dce9722c0cf681cab9c1751793.png',
        sHandImg: 'user/20210322/f4c2f1dce9722c0cf681cab9c1751793.png',
        sOssVideo: '/user/20210310/66de63726925d69d19e60e2c03e40c9b.mp4',
        ...this.validatorRules,
      }
      applyAdsExtCertificate(params)
          .then(() => {
            this.$emit('commit')
            this.active += 1
          })
          .catch((e) => {
            console.log(e)
          })
    },
  },
  watch: {
    upImgLoading(v) {
      if (v) {
        console.log('22', v)
        document.body.style.overflow = 'hidden'
        document.body.removeEventListener('touchmove', this.stopTouch, {capture: true})
      } else {
        document.body.removeEventListener('touchmove', this.stopTouch, {capture: true})
        document.body.style = ''
      }
    },
  }
}
