<template>
    <div class="app-gapy-user">
        <router-view/>
        <van-row class="flex-wrapper" type="flex" justify="end">
            <van-col class="flex-right" @click="goRegister">
                注册
            </van-col>
        </van-row>
        <div class="flex-logo">
            <img class="logo" src="../../assets/img/applogo192.png" alt="">
        </div>
        <div class="login-form-box">
            <van-form @submit="login">
                <van-cell-group class="mlr">
                    <div class="label-text">手机号码</div>
                    <van-field
                        class="input-style"
                        v-model="validateForm.username"
                        name="username"
                        placeholder="请输入手机号"
                        clearable
                        required
                        size="large"
                        autocomplete="off"
                        :maxlength="11"
                        :rules="[{ validator :usernameValidator,required: true, message: usernameMsg }]"
                    />
                    <div class="label-text">密码</div>
                    <div class="pwd-box">
                        <van-field
                            class="input-style"
                            v-model="validateForm.password"
                            name="password"
                            placeholder="请输入不少于8位密码"
                            clearable
                            required
                            size="large"
                            :type="passwordType"
                            autocomplete="off"
                            :show-error-message="true"
                            :rules="[{ validator :passwordValidator,required: true, message:passwordMsg}]"
                        />
                        <a class="show-pwd" @click="showPwd">
                            <!--眼睛图标-->
                            <van-icon :name="passwordType === 'password'?'closed-eye':'eye-o'" />
                        </a>
                    </div>
                    <div class="forget-box">
                        <a @click="goUserForget">忘记密码？</a>
                    </div>
                </van-cell-group>
                <div style="margin: 16px;">
                    <van-button v-if="loginLoad" loading round block type="info" loading-text="登录中..." />
                    <van-button v-else round block type="info" :disabled="!LoginBnt" native-type="submit">
                        登录
                    </van-button>
                </div>
            </van-form>
        </div>
        <!-- <loading-modal
            ref="loadingModal"
            :message='loadmsg'
        /> -->
        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi = 'iVerifyBusi'
            @ok="safeLogin"
            :need-cellphone="needCellphone"
            :need-email="needEmail"
            :need-google="needGoogle"
            :need-type="false"
            :email-val="sEmail"
            :phone-val="sCellphone"
            area-val="86"
        />
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    // import LoadingModal from "@/components/LoadingModal/index";
    import SafeVerifyModal from "@/components/SafeVerifyModal"

    export default {
        name: "Login",
        components: {
            // LoadingModal,
            SafeVerifyModal
        },
        data() {
            return {
                showLoading: false,
                loadmsg: '正在登录中...',
                usernameRules: [
                    {validate: (val) => !!val, message: ' '},
                    {validate: (val) => val.length >= 11, message: ' '}
                ],
                passwordRules: [
                    {validate: (val) => !!val, message: ' '},
                    {validate: (val) => val.length >= 8, message: ' '}
                ],
                argeeRules: [{validate: (val) => !!val, message: '必须同意用户协议'}],
                validateForm: {
                    username: '',
                    password: '',
                    isAgree: false
                },
                visibility: false,
                cleanable: false,
                checked: false,
                disable: true,
                loginLoad: false,
                usernameMsg: ' ',
                passwordMsg: ' ',
                passwordType: 'password',
                // 安全验证
                iVerifyBusi:null,
                needCellphone:2,
                needEmail:2,
                needGoogle:2,
                sCellphone:'',
                sEmail:'',

                bSwitchGoogleVerify: null,
                bSwitchEmailVerify: null,
                bSwitchCellphoneVerify: null,
            };
        },
        computed:{
            LoginBnt(){
                let val = this.validateForm
                if(val.username.length <11 || val.password.length <8){
                    return false
                }else{
                    return true
                }
            }
        },
        methods: {
            ...mapActions(['Login', 'GetUserExten', 'GetUserInfo', 'SafeLogin']),
            showPwd() {
                if (this.passwordType === "password") {
                    this.passwordType = "";
                } else {
                    this.passwordType = "password";
                }
            },
            safeLogin(codes) {
                const params = {
                    iSource: 2,
                    iLoginType: 1,
                    sCellphone: this.validateForm.username,
                };
                if (this.bSwitchGoogleVerify)
                    params.sGoogleVerifyCode = codes.sGoogleVerifyCode;
                if (this.bSwitchEmailVerify)
                    params.sEmailVerifyCode = codes.sEmailVerifyCode;
                if (this.bSwitchCellphoneVerify)
                    params.sSMSVerifyCode = codes.sSMSVerifyCode;
                this.SafeLogin(params).then(() => {
                    //this.GetUserExten({ userType: 3, isLogin: true });
                    this.GetUserExten({userType:3,isLogin:true}).then((res) =>{
                        if(res.iUserType !==1){
                            this.$router.push('/main')
                        }
                    })
                });
            },
            login() {
                const params = {
                    iSource: 2,
                    iLoginType: 1,
                    sPassword: this.validateForm.password,
                    sCellphone: this.validateForm.username
                }
                this.loginLoad = true
                this.Login(params)
                    .then((res) => {
                        this.loginLoad = false
                        //判断是否开启了谷歌登录验证
                        const { bSwitchGoogleVerify, bSwitchEmailVerify, bSwitchCellphoneVerify } = res.data;

                        if (bSwitchGoogleVerify) {
                            this.bSwitchGoogleVerify = bSwitchGoogleVerify;
                            this.bSwitchEmailVerify = bSwitchEmailVerify;
                            this.bSwitchCellphoneVerify = bSwitchCellphoneVerify;
                            
                            this.needCellphone = bSwitchCellphoneVerify ? 1 : 0;
                            this.needEmail = bSwitchEmailVerify ? 1 : 0;
                            this.needGoogle = bSwitchGoogleVerify ? 1 : 0;
                            this.$refs.safeVerifyModal.visible = true;
                        }else{
                            this.GetUserExten({userType:3,isLogin:true}).then((res) =>{
                              // debugger
                                if(res.iUserType !==1){
                                    this.$router.push('/main')
                                }
                                //this.$toast.clear();
                                //this.$router.push('/main')
                            })
                        }
                        //this.$toast({message:'登录成功!',position: 'bottom'})
                        // this.GetUserInfo()
                        //this.$toast.clear();
                        
                        //this.$router.push('/main')
                    })
                    .catch(err => {
                        this.loginLoad = false
                    })
            },
            usernameValidator(val) {
                if (val.length < 11) {
                    this.usernameMsg = ' ';
                    return false
                }
            },
            passwordValidator(val) {
                if (val.length < 8) {
                    this.passwordMsg = " ";
                    return false
                }
            },
            inputChange(val) {
                if (this.validateForm.username.length >= 11 && this.validateForm.password.length >= 8) {
                    this.disable = false
                } else {
                    this.disable = true;
                }
            },
            goRegister() {
                return this.$router.push({name: 'register'})
            },
            goUserForget() {
                return this.$router.push({name: 'userForget'})
            },
            clearUsername() {
                this.validateForm.username = "";

            },
            clearPassword() {
                this.validateForm.password = "";

            },
            clear() {
                this.$refs.form.clear();
                this.validateForm = {
                    username: '',
                    password: '',
                    isAgree: false
                };
            },
            showLoadingfun() {
                this.$refs.loadingModal.visible = true
            },
            applyVerify(codes){// 回调
                this.$refs.safeVerifyModal.visible = false
                this.editPwd = true
                this.values = codes
                this.values.sGoogleVerifyCode = !this.values.sGoogleVerifyCode?'':this.values.sGoogleVerifyCode
                this.step = 3
            },
        },
    }
</script>

<style lang="less" scoped>
    @import url('../../assets/style/userStyle.less');
    .flex-wrapper {
        width: 100%;
        /*height: 56px;*/
        margin-top: 8px;
    }
    .flex-btn {
        width: 200px;
        /*height: 32px;*/
        /*background-color: #e0e0e0;*/
        text-align: center;
        /*line-height: 32px;*/
        /*margin-left: 8px;*/
        margin-top: 10%;
    }

    .flex-wrapper:first-child {
        margin-top: 0;
    }

    .flex-demo:first-child {
        margin-left: 0;
    }

    .home-page {
        text-align: center;
        margin-top: 60px;
    }

    .mu-demo-form {
        width: 100%;
        height: 100%;
        /*max-width: 460px;*/
        margin-top: 20px;
        padding-left: 10px;
        padding-right: 10px;
    }

    .sub_com {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        position: absolute;
        bottom: 20px;
    }
    .show-pwd {
        position: absolute;
        right: 60px;
        top: 50%;
        font-size: 18px;
        cursor: pointer;
        user-select: none;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
    }
    .pwd-box{
        position: relative;
    }
</style>
