<template>
    <div>
        <van-popup v-model="visible"
            :closeable="false"
            round
            title=""
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            position="bottom"
        >
            <div class="popup-title-hed">添加提币地址</div>
            <van-form @submit="onSubmit" ref="forms" class="forms" validate-trigger="onChange">
                <van-field disabled label="币种" name="sToken" value="USDT">

<!--                    <template #input>-->
<!--                        <a :class="{'font-c8c':!sToken}" @click="show=true">{{sToken?sToken:'选择币种'}}</a>-->
<!--                    </template>-->
                </van-field>
                <van-field clearable label="选择协议" name="sChain" right-icon="arrow" @click="showsChain=true">
                    <template #input>
                        <a :class="{'font-c8c':!sChain}" >{{sChain?sChain:'选择协议'}}</a>
                    </template>
                </van-field>
                <van-field
                    v-model="sAddress"
                    clearable
                    name="提币地址"
                    label="提币地址"
                    placeholder="请填写提币地址"
                    autocomplete="off"
                    :rules="[{ required: true, message: '' }]"
                />
                <van-field
                    v-if="(tokens.find(i=>i.sToken === selToken))? tokens.find(i=>i.sToken === selToken).bNeedTag : false"
                    v-model="sTag"
                    clearable
                    name="地址标签"
                    label="地址标签"
                    placeholder="请填写地址标签"
                    autocomplete="off"
                    :rules="[{ required: true, message: ' ' }]"
                />
                <van-field
                    v-model="sRemark"
                    clearable
                    name="备注"
                    label="备注"
                    placeholder="请填写备注"
                    type="textarea"
                    autosize
                    maxlength="30"
                    show-word-limit
                    autocomplete="off"
                    :rules="[{ required: true, message: ' ' }]"
                />
                <div style="margin: 16px;">
                    <van-button style="width:46%;display: inline-block;background: #FFFFFF;border: 1px solid #C8CBCE;color: #919DAF;" native-type="button" round block type="info" @click="bnt(false)">
                        取消
                    </van-button>
                    <van-button style="width:46%;display: inline-block; margin-left:.4rem;" round block type="info" native-type="submit">
                        添加地址
                    </van-button>
                </div>
            </van-form>
        </van-popup>
        <van-action-sheet
        v-model="show"
        cancel-text="取消"
        close-on-click-action
        >
            <div class="contents">
                <span @click="tokenChange(token)" v-for="token in tokenNames" :key="token.sToken">{{token.sToken}}</span>
            </div>
        </van-action-sheet>
        <van-action-sheet
        v-model="showsChain"
        cancel-text="取消"
        close-on-click-action
        >
            <div class="contents" v-if="protocols.length>0">
                <span @click="chainChange(token)" v-for="token in protocols" :key="token.sChain">{{token.sProtocol}}</span>
            </div>
            <van-empty v-else image-size="50" :image="require('../../assets/img/empty-image-default.png')" description="没有数据" />
        </van-action-sheet>
        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi = 'iVerifyBusi'
            @ok="applyVerify"
            area-val="86"
        />
    </div>
</template>
<script>
import { createOrder } from '@/api/otc'
import {getMerchantTokens,addWalletAddress} from '@/api/wallet'
import {mapGetters,mapActions} from "vuex"
import SafeVerifyModal from "@/components/SafeVerifyModal/index";
export default {
    data(){
        return {
            visible: false,
            record:{},
            type:'',
            tokenNames:[],
            selToken:'USDT',
            sToken:'USDT',
            sChain:'TRC20',
            sTag:'',
            sAddress:'',
            sRemark:'',
            show: false,
            showsChain:false,
            tokens: [],
            iVerifyBusi:null,
            sChainVal:'Tron'
        }
    },
    components:{
        SafeVerifyModal
    },
    created(){
        getMerchantTokens({iIdle:0})
        .then(res=>{
            this.tokenNames = []
            this.tokens = res.data.vTokens
            this.tokens.forEach(token =>{
                if(this.tokenNames.findIndex(t=> t.sToken === token.sToken)<0) {
                this.tokenNames.push(token)
                }
            })
        })
    },
    computed:{
        protocols(){
            const protocols =[]
            this.tokens.forEach(token=>{
                if(token.sToken === this.selToken){
                    protocols.push(token)
                }
            })
            return protocols
        },
    },
    methods:{
        ...mapGetters(['switchEmailVerify','switchCellphoneVerify','switchGoogleVerify']),
        init(){
            this.visible = true
            this.$nextTick(() =>{
                this.$refs.forms.resetValidation()
            });
        },
        tokenChange(v){
            //this.$refs.forms.resetValidation('sToken')
            //this.$refs.forms.resetValidation('sChain')
            this.selToken = v.sToken
            this.sToken = v.sToken
            this.sChain = this.protocols[0].sProtocol
            this.sChainVal = this.protocols[0].sChain
            this.show = false
        },
        chainChange(v){
            console.log(v)
            this.sChain = v.sProtocol
            this.sChainVal = v.sChain
            this.showsChain = false
        },
        bnt(t){
            this.$refs.forms.resetValidation()//重置验证
            this.visible = false
            this.sToken = 'USDT'
            this.sChain = 'TRC20'
            this.sChainVal = 'Tron'
            this.sTag = ''
            this.sAddress = ''
            this.sRemark = ''
        },
        onSubmit(vals) {
            if(!this.sToken){
                this.$toast('请选择币种')
                return
            }
            // this.iVerifyBusi= 20
            // //this.params = values
            // this.needGoogle = res.data.bSwitchGoogleVerify?1:0;
            // this.needEmail = res.data.bSwitchEmailVerify?1:0;
            // this.needCellphone = res.data.bSwitchCellphoneVerify?1:0;
            // this.sCellphone = res.data.sCellphone
            // this.sEmail = res.data.sEmail
            // console.log(this.sCellphone,this.sEmail)
            this.iVerifyBusi = 20
            this.$refs.safeVerifyModal.visible = true
        },
        applyVerify(codes){
            console.log(codes)
            this.loading = true
            const params = {
                sChain:this.sChainVal,
                sToken:this.sToken,
                sRemark:this.sRemark ? this.sRemark : '',
                sAddress:this.sAddress,
                sTag:this.sTag ? this.sTag : '',
                iSource:1
            }
            if(this.switchEmailVerify())params.sEmailVerifyCode = codes.sEmailVerifyCode
            if(this.switchCellphoneVerify())params.sSMSVerifyCode = codes.sSMSVerifyCode
            if(this.switchGoogleVerify())params.sGoogleVerifyCode = codes.sGoogleVerifyCode
            console.log(params)
            addWalletAddress(params)
                .then(()=>{
                    this.visible = false
                    this.$emit('aplOK');
                    this.bnt()
                    this.$refs.safeVerifyModal.visible = false
                    this.$message.success('添加成功！')
                })
                .finally(()=>{
                    this.loading = false
                    //this.$refs.safeVerifyModal.visible = false
                })
        }
    }
}
</script>
<style lang="less" scoped>
    .forms .van-cell{
        //align-items: center;
    }
    
    /deep/.van-field__body1{
        background: #FFFFFF;
        border: 1px solid #C8CBCE;
        border-radius: 5px;
        padding: .1rem .15rem;
    }
    .contents{
        span{
            display: block;
            text-align: center;
            padding: 14px 16px;
        }
    }
</style>