<template>
  <div class="ads-list-box">
    <van-nav-bar
        title="充值接单管理"
        left-text=""
        class="header_title"
        @click-left="onClickLeft"
        safe-area-inset-top
        placeholder
        :fixed="true"
    />
    <!--        <van-nav-bar-->
    <!--            title="广告管理"-->
    <!--            left-text=""-->
    <!--            class="header_title"-->
    <!--            @click-left="onClickLeft"-->
    <!--            safe-area-inset-top-->
    <!--            placeholder-->
    <!--            @click-right="onClickRight"-->
    <!--            :fixed="true"-->
    <!--        />-->
    <div style="height:1rem"></div>
    <div class="ads-list-cont">

      <van-cell-group>
        <van-cell title="USDT当前市价" >
          <span style="font-size: 18px; font-weight: 600;color:#80c180">{{ getMarketdPrice()('USDT') }}</span>
          <span style="color:#b6b6b6;font-size: 12px;"> CNY</span>
        </van-cell>
        <van-cell title="账户可用余额">
          <span style="font-size: 18px; font-weight: 600;">{{ usdtBalance }}</span>
          <span style="color:#b6b6b6;font-size: 12px;"> USDT</span>
        </van-cell>
        <van-cell title="接单状态">
          <span v-if="iTakingOrder() === 1" style=" ">
            <a-badge status="processing" />接单中
          </span>
          <span v-if="iTakingOrder() === 0" style="color:#f5222d">
            <a-badge status="error" />未接单
          </span>
        </van-cell>
        <van-cell  v-if="device !='ios'" title="收款短信模板管理" isLink label="设置模板后可进行短信监控，自动放币" @click="$router.push('/smsModels')">
        </van-cell>
      </van-cell-group>
      <div style="text-align: center;margin: 16px" v-if="device !='ios'">
        <van-button v-if="!listenSmsStatus()" style="width: 45%;margin-right: 16px" :disabled="iTakingOrder() === 0"  type="default" @click="startSMS">开启无人值守</van-button>
        <van-button v-if="listenSmsStatus()" style="width: 45%;margin-right: 16px"  type="default" @click="stopSMS">关闭无人值守</van-button>
        <van-button v-if="iTakingOrder() === 1" style="width: 45%"  type="danger" @click="stopTakingOrder">停止接单</van-button>
        <van-button v-if="iTakingOrder() === 0" :disabled="iAdsStatus()!==2" style="width: 45%" type="info" @click="startTakingOrder">开始接单</van-button>
      </div>
      <div v-else style="text-align: center;margin: 16px">
        <van-button v-if="iTakingOrder() === 1" style="width: 85%"  type="danger" @click="stopTakingOrder">停止接单</van-button>
        <van-button v-if="iTakingOrder() === 0" :disabled="iAdsStatus()!==2" style="width: 85%" type="info" @click="startTakingOrder">开始接单</van-button>
      </div>
      <div style="text-align: center" v-if="iAdsStatus()!==2">您还未进行承兑商资格认证，<a style="color:#3D7EFF" @click="$router.push('/my')">前往认证</a></div>

<!--      <van-button @click="test">test</van-button>-->
      <template v-if="device !='ios'">
        <template v-if="!listenSmsStatus()">
          <div style="text-align: center" v-if="smsTps.length ===0 ">您还未添加收款短信模板，无法开启无人值守模式</div>
          <template v-else>
            <div style="color: #b30000;padding: 0 12px">注：开启无人值守模式后，将自动监控本手机收到的收款短信，自动匹配放币，离开本页面将自动关闭无人值守模式</div>
          </template>
        </template>
        <template v-else>
          <div style="color: #b30000;padding: 0 12px">注：无人值守模式已开启，离开本页面将自动关闭本模式</div>
          <div v-for="(item,index) in logs" :key="index" style="padding: 0 12px">
            <span style="padding:4px;padding-right: 8px;">{{item.time}}</span>
            <span>{{item.text}}</span>
          </div>
        </template>
      </template>

    </div>
  </div>
</template>

<script>
// import {NavBar} from 'vant';
import {otcUrl, publishAds, getPayMode, getPayDictionary, getMyAdsList, adsUp, adsDown,getUserSmsTplList,noticeSms} from '@/api/otc'
import {mapGetters,mapActions} from 'vuex'
import { MSG_TIPS } from "@/utils/msgCode"
import {getUserBalance,userTakeingOrder,} from '@/api/udc'
import moment from 'dayjs'
import {GpayListMixin} from '@/mixins/GpayListMixin'
import {Badge} from 'ant-design-vue'

export default {
  name: "AdsList",
  mixins: [GpayListMixin],
  components: {
    [Badge.name]: Badge,
  },
  data() {
    return {
      device:window._CONFIG['device'],
      myAdsList: [],
      myAdsParam: {},
      payDictionarys: [],
      activeKey: 1,
      activeSCoinCode: 'USDT',
      resultName: 'vAdsData',
      queryParam: {
        iAdsId: 0,
        sCoinCode: 'USDT',
        //iSideType: 1
      },
      url: {
        list: otcUrl.GetMyAdsList,
      },
      balanceInterval:null,
      balances:[],
      smsTps:[],
      logs:[],
      lock:false,
      testText:[
        '业务种类=网银转入，尾号=1234，发生额=1222.1，余额=3321，付款人：张三三客服热线95574【宁波银行】',
        '张三三3月25日10时48分向您尾号1234的储蓄卡电子汇入存入人民币4848.11元，活期余额38383元【建设银行】',
        '您的借记卡账户1631，于03月12日手机银行收入人民币499.91元,交易后余额1311.2【中国银行】'
      ],
      testIndex:2,
      getUserInfoInterval:null
    };
  },
  computed:{
    usdtBalance(){
      const index = this.balances.findIndex(i=>i.sToken ==='USDT')
      return index > -1 ? Number(this.balances[index].iAvailableAmount).toFixed(4) : 0
    }
  },
  created() {
    //查询数据
    this.initParam();
    // this.refreshData();
    this.initUserBanlance()
    getUserBalance({iIdle:0})
        .then((res)=>{
          this.balances = res.data.vBalance
        })
    this.GetMarketPrice()
    this.listenSms()
    this.loadSMSTps()
    this.getUserInfoInterval =setInterval(()=>{
      this.GetUserExten()
    },3000)
    this.keepAwake()
    // if(this.iTakingOrder()===1 ){
    //
    // }
    // getPayDictionary({iDictType: 3})
    //     .then((res) => {
    //       this.payDictionarys = res.data.vPayDictionary
    //     })
  },
  beforeDestroy(){
    clearInterval(this.balanceInterval)
    clearInterval(this.getUserInfoInterval)
    document.removeEventListener('onSMSArrive',this.listenCallback)
    this.unkeepAwake()
  },
  methods: {
    moment,
    ...mapActions(['GetMarketPrice','userTakeingOrder','GetUserExten']),
    ...mapGetters(['iAdsStatus','getMarketdPrice','iTakingOrder','iAdsStatus','listenSmsStatus']),
    test(){
      console.log('发起事件!')
      const event = document.createEvent("HTMLEvents")
      event.data={}
      event.data.address = '95566'
      event.data.body = this.testText[this.testIndex]
      event.initEvent("onSMSArrive", false, false);
      document.dispatchEvent(event);
    },
    initParam() {
      this.queryParam.iAdsId = 0;
      this.queryParam.sCoinCode = this.activeSCoinCode;
      //this.queryParam.iSideType = parseInt(this.activeKey)
    },
    initUserBanlance(){
      this.balanceInterval = setInterval(()=>{
        getUserBalance({iIdle:0})
            .then((res)=>{
              this.balances = res.data.vBalance
            })
      },3000)
    },
    returnPayModes(ids) {
      const tmpIds = ids.split(',')
      const modes = []
      if (tmpIds.length > 0 && this.payDictionarys.length > 0) {
        tmpIds.forEach(id => {
          modes.push(this.payDictionarys.find(i => i.iId == id).iId)
        })
      }
      return modes//modes.join(',')
    },
    tabchange(val) {
      this.initParam();
      this.refreshData();
    },
    startTakingOrder(){
      this.loading = true
      userTakeingOrder({iStatus:1})
          .then(res=>{
            // debugger
            if(res.iRspCode === 0 ){
              this.GetUserExten()
            }
            if(res.iRspCode === -1134 ){
              this.$dialog.confirm({
                message: '您还没有设置收款银行卡账号，请先前往设置！',
                messageAlign: 'left',
                confirmButtonText:'前往设置',
                confirmButtonColor: '#3D7EFF',
                cancelButtonText:'等会再说'
              }).then(() =>{
                this.$router.push({name:'my'})
              })
            }

          })
          .finally(()=>{
            this.loading = false
          })
    },
    stopTakingOrder(){
      this.loading = true
      userTakeingOrder({iStatus:0})
          .then(()=>{
            this.GetUserExten()
          })
          .finally(()=>{
            this.loading = false
          })
    },
    onClickLeft() {
      if (this.$route.query.goindex === 'true') {
        this.$router.push('/');
      } else {
        this.$router.back(-1);
      }
    },
    keepAwake() {
      window.plugins.insomnia.keepAwake();
    },
    unkeepAwake() {
      window.plugins.insomnia.allowSleepAgain();
    },
    loadSMSTps(){
      let params = {iIdle:0}
      getUserSmsTplList(params).then((res) => {
        if (res.iRspCode === 0) {
          let data = res.data.vList
          if (data && data.length > 0) {
            this.smsTps = res.data.vList

          }
        } else {
          this.$toast.fail(res.message)
        }
      })
    },
    listenSms(){
      document.addEventListener('onSMSArrive',this.listenCallback)
    },
    listenCallback(e){

        if(!this.listenSmsStatus()) return
        // debugger
        let { address , body } = e.data
        let sms = body
        let idx1 = sms.indexOf('【')
        let idx2 = sms.indexOf('】')
        if(idx1 === -1 || idx2 === -1){
          idx1 = str.indexOf('[')
          idx2 = str.indexOf(']')
        }
        let str = ''
        let tmpNumbers = []
        let typeStrArr = []
        sms.replace(/[0-9]*\.?[0-9]+/g, ( x )=>{
          tmpNumbers.push({
            value:x,
            type:''
          })
        })
        for(let tp of this.smsTps){
          // debugger
          str = ''
          typeStrArr = []
          //发件号码不匹配，跳过这个模板
          if(address != tp.sSenderNo) continue
          //短信签名不匹配，跳过这个模板
          if(idx1 === -1 || idx2 === -1) continue
          if(tp.sBankName != sms.substr(idx1+1,idx2-idx1-1)) continue
          str = sms.replace(/,/g,'')
          tmpNumbers.forEach((item,index)=>{
            tp.sTemplate.replace(/(?<=\{\{)[^}]*(?=\}\})/g,(x)=>{
              typeStrArr.push(x)
            })
            if(index<typeStrArr.length){
              str = str.replace(item.value,'{{'+typeStrArr[index]+'}}')
              item.type = typeStrArr[index]
            }
          })
          this.logs.unshift({
            time: moment().format('HH:mm:ss'),
            text: '收到一条'+ tp.sSenderNo +','+sms.substr(idx1+1,idx2-idx1-1)+'发来的短信!'
          })
          this.logs.unshift({
            time: moment().format('HH:mm:ss'),
            text: '短信内容：' + sms
          })
          let sCardNo = ''
          let sAmount
          if(this.matchStrings(tp.sTemplate,str)){
            tmpNumbers.forEach(item=>{
              if(item.type ==='账户余额' ){
                this.logs.unshift({
                  time: moment().format('HH:mm:ss'),
                  text: item.type + ':' + item.value
                })
              }
              if(item.type ==='账户尾号' ){
                sCardNo = item.value
                this.logs.unshift({
                  time: moment().format('HH:mm:ss'),
                  text: item.type + ':' + item.value
                })
              }
              if(item.type ==='转账金额' ){
                sAmount = item.value
                this.logs.unshift({
                  time: moment().format('HH:mm:ss'),
                  text: item.type + ':' + item.value
                })
              }
            })
            this.lock = true
            const params ={
              sBankName:tp.sBankName,
              sCardNo:sCardNo,
              sAmount:sAmount,
              iTime:moment().valueOf()
            }
            noticeSms(params)
                .then(res=>{
                  if(res.iRspCode ===0){
                    this.logs.unshift({
                      time: moment().format('HH:mm:ss'),
                      text: '短信已上报！'
                    })
                    this.logs.unshift({
                      time: moment().format('HH:mm:ss'),
                      text: '短信监控中...'
                    })
                  }
                  // debugger
                })
                .catch(e=>{
                  let arr = this.$store.state.user.errorMsg
                  this.logs.unshift({
                    time: moment().format('HH:mm:ss'),
                    text: MSG_TIPS(arr,e.code)+',上报失败！'
                  })
                  this.logs.unshift({
                    time: moment().format('HH:mm:ss'),
                    text: '短信监听中...'
                  })

                })
                .finally(()=>{
                  this.lock = false
                })
          }
        }

        // this.logs.push({
        //   time: moment().format('HH:mm:ss'),
        //   text: '模板不匹配，继续监听...'
        // })

    },
    matchStrings(value1,value2){

      // debugger
      let str1 = value1
      let str3 = value1
      let str2 = value2
      let str4 = value2

      const totalLength = str1.length + str2.length

      let a = str1.substr(0,1)
      let b = str2.substr(0,1)
      str1 = str1.substr(1)
      str2 = str2.substr(1)
      let z = 0
      let x = 0
      while (a !== undefined && a !=='' && b !== undefined &&  b !=='' &&  a === b) {
        z++
        a = str1.substr(0,1)
        b = str2.substr(0,1)
        str1 = str1.substr(1)
        str2 = str2.substr(1)
      }

      // debugger
      let c = str3.substr(str3.length-1,1)
      let d = str4.substr(str3.length-1,1)
      str3 = str3.substr(0,str3.length-1)
      str4 = str4.substr(0,str4.length-1)
      while ( c !== undefined && c !=='' && d !== undefined && d !=='' && c === d) {
        x++
        c = str3.substr(str3.length-1,1)
        d = str4.substr(str3.length-1,1)
        str3 = str3.substr(0,str3.length-1)
        str4 = str4.substr(0,str4.length-1)
      }

      // debugger
      const e = (x+z)/totalLength
      return e > 0.6
    },
    startSMS(){
      if(this.smsTps.length === 0){
        this.$dialog.confirm({
          message: '您还没创建短信模板，现在要去设置吗？',
          messageAlign: 'left',
          confirmButtonText:'前往设置',
          confirmButtonColor: '#3D7EFF',
          cancelButtonText:'取消'
        }).then(() =>{
          this.$router.push({name:'smsModels'})
        })
      }else{
        this.$dialog.confirm({
          message: '开启后将自动监控收款短信放币，您确认要开启吗？',
          messageAlign: 'left',
          confirmButtonText:'确认开启',
          confirmButtonColor: '#3D7EFF',
          cancelButtonText:'等会再说'
        }).then(() =>{
          this.$store.commit('SET_LISTENSMS_STATE',true)
          this.logs.push({
            time: moment().format('HH:mm:ss'),
            text: '短信监控中...'
          })
        })

      }
    },
    stopSMS(){
        this.$dialog.confirm({
          message: '关闭后将只能人工确认放币，您确定现在要关闭短信监控吗？',
          messageAlign: 'left',
          confirmButtonText:'仍要关闭',
          confirmButtonColor: '#3D7EFF',
          cancelButtonText:'再等等'
        }).then(() =>{
          this.$store.commit('SET_LISTENSMS_STATE',false)
          this.logs = []

        })
    },

  },
}
</script>

<style lang="less" scoped>

.home-page {
  text-align: center;
  margin-top: 60px;
}
.ads-title{
  padding:8px;
  font-size: 0.38rem;
}
.warp-box{
  padding-bottom: .1rem;
}
