  margin-left: 10px;
}

.detail-info {
  font-weight: 200;
  font-size: 16px;
  margin-bottom: 5px;
}

.detail-info-sub {
  font-weight: 100;
  font-size: 14px;
  margin-bottom: 5px;
}
.ads-list{
  background: #fff;
  box-shadow: 0px 4px 9px 1px rgba(61, 126, 255, 0.23);
  border-radius: 10px;
  margin: 0 10px;
