  background: @gpay-bg3D7EFF;
  .van-nav-bar__text{
    color: @gpay-colorffffff;
  }
  .van-nav-bar__title{
    color: @gpay-colorffffff;
  }
  .van-nav-bar__right .van-nav-bar__text{
