<template>
  <div class="pay-list" style="padding-top: 1.2rem;padding-bottom: 0.4rem">
    <div v-if="step===1">
      <van-nav-bar
          title="新增短信模板"
          left-text=""
          class="header_title"
          @click-left="$router.back(-1);"
          safe-area-inset-top
          placeholder
          left-arrow
          :fixed="true"
      />
      <div>
        <van-field
            v-model="sSenderNo"
            label="发件号码"
            type="number"
            placeholder="请输入短信的发送号码，如 95555"
            show-word-limit
            required
        />

        <van-field
            v-model="message"
            rows="3"
            autosize
            label="短信模板"
            type="textarea"
            maxlength="140"
            placeholder="请复制粘贴一条完整的收款短信"
            show-word-limit
            required
        />
        <div style="margin-top: 0.4rem">
          <van-button
              type="info"
              size="mini"
              style="width:50%;margin:0 auto;padding:15px 0"
              round
              block
              :disabled="analyseDisabled"
              @click="analyseSMS">
            智能分析
          </van-button>
        </div>
        <template v-if="analysed">
          <van-cell-group title="请确认分析结果，若有误请手动更正">
            <van-cell :title="bankName" value="银行签名" />
            <!--          <van-cell :title="amount" value="转账金额" required is-link />-->
            <!--          <van-cell :title="allAmount" value="余额" required is-link />-->
            <van-cell v-for="(item,index) in numbers" :title="item.value"  required :value="item.type === 'none' ? '请选择数据类型' : item.type" :key="index" is-link @click="showPicker = true;pickerIndex = index"/>
          </van-cell-group>
          <div style="margin-top:0.4rem;margin-bottom: 0.4rem">
            <van-button
                :loading="loading"
                loading-text="数据处理中..."
                type="default"
                size="mini"
                style="width:50%;margin:0 auto;padding:15px 0"
                round
                block
                @click="nextStep"
                :disabled="disableNextStep"
            >
              已确认 下一步
            </van-button>
          </div>
          <van-popup v-model="showPicker" round position="bottom">
            <van-picker
                v-if="showPicker"
                title="类型"
                show-toolbar
                :default-index = 'defaultIndex'
                :columns="selOptions"
                @cancel="showPicker = false"
                @confirm="onConfirm"
            />
          </van-popup>
        </template>
      </div>
    </div>
    <div v-if="step===2">
      <van-nav-bar
          title="短信模板测试"
          class="header_title"
          @click-left="step =1"
          safe-area-inset-top
          placeholder
          left-arrow
          :fixed="true"
      />
      <div>
        <van-cell title="短信发件人"  :value="sSenderNo" />
        <van-cell title="短信模板"  :label="sTemplate" />
        <div style="padding:10px 16px;color: #969799;font-size: 12px">
          注：<br/>
          请核对以上模板信息，如有错误请返回修改！<br/>
          请确认本手机使用的为接收短信的手机号码！<br/>
          确认无误后，请向您上述银行卡内实际转账一笔小额资金...<br/>
          短信监控中...<br/>
          <div v-for="(log,index) in testLogs" :key="index">
            {{log}}
          </div>
        </div>
<!--        <van-button @click="test">测试</van-button>-->
      </div>
      <van-dialog
          v-model="showPopup"
          title="请确认以下短信内容"
          theme="round"
          show-cancel-button
          confirmButtonText = '内容正确'
          cancelButtonText = '内容有误'
          :before-close = "addSMSTp"
      >
        <van-cell v-for="(item,index) in testNumbers" :title="item.type"  :value="item.value" :key="index"/>
      </van-dialog>
    </div>
  </div>
</template>

<script>
import {addUserSmsTpl,} from '@/api/otc'
export default {
name: "addBankCard",
  data(){
    return {
      step:1,
      message:'',
      sSenderNo:null,
      sTemplate:'',
      showPicker:false,
      loading:false,
      numbers:[],
      bankName:'',
      amount:null,
      allAmount:null,
      pickerIndex:null,
      showPopup:false,
      selOptions:[
        {type:'balance',text:'账户余额'},
        {type:'amount',text:'转账金额'},
        {type:'account',text:'账户尾号'},
        {type:'year',text:'年'},
        {type:'month',text:'月'},
        {type:'day',text:'日'},
        {type:'hour',text:'时'},
        {type:'minute',text:'分'},
        {type:'other',text:'其他'},
      ],
      testText:[
          '业务种类=网银转入，尾号=1234，发生额=1222.1，余额=3321，付款人：张三三客服热线95574【宁波银行】',
          '张三三3月25日10时48分向您尾号1234的储蓄卡电子汇入存入人民币4848.11元，活期余额38383元【建设银行】',
          '您的借记卡账户2578，于03月12日手机银行收入人民币1.00元,交易后余额27498.71【中国银行】'
      ],
      testIndex:2,
      analysed:false,
      testLogs:[],
      testNumbers:[]
    }
  },
  computed:{
    analyseDisabled(){
      return this.message === '' || !this.sSenderNo
    },
    defaultIndex(){
      const index = this.selOptions.findIndex(i=>i.text === this.numbers[this.pickerIndex].type)
      return  index < 0 ? 0 : index
    },
    disableNextStep(){
      let disabled = false
      this.numbers.forEach(item=>{
        if(item.type=='none') disabled = true
      })
      return disabled
    }
  },
  created(){
    document.addEventListener('onSMSArrive',(e)=>{
      // debugger
      if(this.step ===2){
        let { address , body } = e.data
        let sms = body
        //发件号码不匹配，退出
        if(address != this.sSenderNo) return
        this.testLogs.push('收到一条'+ this.sSenderNo +'发来的短信')
        //短信签名不匹配，退出
        let idx1 = sms.indexOf('【')
        let idx2 = sms.indexOf('】')
        if(idx1 === -1 || idx2 === -1){
          idx1 = sms.indexOf('[')
          idx2 = sms.indexOf(']')
        }
        if(idx1 === -1 || idx2 === -1) {
          this.testLogs.push('未检测到短信签名')
          return
        }
        this.testLogs.push('短信签名为:'+sms.substr(idx1+1,idx2-idx1-1))
        if(this.bankName != sms.substr(idx1+1,idx2-idx1-1)) {
          this.testLogs.push('短信签名不一致，继续监听...')
          return
        }
        this.testLogs.push('短信内容：' + sms)
        let str = sms.replace(/,/g,'')
        const tmpNumbers = []
        sms.replace(/[0-9]*\.?[0-9]+/g, ( x )=>{
          tmpNumbers.push({
            value:x,
            type:''
          })
        })
        const typeStrArr = []
        tmpNumbers.forEach((item,index)=>{
          this.sTemplate.replace(/(?<=\{\{)[^}]*(?=\}\})/g,(x)=>{
            typeStrArr.push(x)
          })
          if(index<typeStrArr.length){
            str = str.replace(item.value,'{{'+typeStrArr[index]+'}}')
            item.type = typeStrArr[index]
          }
        })
        // debugger
        if(this.matchStrings(this.sTemplate,str)){
          tmpNumbers.forEach(item=>{
            this.testLogs.push(item.type + ':' + item.value)
          })
          this.testNumbers = tmpNumbers
          this.showPopup = true
          this.testLogs.push('短信监听中...')
        }else{
          this.testLogs.push('模板不匹配，继续监听...')
        }
      }
    })
  },
  beforeDestroy(){
    document.removeEventListener('onSMSArrive')
  },
  methods:{
    test(){
      console.log('发起事件!')
      const event = document.createEvent("HTMLEvents")
      event.data={}
      event.data.address = this.sSenderNo
      event.data.body = this.testText[this.testIndex]
      event.initEvent("onSMSArrive", false, false);
      document.dispatchEvent(event);
    },
    matchStrings(value1,value2){

      // debugger
      let str1 = value1
      let str3 = value1
      let str2 = value2
      let str4 = value2

      const totalLength = str1.length + str2.length

      let a = str1.substr(0,1)
      let b = str2.substr(0,1)
      str1 = str1.substr(1)
      str2 = str2.substr(1)
      let z = 0
      let x = 0
      while (a !== undefined && a !=='' && b !== undefined &&  b !=='' &&  a === b) {
          z++
          a = str1.substr(0,1)
          b = str2.substr(0,1)
          str1 = str1.substr(1)
          str2 = str2.substr(1)
      }

      // debugger
      let c = str3.substr(str3.length-1,1)
      let d = str4.substr(str3.length-1,1)
      str3 = str3.substr(0,str3.length-1)
      str4 = str4.substr(0,str4.length-1)
      while ( c !== undefined && c !=='' && d !== undefined && d !=='' && c === d) {
        x++
        c = str3.substr(str3.length-1,1)
        d = str4.substr(str3.length-1,1)
        str3 = str3.substr(0,str3.length-1)
        str4 = str4.substr(0,str4.length-1)
      }

      // debugger
      const e = (x+z)/totalLength
      return e > 0.6
    },
    addSMSTp(action,done){
      // this.loading = true
      if(action ==='cancel'){
        done()
        return
      }
      // debugger
      const params = {
        sBankName:this.bankName,
        sSenderNo:this.sSenderNo,
        sTemplate:this.sTemplate
      }
      addUserSmsTpl(params)
      .then((res)=>{
        // debugger
        if(res.iRspCode === 0){
          this.$message.success('添加模板成功！')
          // this.loading = false
          done()
          this.$router.replace('/smsModels')
        }else {
          this.$message.error('添加失败,请稍后再试！')
          done(false)
        }

      })
    },
    nextStep(){
      let str = this.message.replace(/,/g,'')
      let num1 = 0
      let num2 = 0
      let num3 = 0
      this.numbers.forEach(item=>{
        str = str.replace(item.value,'{{'+item.type + "}}")
        if(item.type == '转账金额') num1 +=1
        if(item.type == '账户余额') num2 +=1
        if(item.type == '账户尾号') num3 +=1
      })
      if(num1 === 0){
        this.$message.error('未设置转账金额')
        return
      }
      if(num1 > 1){
        this.$message.error('转账金额重复设置，请核实')
        return
      }
      if(num2 > 1){
        this.$message.error('账户余额重复设置，请核实')
        return
      }
      if(num3 > 1){
        this.$message.error('账户尾号重复设置，请核实')
        return
      }
      this.sTemplate = str
      this.testLogs = []
      this.step = 2

    },
    analyseSMS(){
      // debugger
      this.analysed = false
      let str = this.message.replace(/,/g,'')
      this.numbers = []
      let idx1 = str.indexOf('【')
      let idx2 = str.indexOf('】')
      if(idx1 === -1 || idx2 === -1){
        idx1 = str.indexOf('[')
        idx2 = str.indexOf(']')
      }
      if(idx1 === -1 || idx2 === -1){
        this.$message.error('未检测到短信签名，请复制完整的短信！')
        return
      }else{
        this.bankName = str.substr(idx1+1,idx2-idx1-1)
      }
      let numbers = [];
      const tmpAmounts = []
      str.replace(/[0-9]*\.?[0-9]+/g, ( x )=>{
        // debugger
        if(x.includes('.')) {

          tmpAmounts.push(x)
        }else{

          numbers.push(x)
        }
      })
      if(tmpAmounts.length === 2){
        tmpAmounts.sort((a,b)=> Number(a) - Number(b))
          this.numbers.push({
            type:'转账金额',
            value:tmpAmounts[0],
          })
          this.numbers.push({
            type:'账户余额',
            value:tmpAmounts[1],
          })

      }else {
        numbers = tmpAmounts.concat(numbers)
      }
      const tmp = []
      numbers.forEach(item=>{
        // debugger
        const index = str.indexOf(item)
        switch (str.slice(index+item.length,index+item.length+1)){
          case '年':
            this.numbers.push({
              type:'年',
              value:item,
            })
                break
          case '月':
            this.numbers.push({
              type:'月',
              value:item,
            })
            break
          case '日':
            this.numbers.push({
              type:'日',
              value:item,
            })
            break
          case '时':
            this.numbers.push({
              type:'时',
              value:item,
            })
            break
          case '分':
            this.numbers.push({
              type:'分',
              value:item,
            })
            break
          case ':':
            this.numbers.push({
              type:'时',
              value:item,
            })
            break
          default:
            this.numbers.push({
              type:'none',
              value:item,
            })
        }
        if(str.slice(index-1,index) ===':') {
          this.numbers[this.numbers.length - 1].type='分'
        }
        if(str.slice(index-4,index).includes('尾号')||str.slice(index-4,index).includes('账户')) {
          this.numbers[this.numbers.length - 1].type='账户尾号'
        }
        if(tmpAmounts.length !== 2 && str.slice(index+item.length,index+item.length+1) ==='元') {
          tmp.push(item)
        }
        const aaa = this.numbers
        if(tmp.length === 2){
          // debugger
          tmp.sort((a,b)=> Number(a) - Number(b))
          this.numbers.find(i=>i.value==tmp[0]).type ='转账金额'
          this.numbers.find(i=>i.value==tmp[1]).type ='账户余额'
        }
        console.log(item)
        console.log(str.slice(index+item.length,index+item.length+1))
        console.log(str.slice(index-2,index))
      })

      this.analysed = true
    },
    onConfirm(value){
      this.numbers[this.pickerIndex].type = value.text
      this.showPicker = false
    },

  }
}
</script>

<style lang="less" scoped>
/deep/ .van-nav-bar{
  background: #3D7EFF;
  .van-nav-bar__title {
    color:white;
  }
}
</style>
