<template>
    <div>
        <keep-alive>
            <router-view/>
        </keep-alive>
          <van-dropdown-menu>
            <van-dropdown-item v-model="orderType" :options="option1" @change="tabchange"/>
            <van-dropdown-item v-model="statusValue" :options="option2" @change="tab2change" />
<!--            <van-button>刷新</van-button>-->
          </van-dropdown-menu>

        <div class="list-cont-box">
            <van-empty description="暂无满足条件的订单数据" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length==0&&loadedall" />
            <van-pull-refresh v-else style="min-height: 65vh;background:none;padding-bottom: 12px;" v-model="refreshing" @refresh="refreshData">
                <van-list
                    v-model="loading"
                    loading-text=" "
                    :finished="loadedall"
                    finished-text="没有更多了,下拉刷新"
                    :error.sync="error"
                    error-text="请求失败，点击重新加载"
                    @load="loadData"
                >
                <div class="warp-box">
                    <div v-for="(item,index) in dataSource" :key="index" style="background:#fff;" class="order-list-cont">
                        <van-cell :value="initState(item.iOrderState)" class="cust-hed" :class="{'no-pays':item.iOrderState==1,'suss-pays':item.iOrderState==10}">
                            <template #title>
                                <span class="bs-span">
                                    <van-tag type="primary" v-if="item.iOrderType === 2 ">充值订单</van-tag>
                                    <van-tag type="warning" v-if="item.iOrderType === 1">提币订单</van-tag>
                                </span>
                            </template>
                        </van-cell>
                        <div class="list-cont">
                            <div class="flex-wrapper" align-items="start">
                                <span class="flex-demo ord_title" justify-content="start" fill>数量(USDT)</span>
                                <span class="flex-demo ord_title" justify-content="start" fill>交易额({{item.sUnit}})</span>
                              <span class="flex-demo ord_title" justify-content="start" fill>奖励(USDT)</span>
                            </div>
                            <div class="flex-wrapper" align-items="start">
                                <span class="flex-demo ord_cont" justify-content="start" fill>{{ Number(item.dTradeQuantity).toFixed(2)}}</span>
                                <span class="flex-demo ord_cont" justify-content="start" fill>{{ Number(item.dTradeAmount).toFixed(2) }}</span>
                                <span class="flex-demo ord_cont" justify-content="start" fill>{{ Number(item.dAcceptanceReward).toFixed(2) }}</span>
                            </div>
                            <div style="margin-top: 8px" align-items="start">
                                <span class="flex-demo ord_title " justify-content="start" fill>
                                  {{ moment(item.iCreateTime).format('MM-DD HH:mm:ss') }}
                                </span>
                                <button class="go-detail" @click="$router.push('/orderdetail?id='+item.sOrderId)">查看详情</button>
                            </div>
                        </div>
                    </div>
                </div>
                </van-list>
            </van-pull-refresh>
        </div>
    </div>
</template>

<script>
    import {otcUrl, publishAds, getPayMode, getPayDictionary, getMyAdsList, adsUp, adsDown} from '@/api/otc'
    import {mapGetters,mapActions} from 'vuex'
    import moment from 'dayjs'
    import {GpayListMixin} from '@/mixins/GpayListMixin'

    import {getOrderList,getShareOrderList} from '@/api/otc'

    export default {
        name: "Order",
        mixins: [GpayListMixin],
        components: {
        },
        data() {
            return {
                activeKey:"1",//状态
                orderRoleType:0,
                myOrderList: [],
                myOrderParam: {},
                queryParam:{
                    iOrderType:0,
                    iOrderProcess:1,
                    sToken:'USDT',
                    iRoleType:0
                },
                resultName: 'vList',
                url: {
                    list: otcUrl.GetShareOrderList,
                },
                getTradingOrderInterval: null,
                option1: [
                  { text: '充值&提币', value: 0 },
                  { text: '充值订单', value: 2 },
                  { text: '提币订单', value: 1 },
                ],
                option2: [
                  { text: '进行中', value: '1' },
                  { text: '已完成', value: '2' },
                  { text: '其他', value: '3' },
                  { text: '全部', value: '0' },
                ],
              orderType:0,
              statusValue:'1'
            };
        },
        created() {
            //查询数据
            //this.initParam();
            this.refreshData();
            // this.getTradingOrderInterval = setInterval(()=>{
            //     const params = this.queryParam
            //     params.iPageNo = 0
            //     params.iPageSize = 10
            //     params.sColumn = "iCreateTime"
            //     params.sOrder = "desc"
            //     params.sToken = "USDT"
            //
            //     getShareOrderList(params)
            //     .then(res=>{
            //         console.log('---------------定时跑')
            //         if(res.data.vList&&res.data.vList.length >=1){
            //             this.dataSource = res.data.vList
            //         }
            //     })
            // },3000)
        },
        computed:{
            ...mapGetters(['getOrderCount','getOrderState']),
        },
        methods: {
            moment,
            ...mapGetters(['userType']),
            ...mapActions(['GetOrderCountFun']),
            initParam() {
                // this.queryParam.iOrderType = 0;
                this.queryParam.iOrderProcess =parseInt(this.statusValue);
                this.queryParam.sToken = 'USDT';
                this.queryParam.iRoleType = this.orderRoleType;
            },
            tabchange(value){
                this.queryParam.iOrderType = value
                this.refreshData();
            },
            tab2change(value){
                this.initParam();
                if(value=="0"){
                    this.queryParam.iOrderProcess = 0;
                }
                if(value=="1"){
                    this.queryParam.iOrderProcess = 1;
                }
                if(value=="2"){
                    this.queryParam.iOrderProcess =2;
                }
                if(value=="3"){
                    this.queryParam.iOrderProcess =3;
                }
                this.refreshData();
            },
            initState(state){
                switch (state) {
                    case 1:
                        return '未支付'
                    case 2:
                        return '已支付 等待放币'
                    case 3:
                        return '申诉中'
                    case 4:
                        return '放币超时'
                    case 10:
                        return '已完成'
                    case 11:
                        return '已取消'
                    case 12:
                        return '支付超时'
                }
            },
        },
        beforeDestroy(){
            clearInterval(this.getTradingOrderInterval)
        },
        watch: {
            getOrderCount (nVal) {
                console.log(nVal,'----getOrderCount');
              this.refreshData();
            },
            getOrderState (nVal){
                console.log(nVal,'----getOrderState');
                if(nVal){
                    setTimeout(() =>{
                        this.refreshData();
                    },100)
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .home-page {
        text-align: center;
        margin-top: 60px;
    }
    .item-title{
        font-size: .7rem;
        margin-top: .6rem;
    }
    .flex-demo {
        // width: 50px;
        height: 32px;
        background-color: white;
        // text-align: center;
        line-height: 32px;
        margin-left: 8px;
        flex: 1;
    }
    .flex-wrapper{
        display: flex;
    }
    .ord_title{
        font-size: 12px;
        font-family: PingFang SC;
        font-weight: 400;
        color: #919DAF;
        line-height: 36px;
        display: inline-block;
    }
    .ord_cont{
        color: #212833;
        font-size: 14px;
        font-family: PingFangSC-Regular;
        position: relative;
        top: 0;
        background: none;
        height: auto;
        line-height: normal;
        text-align: left;
        display: inline-block;
    }
    .user-buy{
        font-size: 13pxrem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #212833;
        line-height: 36px;
        margin-bottom: 0.16rem;
    }
    .van-pull-refresh{
        background: #fff;
    }
    .ords-hed{
        background-color: @gpay-bg3D7EFF;
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 6;
        padding-bottom: .18rem;
    }
    /deep/ .ords-hed .van-tabs__nav{
        background-color: @gpay-bg3D7EFF;
    }
    .dw-color{
        font-style: normal;
        font-family: PingFang SC;
        font-weight: 400;
        color: @gpay-bg3D7EFF;
    }
    .order-list-cont{
        background: #FFFFFF;
        box-shadow: 0px 4px 9px 1px rgba(61, 126, 255, 0.23);
        border-radius: 10px;
        margin: 0 10px;
        padding: .2rem;
        margin-bottom: 12px;
        margin-top: 6px;
    }
    /deep/.van-tabs__line{
        background-color: @gpay-bg3D7EFF;
    }
    /deep/.types .van-tab{
        color: rgba(255,255,255,.5);
        font-size: .3rem;
        line-height: normal;
    }
    .cust-hed.van-cell{
        padding: 10px 8px;
        position: relative;
        .bs-span{
            //position:absolute;
            //top: 50%;
            padding-left: 5px;
            width: 100px;
            line-height: 18px;
            transform: translateY(-60%);
        }
    }
    .list-cont-box{
       padding-top: 10px;
       padding-bottom: .8rem;
    }
    .no-pays .van-cell__value{
        color:@gpay-colorFF5763;
    }
    .suss-pays .van-cell__value{
        color: @gpay-color2EBD85;
    }
    .two-mun{
        color: #FEFEFE;
        padding: .1rem 0 0.16rem 0;
        background: @gpay-bg3D7EFF;
    }
    /deep/ .van-cell{
        font-size: .30rem;
        line-height: normal;
    }
    /deep/ .two-mun .van-tab{
        color: rgba(255,255,255,.6);
        font-size: 0.24rem;
        line-height: normal;
    }
    /deep/.two-mun .van-tab--active{
        span.van-tab__text{
            font-family: PingFang SC;
            font-weight: 400;
            color: @gpay-bg3D7EFF;
            background: #FFFFFF;
            border-radius: 15px;
            padding: 0.008rem 0.16rem;
        }
    }

    .go-detail{
        background: #FFFFFF;
        border: 1px solid @gpay-bg3D7EFF;
        border-radius: 24px;
        font-size: 12px;
        font-family: PingFang SC;
        font-weight: 400;
        color: @gpay-bg3D7EFF;
        padding: 1px 9px;
        margin-top: 6px;
        margin-right: 12px;
        float: right;
    }
    .custom-title{
        img{
            width: 28px;
            margin-right: .1rem;
            position: relative;
            top: -2px;
        }
    }
    /deep/ .van-tabs__wrap,.van-tab__text--ellipsis{
        overflow: visible;
    }
</style>
