import Vue from 'vue'
import { postAction } from './index'

const baseUrl = process.env.VUE_APP_GPAY_API_URL

const walletUrl = {
    GetWithdrawLimit:'/v1/udc/GetWithdrawLimit',                                                        //查询提币规则
    GetDepositAddress:'/v1/udc/GetDepositAddress',                                                      //获取充值地址
    UserWithdraw:'/v1/udc/UserWithdraw',                                                                //用户提币
    QueryWithdraw:'/v1/udc/QueryWithdraw',                                                              //查询提币记录
    GetMerchantTokens:'/v1/udc/GetMerchantTokens',                                                      //获取可提币种列表
    QueryDeposit:'/v1/udc/QueryDeposit',                                                                //查询充值记录
    EditWalletAddress:'/v1/udc/EditWalletAddress',                                                      //修改提现地址
    AddWalletAddress:'/v1/udc/AddWalletAddress',                                                        //添加提币地址
    DelWalletAddress:'/v1/udc/DelWalletAddress',                                                        //删除提币地址
    GetWalletAddress:'/v1/udc/GetWalletAddress',                                                        //获取提币地址
    QueryFinanceList:'/v1/udc/QueryFinanceList',                                                        //查询财务记录
}

const getWithdrawLimit = (params)=>postAction(walletUrl.GetWithdrawLimit,params);                       //查询提币规则
const getDepositAddress = (params)=>postAction(walletUrl.GetDepositAddress,params);                     //获取充值地址
const userWithdraw = (params)=>postAction(walletUrl.UserWithdraw,params);                               //用户提币
const queryWithdraw = (params)=>postAction(walletUrl.QueryWithdraw,params);                             //查询提币记录
const getMerchantTokens = (params)=>postAction(walletUrl.GetMerchantTokens,params);                     //获取可提币种列表
const queryDeposit = (params)=>postAction(walletUrl.QueryDeposit,params);                               //查询充值记录
const editWalletAddress = (params)=>postAction(walletUrl.EditWalletAddress,params);                     //修改提现地址
const addWalletAddress = (params)=>postAction(walletUrl.AddWalletAddress,params);                       //添加提币地址
const delWalletAddress = (params)=>postAction(walletUrl.DelWalletAddress,params);                       //删除提币地址
const getWalletAddress = (params)=>postAction(walletUrl.GetWalletAddress,params);                       //获取提币地址
const gueryFinanceList = (params)=>postAction(walletUrl.QueryFinanceList,params);                       //获取提币地址

for(let k in walletUrl) {
    walletUrl[k] = baseUrl + walletUrl[k]
}

export {
    walletUrl,
    getWithdrawLimit,
    getDepositAddress,
    userWithdraw,
    queryWithdraw,
    getMerchantTokens,
    queryDeposit,
    editWalletAddress,
    addWalletAddress,
    delWalletAddress,
    getWalletAddress,
    gueryFinanceList,
}