<template>
  <div>
    <van-nav-bar
        title="承兑商入驻"
        class="header_title"
        right-text="退出"
        @click-right="quit"
        safe-area-inset-top
        placeholder
        :fixed="true"
    />
    <div v-if="changeNameStatus==0">
      <step-one />
    </div>
    <div v-else>
      <div v-if="steps < 4 && steps != null">
        <step-two :status="steps" @commit="steps = 1 "/>
      </div>
      <div v-if="steps > 4 && steps<20 && steps !=13">
        <div style="height: 1.5rem;"></div>
        <step-three :status="steps" @ok="getInfo"/>
      </div>
      <div v-if="steps ==13 || steps>20">
        <div style="height: 1rem;"></div>
        <step-four :depositAmount="certificateExtInfo.iDepositAmount"/>
      </div>
    </div>
  </div>
</template>

<script>
import {Toast} from 'vant';
import StepOne from '@/views/register/step_one'
import StepTwo from '@/views/register/step_two'
import StepThree from '@/views/register/step_three'
import StepFour from "@/views/register/step_four"


import { getAdsExtCertificate} from '@/api/udc'
import {mapGetters,mapActions} from 'vuex'

export default {
  name: "register_steps",
  components: {
    [Toast.name]: Toast,
    StepOne,
    StepTwo,
    StepThree,
    StepFour
  },
  data(){
    return {
      steps:null,
      certificateExtInfo:{},
      getInterval:null,
    }
  },
  created(){
    this.getInfo()
    this.GetUserExten()
    this.setCertificateInterval()
  },
  computed:{
    changeNameStatus(){
      return  this.iIsChangeNameStatus()
    },

  },
  beforeDestroy(){
    clearInterval(this.getInterval)
  },
  methods:{
    ...mapGetters(['uid','iIsChangeNameStatus','iDrawalStatus','switchEmailVerify','switchCellphoneVerify','iAdsStatus']),
    ...mapActions(['GetUserExten','Logout']),

    getInfo(){
      getAdsExtCertificate({iIdle:0})
          .then(res=>{
            this.certificateExtInfo = res.data.stInfor
            this.steps = this.certificateExtInfo.iStep
            if(this.iAdsStatus() ===2){
              this.$router.push('/main')
            }
          })
    },
    setCertificateInterval(){
      this.getInterval = setInterval(()=>{
        this.getInfo()
        this.GetUserExten()
      },5000)

    },
    quit(){
      this.$dialog.confirm({
        message: '您确定退出当前账户',
        //messageAlign: 'left',
        confirmButtonText:'退出',
        confirmButtonColor: '#ee0a24',
        cancelButtonText:'取消'
      }).then(() =>{
        this.Logout();
      })
    },
  },


}
</script>

<style lang="less" scoped>
/deep/ .van-nav-bar{
  background: @gpay-bg3D7EFF;
.van-nav-bar__text{
  color: @gpay-colorffffff;
}
.van-nav-bar__title{
  color: @gpay-colorffffff;
}
.van-nav-bar__right .van-nav-bar__text{
  color: @gpay-color3D7EFF;
  background: #ffffff;
  border-radius: 20px;
  padding: 1px 6px;
  font-size: 12px;
}
}

/deep/ .van-icon-passed {
  font-size: 1rem;
  color: @gpay-color3D7EFF;
  margin-top: .3rem;
}


.van-checkbox {
  margin-right: 10px;
}



</style>