<template>
    <div>
        <van-dialog v-model="visible" title="绑定邮箱" show-cancel-button :beforeClose="editMail" v-if="false">
            <van-field
                class="email-icon"
                v-model="myemail"
                center
                clearable
                required
                label="邮箱"
                placeholder="请输入邮箱"
            >
            </van-field>
            <van-field
                class="email-icon"
                v-model="emailCode"
                center
                clearable
                required
                :maxlength="6"
                label="邮箱验证码"
                placeholder="请输入邮箱验证码"
            >
                <template #button>
                    <van-button size="small" type="info" @click="getEmailCode" :disabled="!getEmailCodeFlag">{{
                        getEmailCodeFlag ? '发送验证码' : emailCountTime + '秒' }}
                    </van-button>
                </template>
            </van-field>
        </van-dialog>
        <van-popup v-model="visible"
            :closeable="false"
            round
            title=""
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            position="bottom"
            :style="{ height: '46%', }"
        >
            <div class="popup-title-hed">{{iVerifyBusi==7?'绑定邮箱':'更换邮箱'}}</div>
            <van-field
                v-model="myemail"
                center
                clearable
                class="email-icon"
                :maxlength="30"
                :left-icon="require('../../assets/img/email-icon.png')"
                placeholder="请输入邮箱"
            >
            </van-field>
            <van-field
                v-model="emailCode"
                center
                clearable
                class="email-icon"
                :left-icon="require('../../assets/img/pwd-icon.png')"
                :maxlength="6"
                placeholder="请输入邮箱验证码"
            >
            <template #button>
                <van-button size="small" style="height:26px" type="info" @click="getEmailCode" :disabled="!getEmailCodeFlag">{{
                    getEmailCodeFlag ? '发送验证码' : emailCountTime + '秒' }}
                </van-button>
            </template>
            </van-field>
            <div style="margin: 36px 16px; 0 16px">
                <van-button style="width:45%;display: inline-block;background: #FFFFFF;border: 1px solid #C8CBCE;color: #919DAF;" round block type="info" @click="editMail(true)">
                    取消
                </van-button>
                <van-button style="width:45%;display: inline-block; margin-left:.4rem;" round block type="info" @click="editMail(false)">
                    确认
                </van-button>
            </div>
        </van-popup>

        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi='iVerifyBusi'
            @ok="applyVerify"
        />
    </div>
</template>

<script>
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {changePassword, emailVerify, sendEmailVerifyCode} from '@/api/auth'
    import SafeVerifyModal from "@/components/SafeVerifyModal/index";
    import {isEmail} from "@/utils/validate"

    export default {
        name: 'user-setting-email',
        components: {
            [Toast.name]: Toast,
            SafeVerifyModal,
        },
        data() {
            return {
                visible: false,
                fixed: true,
                message: '',
                getEmailCodeFlag: true,
                label: '',
                myemail: '',
                emailCode: '',
                iVerifyBusi: 7,
                action: null,
                emailCountTime: 60
            }
        },
        methods: {
            ...mapGetters(['isHasEmail', 'isLogin', 'userName', 'cellphone', 'email', 'switchEmailVerify', 'switchCellphoneVerify', 'switchGoogleVerify']),
            ...mapActions(['GetUserExten', 'GetUserInfo']),
            getEmailCode() {
                if (!isEmail(this.myemail)) {
                    this.$toast('请输入正确的邮箱！');
                    return
                }
                const params = {
                    iSource: 2,
                    iSendMethod: 2,
                    iVerifyBusi: this.iVerifyBusi,
                    sEmail: this.myemail
                }
                sendEmailVerifyCode(params)
                    .then(() => {
                        this.$toast('邮箱验证码已发送!')
                        this.getEmailCodeFlag = false
                        this.emailCaptchaInterval = setInterval(() => {
                            this.countEmail()
                        }, 1000)
                    })
            },
            countEmail() {
                if (this.emailCountTime <= 0) {
                    clearInterval(this.emailCaptchaInterval)
                    this.emailCountTime = 60
                    this.getEmailCodeFlag = true
                } else {
                    this.emailCountTime -= 1
                }
            },
            goEmail() {
                this.visible = true;
                this.getEmailCodeFlag = true;
                this.emailCountTime = 60;
                this.iVerifyBusi = 7;
            },
            editMail(type){
                if(type){
                    this.myemail = ''
                    this.emailCode = ''
                    this.visible = false;
                }else{
                    console.log(isEmail(this.myemail))
                    if (!isEmail(this.myemail)) {
                        this.$toast('请输入正确的邮箱！');
                        return
                    }
                    if (!this.emailCode) {
                        this.$toast('请输入邮箱验证码！');
                        return
                    }
                    if (!this.isHasEmail()) {
                        const params = {
                            iSource: 1,
                            sEmail: this.myemail,
                            sNewEmailVerifyCode: this.emailCode,
                        }
                        emailVerify(params)
                            .then(() => {
                                this.$toast.success('绑定成功！')
                                this.GetUserInfo();
                                this.visible = false;
                            })
                    } else {
                        //换绑邮箱需要走安全验证
                        this.$refs.safeVerifyModal.visible = true
                    }
                }
            },
            editMails(action, done){
                if (action === 'cancel') {//取消按钮
                    done();
                    this.myemail = ''
                    this.emailCode = ''
                } else if (action === 'confirm') {//确定按钮
                    //向后端传值并关闭dialog弹出框
                    done(false);
                    this.visible = true;
                    if (!this.myemail&&isEmail(this.myemail)) {
                        this.$toast('请输入正确的邮箱！');
                        return
                    }
                    if (!this.emailCode) {
                        this.$toast('请输入邮箱验证码！');
                        return
                    }
                    if (!this.isHasEmail()) {
                        const params = {
                            iSource: 1,
                            sEmail: this.myemail,
                            sNewEmailVerifyCode: this.emailCode,
                        }
                        emailVerify(params)
                            .then(() => {
                                this.$toast.success('绑定成功！')
                                this.GetUserInfo();
                                this.visible = false;
                                done();
                            })
                    } else {
                        //换绑邮箱需要走安全验证
                        this.$refs.safeVerifyModal.visible = true
                    }
                }
            },
            editMails(e) {
                // if (!this.myemail) {
                //     this.$toast('请输入邮箱！');
                //     return
                // }
                // if (!this.emailCode) {
                //     this.$toast('请输入邮箱验证码！');
                //     return
                // }
                // if (!this.isHasEmail()) {
                //     const params = {
                //         iSource: 1,
                //         sEmail: this.myemail,
                //         sNewEmailVerifyCode: this.emailCode,
                //     }
                //     emailVerify(params)
                //         .then(() => {
                //             this.$toast.success('绑定成功！')
                //             this.GetUserInfo();
                //             this.visible = false;
                //         })
                // } else {
                //     //换绑邮箱需要走安全验证
                //     this.$refs.safeVerifyModal.visible = true
                // }
            },
            applyVerify(codes) {
                const params = {
                    iSource: 2,
                    sEmail: this.myemail,
                    sNewEmailVerifyCode: this.emailCode,
                }
                if (this.switchEmailVerify()) params.sEmailVerifyCode = codes.sEmailVerifyCode
                if (this.switchCellphoneVerify()) params.sSMSVerifyCode = codes.sSMSVerifyCode
                if (this.switchGoogleVerify()) params.sGoogleVerifyCode = codes.sGoogleVerifyCode
                
                emailVerify(params)
                    .then(() => {
                        this.$toast.success('绑定成功！')
                        this.visible = false
                        this.GetUserInfo();
                        this.$refs.safeVerifyModal.visible = false
                    })
            }
        }
    }

</script>

<style lang="scss" scoped>
    .popup-title-hed{
        font-size: .32rem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #000000;
        margin: .32rem 0 .24rem .42rem;
    }
    /deep/.email-icon .van-icon{
        font-size: .42rem;
        margin-top: -4px;
        margin-right: .06rem;
        .van-icon__image{
            height: auto;
        }
    }
</style>
