import Vue from 'vue'
import { postAction } from './index'

const baseUrl = process.env.VUE_APP_GPAY_API_URL

const ossUrl = {
    Report:'/v1/oss/Report',                            //APP上报异常
    GetSignedUrl:'/v1/oss/GetSignedUrl',                //获取私密文件链接
    PrivateUpload:'/v1/oss/PrivateUpload',              //上传私密文件
    PublicUpload:'/v1/oss/PublicUpload',                //上传公开文件
}
for(let k in ossUrl) {
    ossUrl[k] = baseUrl + ossUrl[k]
}

const getSignedUrl =async (url)=>{                                                          //获取私密文件链接
    const res = await postAction(ossUrl.GetSignedUrl, {sOssKey:url})
    return res.data.sUrlPath
}

const getSignedUrlArr = async (url)=>{                                                          //获取私密文件链接[]
    return new Promise((resolve, reject)=>{
        let arr = [];
        postAction(ossUrl.GetSignedUrl, {sOssKey:url}).then((res) =>{
            arr.push(res.data.sUrlPath)
            resolve(arr.join(','))
        }).catch(() =>{
            reject([])
        })
    })
}

export {
    ossUrl,
    getSignedUrl,
    getSignedUrlArr
}
