<template>
    <div style="background:#fff;height: 100%;">
        <!-- <keep-alive >
            <router-view v-if="$route.meta.keepAlive"/>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"/> -->
        <header class="hed-box">
            <van-nav-bar
                :title="detail.sToken"
                left-arrow
                @click-left="onClickLeft"
                :border="false"
                :fixed="false"
            />
            <div class="conts-monery">
                <ul>
                    <li>
                        可用
                        <p>{{detail.iAvailableAmount}}</p>
                    </li>
                    <li>
                        冻结
                        <p>{{detail.sToken ==='USDT' ? (Number(detail.iFrozenAmount) - Number(iDepositAmount())).toFixed(8) : detail.iFrozenAmount}}</p>
                    </li>
                    <li>
                        法币估值(CNY)
                        <p>{{detail.sToken ==='USDT' ? depositCnyAmount(detail) : Number(detail.iFiat).toFixed(2)}}</p>
                    </li>
                </ul>
            </div>
        </header>
        <div class="mon-list">
            <van-cell value="充提记录" class="f_header" />
            <van-empty description="没有数据" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length == 0 && loadedall" />
            <van-pull-refresh v-else v-model="refreshing" style="min-height:52vh;padding-bottom:16px;" @refresh="refreshData">
                <van-list
                    v-model="loading"
                    :finished="loadedall"
                    finished-text="没有更多了"
                    @load="loadData"
                >
                    <span v-for="(item,index) in dataSource" :key="index">
                        <van-cell title="类型" :value="initType(item.iType)" class="type-color" isLink @click="goPage(item)"/>
                        <van-row class="fin_list" type="flex" gutter="20" >
                            <van-col span="8">数量{{item.iType}}</van-col>
                            <van-col span="8">状态</van-col>
                            <van-col span="8">时间</van-col>
                        </van-row>
                        <van-row class="fin_list f-conts" type="flex" gutter="20"  >
                            <van-col span="8">{{ Number(item.dAmount).toFixed(8) }}</van-col>
                            <van-col span="8">{{returnStatus(item.iStatus)}}</van-col>
                            <van-col span="8">{{moment(item.iCreateTime).format('YYYY-MM-DD HH:mm:ss')}}</van-col>
                        </van-row>
                    </span>
                </van-list>
            </van-pull-refresh>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {walletUrl} from '@/api/wallet'
    import {udcUrl} from '@/api/udc'
    import {getMerchantTokens} from '@/api/wallet'

    import bg_default from '../../assets/img/ic_asset_bg.png';
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import moment from 'dayjs'

    export default {
        name: 'user-financial',
        mixins: [GpayListMixin],
        components: {
            [Toast.name]: Toast,
        },
        data() {
            return {
                background_image: bg_default,
                type:0,
                detail:null,
                queryParam:{
                    iType:0,
                    sToken:''
                },
                resultName:'vRecords',
                url: {
                    list: walletUrl.QueryFinanceList,
                }
            }
        },
        computed:{},
        mounted() {

        },
        created() {
            const vBalance = Vue.ls.get("vBalance");
            this.detail = vBalance
            this.GetFiatQuote('BTC')
            this.getFiatQuoteInterval = setInterval(()=>{
                this.GetFiatQuote('BTC')
            },60000)
            this.initParam()
            this.refreshData();
        },
        beforeRouteLeave(to, from, next) {
            from.meta.keepAlive=true;
            next();
        },
        methods: {
            moment,
            ...mapGetters(['isLogin', 'userName', 'cellphone', 'email','iDepositAmount','fiatQuote']),
            ...mapActions(['GetUserExten','GetFiatQuote']),
            initParam() {
                this.queryParam.iType=0;
                this.queryParam.sToken=this.detail.sToken
            },
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            initType(type){
                switch (type) {
                    case 1:
                        return '提币'
                    case 2:
                        return '充值'
                }
            },
            returnStatus(index){
                switch (index){
                    case 5 :
                    return '提币完成'
                    case 6 :
                    return '拒绝提币'
                    case 7 :
                    return '提币失败'
                    case 8 :
                    return '用户取消'
                    case 9 :
                    return '异常订单'
                    case 13 :
                    return '充值完成'
                    case 14 :
                    return '充值失败'
                    default:
                    return '进行中'
                }
            },
            goPage(item){
                Vue.ls.set("vRecords", item);
                this.$router.push({name:'financialdetail'})
            },
            depositCnyAmount(item){
                const fiatQuote = this.fiatQuote()
                if(fiatQuote){
                    // debugger
                    const valueCNY = fiatQuote.find(i=>i.sName ==='Chinese Yuan').dValue
                    const value = fiatQuote.find(i=>i.sName === 'US Dollar').dValue
                    const amount =Number(item.iAvailableAmount) + Number(item.iFrozenAmount) - Number(this.iDepositAmount())
                    return (amount * valueCNY / value).toFixed(2)
                }else{
                    return 0.00000000
                }
            }
        }
    };

</script>

<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    .hed-box{
        background-color: @gpay-bg3D7EFF;
        position: fixed;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 6;
    }
    .conts-monery{
        margin-top: .4rem;
        ul{
            display: flex;
            li{
                font-size: .26rem;
                font-family: PingFang SC;
                font-weight: 400;
                color: #FFFFFF;
                flex: 1;
                text-align: center;
                p{
                    font-size: .28rem;
                }
            }
        }
    }
    .mon-list{
        top: 2.5rem;
        background: #fff;
        position: relative;
    }
    .f_header {
        font-size: .3rem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #000000;
        line-height: normal;
    }
    .fin_list{
        position: relative;
        text-align: left;
        background-color: white;
        margin-top: 5px;
        margin-left: 20px;
        margin-bottom: .1rem;
        color: #647288;
    }
    .f-conts{
        color: #000000;
        font-size: .3rem;
    }

    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }
    .user_avatar {
        margin-bottom: 10px;
        img {
            border: 0;
            border-radius: 50%;
        }
    }

    .warp-box{
        background: #F3F5FD;
        margin-top: 2.6rem;
    }
    
    .border-top{
        border-top: 1px solid #e4e5e6;
        padding-top: 12px;
    }
    .border-bm{
        border-bottom: 1px solid #ebedef;
        padding-bottom: 12px;
        margin-bottom: 12px;
    }
    .gflex{
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
    }
    .mbmt{
        margin-bottom: 6px;
    }
    .qtright{
        float: right;
        margin-right: 20px;
    }
    /deep/ .type-color .van-cell__title1{
        color: @gpay-color3D7EFF;
        font-size: .3rem;
        font-weight: bold;
    }
</style>
