<template>
    <div>
      <div v-if="drawalStatus!==1">
        <van-cell-group title="请设置资金密码">
          <van-field
              v-model="drawal.sDrawalPassword"
              center
              clearable
              required
              :maxlength="20"
              type="password"
              label="资金密码"
              placeholder="请输入资金密码"
          >
          </van-field>
          <van-field
              v-model="drawal.sDpassword"
              center
              clearable
              required
              :maxlength="20"
              type="password"
              label="确认密码"
              placeholder="请再次输入资金密码"
          >
          </van-field>
        </van-cell-group>
        <div class="bzt">
          在提币、放币、添加银行卡时将需要提供资金密码，请妥善保管，以免造成您的资产损失！
        </div>
        <div style="margin: 36px 16px; 0 16px">
          <van-button style="display: inline-block;" round block type="info" @click="drawalPsw">
            确认设置，下一步
          </van-button>
        </div>

        <!--安全验证-->
        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi='iVerifyBusi'
            @ok="applyVerify"
        />
      </div>
      <div class="user-pay-cont" v-if="pLoading && drawalStatus==1">
        <template v-if="payModes.length === 0">
          <van-empty  description="您暂未设置收款方式" :image="require('../../assets/img/empty-image-default.png')" />
          <van-button  icon="plus" type="default" size="mini" style="width:50%;margin:0 auto;padding:15px 0" round block @click="addPay">添加收款方式</van-button>
        </template>
        <template v-else>
          <div v-for="item in payModes" :key="item.iId" :name="item.iId">
            <div v-if="status === 12">
              <div class="cert-box p-mt">
                <a-result title="您的银行卡信息已提交，请等待审核结果！"></a-result>
              </div>
            </div>
            <div v-if="status === 14">
              <div class="cert-box p-mt">
                <a-result
                    status="error"
                    title="审核不通过！">
                  <template #extra>
                    <a-button key="console" type="primary" @click="resetBankCark">
                      重新提交
                    </a-button>
                  </template>
                </a-result>
              </div>
            </div>
            <div v-for="item in payModes" :key="item.iId">
              <van-cell-group title="银行卡信息">
                <van-cell title="银行名" :value="item.sBankName" />
                <van-cell title="银行支行" :value="item.sSubbranchBankName" />
                <van-cell title="账户名" :value="item.sName" />
                <van-cell title="银行账号" :value="item.sAccount" />
              </van-cell-group>
            </div>
          </div>
<!--          <van-swipe-cell class="plist border-bm" :before-close="beforeClose" v-for="item in payModes" :key="item.iId" :name="item.iId">-->
<!--            <div class="p-flex">-->
<!--              <div class="imgs">-->
<!--                <img class="pay-icon" src="../../assets/img/yhk.png">-->
<!--              </div>-->
<!--              <div class="p-cont">-->
<!--                <p class="p-title">{{item.sName}} </p>-->
<!--                <div class="card-info">-->
<!--                  <div>{{item.sAccount | bankNum}}</div>-->
<!--                  <div>{{item.sBankName}}</div>-->
<!--                </div>-->
<!--              </div>-->
<!--              <div v-if="item.iStatus ==1" class="r-info">-->
<!--                审核中...-->
<!--              </div>-->
<!--              <div v-if="item.iStatus ==3"  class="r-info">-->
<!--                审核未通过-->
<!--              </div>-->
<!--              <div class="r-bnt"  v-if="item.iStatus ==2">-->
<!--                <van-switch v-model="item.iIsDisplay" @change="iIsDisplay(item)" size="20px" style="margin-right:12px" />-->
<!--                &lt;!&ndash; <van-button type="danger" size="mini" style="margin-right:12px">删除</van-button> &ndash;&gt;-->
<!--              </div>-->
<!--            </div>-->
<!--            <template #right>-->
<!--              <van-button square text="删除" type="danger" style="height: 100%;" class="delete-button" />-->
<!--            </template>-->
<!--          </van-swipe-cell>-->
          <br><br>
        </template>
      </div>
      <pay-add ref="payType" @refPay="init"></pay-add>
    </div>
</template>

<script>
import PayAdd from "@/components/pay/addPay";
import SafeVerifyModal from "@/components/SafeVerifyModal/index";
import {delPayMode, getPayDictionary, getPayMode, updateDisplay} from "@/api/otc";
import {regPwd} from "@/utils/validate";
import {resetDrawalPassword} from "@/api/udc";
import {mapGetters,mapActions} from 'vuex';
import {Result,Button} from 'ant-design-vue'
import {Dialog} from 'vant'

export default {
  name: "step_three",
  props:{
    status:Number
  },
  components:{
    PayAdd,
    SafeVerifyModal,
    [Result.name]:Result,
    [Button.name]:Button
  },
  data(){
    return {
      payModes:[],
      openType: true,
      pLoading: false,
      reseting:false,
      drawal:{
        sDrawalPassword: '',
        sDpassword: ''
      },
      iVerifyBusi: 3,
    }
  },
  computed:{
    drawalStatus(){
      return this.iDrawalStatus()
    }
  },
  created(){
    this.init()
  },
  methods:{
    ...mapGetters(['uid','iIsChangeNameStatus','iDrawalStatus','switchEmailVerify','switchCellphoneVerify']),
    ...mapActions(['GetUserExten']),
    // position 为关闭时点击的位置
    // instance 为对应的 SwipeCell 实例
    beforeClose({ position, instance, name, id }) {
      console.log(name,'name');
      const _this = this
      switch (position) {
        case 'left':
        case 'cell':
        case 'outside':
          instance.close();
          break;
        case 'right':
          Dialog.confirm({
            message: '确定删除吗？',
          }).then(() => {
            _this.del(name)
            instance.close();
          });
          break;
      }
    },
    del(id){
      console.log(id);
      delPayMode({iId:id})
          .then(()=>{
            //this.$message.success('删除成功')
            getPayMode({iIdle:0})
                .then((res)=>{
                  this.payModes = res.data.vPayModeList
                  this.payModes.forEach(i=>{
                    i.iIsDisplay = i.iIsDisplay ? true:false
                  })
                })
          })
    },
    init(){
      this.$emit('ok')
      setTimeout(()=>{
        getPayMode({iIdle:0})
            .then((res)=>{
              // debugger
              this.payModes = res.data.vPayModeList
              this.payModes.forEach(i=>{
                i.iIsDisplay = i.iIsDisplay ? true:false
              })
            })
        getPayDictionary({iDictType:3})
            .then((res)=>{
              this.payDictionarys = res.data.vPayDictionary
              this.pLoading = true
            })
      },200)

    },
    addPay(){
      this.$refs.payType.init()
    },
    iIsDisplay(item){
      const params ={
        iId:item.iId,
        iIsDisplay:item.iIsDisplay ? 1:0
      }
      updateDisplay(params)
          .then(()=>{
            this.$message.success('设置成功');
          })
    },
    drawalPsw(){
      if (!this.drawal.sDrawalPassword) {
        this.$toast('请输入资金密码！');
        return
      }
      if(!regPwd(this.drawal.sDrawalPassword)){
        this.$toast('密码需包含数字和大小写字母中至少两种字符的8-10位字符');
        return
      }
      if (!this.drawal.sDpassword) {
        this.$toast('请再次输入新密码！');
        return
      }
      if (this.drawal.sDrawalPassword !== this.drawal.sDpassword) {
        this.$toast('两次密码不一致！');
        return
      }
      this.iVerifyBusi = this.iDrawalStatus() === 1 ?	19 :18;
      this.$refs.safeVerifyModal.visible = true
    },
    applyVerify(codes) {
      const params ={
        iSource:1,
        sDrawalPassword:this.drawal.sDrawalPassword,
      }
      if (this.switchEmailVerify()) params.sEmailVerifyCode = codes.sEmailVerifyCode
      if (this.switchCellphoneVerify()) params.sSMSVerifyCode = codes.sSMSVerifyCode
      // if (this.switchGoogleVerify()) params.sGoogleVerifyCode = codes.sGoogleVerifyCode
      // 设置资金密码
      resetDrawalPassword(params)
          .then(()=>{
            this.$message.success((this.iDrawalStatus() === 1 ? '重置' : '设置') +'成功！')
            this.$refs.safeVerifyModal.visible = false
            this.GetUserExten()
          })
    },
    resetBankCark(){
      delPayMode({iId:this.payModes[0].iId})
      .then(()=>{
        getPayMode({iIdle:0})
            .then((res)=>{
              this.payModes = res.data.vPayModeList
              this.reseting = true
            })
      })
    }
  },

}
</script>

<style lang="less" scoped>
.bzt{
  color: darkgray;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 12px;
}
.card-info{
  font-size: 0.14rem;
  color: #b8b8b8;
}
.p-flex{
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;
  .p-cont{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    min-width: 0;
  }
  .imgs{
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    img{
      width: 30px;
    }
  }
  .p-title{
    font-weight: 400;
    //font-size: .22rem;
    line-height: 1.4;
    color: rgba(0,0,0,0.9);
    color: var(--weui-FG-0);
    width: auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    word-wrap: break-word;
    -webkit-hyphens: auto;
    hyphens: auto;
    margin-bottom: 0;
  }
  .p-dec{
    color: rgba(0,0,0,0.3);
    color: var(--weui-FG-2);
    font-size: 14px;
    line-height: 1.4;
    padding-top: 4px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    word-wrap: break-word;
    -webkit-hyphens: auto;
    hyphens: auto;
  }

}
/deep/
.ant-result-title{
  font-size: 0.45rem;
}
</style>