<template>
  <div>
    <div style="padding:16px;padding-top: 96px">
      <van-cell-group title="修改昵称">
        <van-field
            class="nk-icon"
            v-model="message"
            center
            clearable
            type="text"
            :maxlength="10"
            :left-icon="require('../../assets/img/nk-icon.png')"
            placeholder="请输入昵称"
        >
        </van-field>
        <div class="bzt">
          请注意：昵称只能修改一次，保存后将无法修改！
        </div>
      </van-cell-group>
    </div>
    <div style="margin: 36px 16px; 0 16px">
      <van-button style="display: inline-block;" round block type="info" @click="changeNickname">
        确认修改，进入下一步
      </van-button>
    </div>
  </div>
</template>

<script>
import {chgUserExten} from "@/api/udc";
import {mapActions} from 'vuex'

export default {
name: "step_one",
  data(){
    return {
      message:''
    }
  },
  methods:{
  ...mapActions(['GetUserExten']),
    changeNickname(){
      if (this.message == '' || this.message == null || this.message == undefined) {
        this.$toast("请输入用户昵称");
      } else {
        this.setNickname()
      }
    },
    setNickname() {
      const params = {
        sNickName: this.message,
      }
      chgUserExten(params)
          .then(() => {
            this.$toast('修改成功！');
            this.show = false;
            this.GetUserExten()
          })
    },
  }
}
</script>

<style lang="less" scoped>
.bzt{
  color: red;
  padding-left: 20px;
  font-size: 12px;
}
</style>