<template>
    <div>
        <!-- <router-view/> -->
        <van-nav-bar
            :title="selKey==='2'?'高级认证':''"
            left-text="返回"
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            :fixed="fixed"
        />
        <div style="height: 1rem;"></div>
        <div v-if="liveCertificateStatus === 0 || liveCertificateStatus === 3">
            <van-cell value="视频认证" class="f_header"/>
            <van-notice-bar style="margin:16px; font-size:12px" color="#979A9F" background="#E9EEF5" left-icon="info-o" wrapable text="请手持身份证正面，朗读以下范本并进行视频录制（保证声音和影像清晰）
本人（姓名），身份证号（身份证号码），我的资金来源合法可靠，自愿交易比特币等数字资产，本人充分了解 数字货币及潜在风险，本人具有抗风险的能力并愿意承担一切风险。另外，我确定此账号所有操作均为本人操作，该账号所产生的一切法律责任，由我个人承担。。"/>
            <div style="margin:16px; margin-bottom:30px">
                <van-row type="flex" justify="space-around">
                    <van-col>
                        <van-row type="flex" class="card-box" justify="center">
                            <van-col>
                                <!-- <van-image v-if="videoUrl == ''||videoUrl == null" :src="require('../../assets/img/vedio.png')"/> -->
                                <div class="video-box">
                                    <p v-if="videoUrl == ''||videoUrl == null">视频显示区域</p>
                                    <video width="285" height="195" v-if="videoUrl !== ''" :src="videoFile"  alt="avatar" controls="controls" />
                                </div>
                            </van-col>
                        </van-row>
                        <van-row type="flex" class="card-box" justify="center">
                            <van-col>
                                <van-button round icon="play-circle-o" type="info" @click="show=true">{{videoUrl?'更换视频':'点击选择视频'}}</van-button>
                            </van-col>
                        </van-row>
                    </van-col>
                </van-row>
            </div>
            <div style="margin: 16px 26px;">
                <van-button round block type="info" @click="submitAudit">
                    提交审核
                </van-button>
            </div>
        </div>
        <div v-if="liveCertificateStatus === 1">
            <div class="cert-box p-mt">
                <van-row type="flex" justify="space-around" >
                    <van-col>
                        <van-row type="flex" class="card-box" justify="center">
                            <van-col>
                                <van-image width="40" :src="require('../../assets/img/ic_transaction_success.png')"/>
                            </van-col>
                        </van-row>
                        <br>
                        <van-row type="flex" justify="center">
                            <van-col>提交成功，请等待审核结果！</van-col>
                        </van-row>
                    </van-col>
                </van-row>
            </div>
            <div class="cert-foot-bnt">
                <van-button round block type="info" @click="success">
                    完成
                </van-button>
            </div>
        </div>

        <div v-if="liveCertificateStatus === 2">
            <div style="width: 100%;">
                <van-row type="flex" justify="space-around" >
                    <van-col>
                        <van-row type="flex" class="card-box" justify="center">
                            <van-col>
                                <van-icon name="passed" />
                            </van-col>
                        </van-row>
                        <br>
                        <div class="success-box">
                            恭喜您，已通过高级身份认证！
                        </div>
                    </van-col>
                </van-row>
            </div>
            <div class="cert-foot-bnt">
                <van-button round block type="info" @click="success">
                    完成
                </van-button>
            </div>
        </div>
        <loading-modal
            ref="loadingModal"
            :message='loadmsg'
        />
        <div style="height: 1rem;"></div>
        <van-action-sheet
            v-model="show"
            cancel-text="取消"
            close-on-click-action
            :actions="actions"
            @select="takePhoto"
        />
    </div>

</template>

<script>
    import Vue from 'vue'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {ossUrl,getSignedUrl} from '@/api/oss'
    import {getMerchantTokens} from '@/api/wallet'
    import {addLiveCertificate} from "@/api/udc";
    import LoadingModal from "@/components/LoadingModal/index";

    import bg_default from '../../assets/img/ic_asset_bg.png';
    import moment from 'dayjs'

    const file_url = process.env.VUE_APP_FILE_API_URL
    export default {
        name: 'authLive',
        components: {
            [Toast.name]: Toast,
            LoadingModal
        },
        data() {
            return {
                ossUrl,
                type: '',
                selKey: '1',
                sOssVideo: '',
                videoFile:null,
                ssk: Vue.ls.get('SSK'),
                fixed: true,
                showLoading: false,
                liveCertificateStatus:0,
                certificateStatus:0,
                loadmsg: '视频上传中...',
                actions: [
                    {name: '从相册选择'},
                    {name: '录制视频'},
                ],
                show: false
            }
        },
        mounted() {

        },
        computed:{
            videoUrl(){
                return  this.sOssVideo ==='' ? '' : process.env.VUE_APP_FILE_API_URL + this.sOssVideo
            },
            loadMsg(){
                return this.loadmsg;
            }
        },
        created() {
            this.type = this.$route.query.type;
            if (this.$route.query.type === '1') this.selKey = '1';
            if (this.$route.query.type === '2') this.selKey = '2';
            if (this.$route.query.type === '3') this.selKey = '3';
            this.liveCertificateStatus = this.iLiveCertificateStatus()
            this.certificateStatus = this.iCertificateStatus()

        },
        methods: {
            moment,
            ...mapGetters(['iLiveCertificateStatus','iCertificateStatus']),
            ...mapActions(['GetUserExten']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            recording() {
                let that = this;
                let permissions = cordova.plugins.permissions;
                permissions.checkPermission(
                    permissions.READ_EXTERNAL_STORAGE,
                    function(s) {
                        if (!s.hasPermission) {
                            permissions.requestPermission(
                                permissions.READ_EXTERNAL_STORAGE,
                                function(s) {
                                    if (s.hasPermission) {
                                        //申请成功
                                        vedio();
                                    } else {
                                        this.$toast('写入权限申请失败');
                                    }
                                },
                                function(error) {
                                    this.$toast.fail(error)
                                }
                            )
                        }else{
                            //
                            vedio();
                        }
                    },
                    null
                )

                let vedio = function () {
                    let options = {
                        limit: 1,
                        duration: 30 // 限制录制的视频时间长度，单位：/秒
                    }
                    //插件提供的拍视函数
                    navigator.device.capture.captureVideo(
                        function (mediaFiles) {
                            let file = mediaFiles[0];
                            // Toast(file.fullPath )
                            that.videoFile = file;
                            //alert(JSON.stringify(file))
                            window.resolveLocalFileSystemURL(file.fullPath , function (entry) {
                                let fileurl = entry.toURL()//如果此步转换失败检查app文件读取权限
                                let videoPath = fileurl;
                                that.$refs.loadingModal.visible = true
                                that.loadmsg = "视频压缩中...";
                                VideoEditor.transcodeVideo(
                                    function (result) {
                                        that.$refs.loadingModal.visible = false;
                                        let videoPath = result;
                                        // Toast(videoPath)
                                        that.fileUpload(videoPath,1);
                                    }, function (err) {
                                        that.$refs.loadingModal.visible = false;
                                    },
                                    {
                                        fileUri: videoPath, // the path to the video on the device
                                        outputFileName: 'ReportVideo', // the file name for the transcoded video
                                        outputFileType: VideoEditorOptions.OutputFileType.mp4, // android is always mp4
                                        saveToLibrary: true, // optional, defaults to true
                                        maintainAspectRatio: true,
                                        deleteInputFile: false, // optional (android only), defaults to false
                                        width: 640, // optional, see note below on width and height
                                        height: 640, // optional, see notes below on width and height
                                        videoBitrate: 1000000, // optional, bitrate in bits, defaults to 1 megabit (1000000)
                                        fps: 24, // optional (android only), defaults to 24
                                        audioChannels: 2, // optional (ios only), number of audio channels, defaults to 2
                                        progress: function (info) {

                                            console.log('transcodeVideo progress callback, info: ' + info)

                                        } // info will be a number from 0 to 100
                                    }
                                )

                            }, function (err) {
                                that.$refs.loadingModal.visible = false;
                            });
                        },
                        error => {
                            that.$refs.loadingModal.visible = false;
                        },
                        options
                    )
                }
            },
            takePhoto(action, index){
                let that = this;
                function setOptions(srcType) {
                    let options = {
                        // Some common settings are 20, 50, and 100
                        quality: 80,
                        destinationType: Camera.DestinationType.FILE_URI,
                        // In this app, dynamically set the picture source, Camera or photo gallery
                        sourceType: srcType,
                        //encodingType: Camera.EncodingType.JPEG,
                        mediaType: Camera.MediaType.VIDEO,
                        correctOrientation: true  //Corrects Android orientation quirks
                    }
                    return options;
                }
                if(index ===0){
                    let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
                    let options = setOptions(srcType);
                    navigator.camera.getPicture(function cameraSuccess(imageUri) {
                        that.fileUpload(imageUri,2)
                    }, function cameraError(error) {
                        console.debug("Unable to obtain picture: " + error, "app");
                    }, options);
                }else{
                    this.recording()
                }
            },
            goAuthSetup() {
                this.$router.replace({name: "authsetup"});
            },
            // 使用cordova FileUpload上传图片
            fileUpload (imageURI, ftype) {
                let that = this
                let FileUploadOptions = window.FileUploadOptions
                let FileTransfer = window.FileTransfer
                let options = new FileUploadOptions()
                options.fileKey = 'video'

                let path = imageURI.substr(imageURI.lastIndexOf('/') + 1);

                if(ftype === 2){
                    options.fileName = path
                }else{
                    options.fileName = that.videoFile.name
                    options.mimeType = that.videoFile.type
                }

                //alert(JSON.stringify(options.fileName));
                
                let headers = {'Authorization': ' ' + this.ssk}
                options.headers = headers;

                let ft = new FileTransfer()
                that.$refs.loadingModal.visible = true
                that.loadmsg = "视频上传中...";
                ft.upload(imageURI, encodeURI(this.ossUrl.PrivateUpload), function (data) {
                    let resString = data.response
                    console.log("=========0=============="+ resString)
                    that.$refs.loadingModal.visible = false
                    let resObject = JSON.parse(resString) // 字符串转对象
                    if (resObject.iRspCode === 0) {
                        that.sOssVideo =  resObject.data.vUrlPath[0];
                        // alert(that.sOssVideo)
                        getSignedUrl(that.sOssVideo).then(res=>{
                            // let video = document.createElement('video');
                            // video.setAttribute('crossOrigin', 'Anonymous')
                            // video.src = res
                            that.videoFile = res;
                            // alert(that.videoFile)
                            // alert(  video.src )
                            // console.log( "=========2=============="+ video.src)
                        })

                        // video.src = file_url + that.sOssVideo;
                        // video.onloadeddata = that.loadedmetadata;
                    } else {
                        //this.$toast(resObject.msg)
                        that.$toast.fail('上传出错稍后重试！')
                    }
                }, function (err) {
                    that.$refs.loadingModal.visible = false
                    //alert(JSON.stringify(err))
                    that.$toast.fail('上传出错稍后重试！')
                }, options)
            },
            submitAudit(){
                if(this.sOssVideo === ''){
                    this.$toast.fail('请上传认证视频！')
                    return
                }
                const params = {
                    sOssVideo:this.sOssVideo,
                }
                addLiveCertificate(params)
                    .then(()=>{
                        this.liveCertificateStatus = 1
                    })
                    .catch((e)=>{})
            },
            success() {
                console.log("=========0==============")
                this.$router.back(-1);
            }
        }
    }
</script>

<style lang="less" scoped>
    .f_header {
        font-weight: 500;
        font-size: 16px;
        margin-bottom: 5px;
        color: darkblue;
    }

    .card-box {
        margin-top: 20px;
        vertical-align: middle;
    }

    .user_header {
        position: relative;
        background-repeat: no-repeat;
        background-size: cover;
        height: 145px;
        box-sizing: border-box;
        text-align: left;
        color: #fff;
        padding-top: 30px;

        .text {
            margin-left: 20px;
        }

        .flex-type {
            margin-bottom: 20px;
        }
    }

    .fin_list {
        position: relative;
        text-align: left;
        background-color: white;
        margin-left: 20px;
        margin-bottom: 20px;
    }

    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }

    .user_avatar {
        margin-bottom: 10px;

        img {
            border: 0;
            border-radius: 50%;
        }
    }
    .success-box{
        font-size: .34rem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #212833;
        text-align: center;
        p{
            font-size: .3rem;
            margin-top: .2rem;
        }
    }
    /deep/.van-icon-passed{
        font-size: 1rem;
        color: @gpay-color3D7EFF;
        margin-top: .3rem;
    }
    .video-box{
        p{
            border: 1px rgb(233, 238, 245) dashed;
            border-radius: 5px;
            width: 285px;
            height: 195px;
            color: #c5c9d0;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    .cert-box{
        margin: 0 1px;
        margin-bottom:30px
        //background-color: #F7F6FB
    }
    .p-mt{
        padding-top: 30px;
    }
    .cert-foot-bnt{
        margin: 30px;
    }
</style>
