<script>
    import {Form, Button, Field,Cell, CellGroup,Checkbox, Col, Row,Tabbar, TabbarItem,Dialog } from 'vant';
    import {Badge} from 'ant-design-vue'
    import {mapActions,mapGetters} from 'vuex'
    import {chgUserExten} from "@/api/udc";
    
    export default {
        name: "Home",
        components: {
            [Form.name]: Form,
            [Button.name]: Button,
            [Field.name]: Field,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
            [Checkbox.name]: Checkbox,
            [Col.name]: Col,
            [Row.name]: Row,
            [Tabbar.name]: Tabbar,
            [TabbarItem.name]: TabbarItem,
            [Dialog.name]: Dialog ,
            [Badge.name]: Badge,
        }, data() {
            return {
              active: 1,
              show:false,
              message:'',
              countInterval:null,
            };
        },
        mounted(){
          // debugger
          // if(this.iIsChangeNameStatus() ===0){
          //     this.show = true
          //   }
          this.getCount()
          this.countInterval = setInterval(()=>{
            this.getCount()
          },2000)
        },
        beforeDestroy(){
            clearInterval(this.countInterval)
        },
        computed:{
          ...mapGetters(['getOrderCount']),
        },
        methods: {
            ...mapGetters(['iIsChangeNameStatus']),
            ...mapActions(['Login','GetUserExten','GetUserInfo','GetOrderCountFun']),
          closeDialog(){
            if (this.message == '' || this.message == null || this.message == undefined) {
              this.$toast("请输入用户昵称");
            } else {
              this.setNickname()

            }
          },
          setNickname() {
            const params = {
              sNickName: this.message,
            }
            chgUserExten(params)
                .then(() => {
                  this.$toast('修改成功！');
                  this.show = false;
                  this.GetUserExten()
                })
          },
          getCount(){
              const orderParams={
                iOrderType:0,
                iOrderProcess:1,
                sToken:'USDT',
                iPageNo:this.iPageNo - 1,
                iPageSize:this.pageSize,
                iRoleType:this.orderRoleType
              }
            this.GetOrderCountFun(orderParams)
          }
        },
    }
</script>

