<template>
    <section>
        <van-nav-bar
            title="申诉历史"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :fixed="true"
        />
        <div class="warp-loading" v-if="loading">
            <van-loading size="32px" vertical color="#0094ff">
                <span style="color:#0094ff;font-size:15px">数据加载中...</span>
            </van-loading>
        </div>
        <ul class="list-cont" style="padding-top: 50px;" v-else>
            <li v-for="itme in appealList" :key="itme.iId">
                <div class="time">
                    <span>{{ itme.iCreateTime | time }}</span>
                </div>
                <div class="content">
                    <div class="title">
                        <b v-if="itme.iQuestionType!=101&&itme.iQuestionType!=102&&itme.iQuestionType!=103&&itme.iQuestionType!=104&&itme.iQuestionType!=3&&itme.iQuestionType!=4&&itme.iQuestionType!=5&&itme.iQuestionType!=110">
                            <span v-if="detailData.iBuyUserId === itme.iUserId">申诉人<font style="color:#3D7EFF">{{detailData.sBuyNickName}}</font></span>
                            <span v-if="detailData.iSellUserId === itme.iUserId">被申诉人<font style="color:#3D7EFF">{{detailData.sSellNickName}}</font></span>{{itme.iType | iType}}
                        </b>
                        <b v-else>
                            <span v-if="itme.iQuestionType==3||itme.iQuestionType==4||itme.iQuestionType==5">
                                <font style="color:#3D7EFF">{{detailData.iBuyUserId === itme.iUserId?detailData.sBuyNickName:detailData.sSellNickName}}</font>
                            </span>
                            {{itme.iQuestionType | iType}}
                        </b>
                    </div>
                    <div class="conts">
                        <p v-if="itme.iQuestionType==103">
                            <ul class="shs-ul">
                                <li>客服处理结果-客服放行订单</li>
                            </ul>
                        </p>
                        <p v-if="itme.iQuestionType==104">
                            <ul class="shs-ul">
                                <li>客服处理结果-客服取消订单</li>
                            </ul>
                        </p>
                        <p class="" v-if="itme.iQuestionType==110">
                            <ul class="shs-ul">
                                <li><p>客服处理结果-客服关闭申诉</p></li>
                            </ul>
                        </p>
                        <p v-if="itme.iQuestionType!=101&&itme.iQuestionType!=102&&itme.iQuestionType!=103&&itme.iQuestionType!=104&&itme.iQuestionType!=3&&itme.iQuestionType!=4&&itme.iQuestionType!=5&&itme.iQuestionType!=110" style="padding:8px">
                            <ul class="shs-ul">
                                <li>原因：<p>{{itme.iQuestionDetail | qType}}</p></li>
                                <li v-if="itme.sDescription">描述：<p>{{itme.sDescription}}</p></li>
                                <li v-if="itme.sProof[0]">凭证：
                                    <p style="display:flex">
                                        <van-image style="width:30%;height:22vw;margin:0 10px;" v-for="(url,index) in itme.sProof" :key="index" :src="url" @click="preview(itme.sProof,index)">
                                            <template v-slot:loading>
                                                <van-loading type="spinner" size="20" />
                                            </template>
                                        </van-image>
                                    </p>
                                </li>
                            </ul>
                        </p>
                        <template v-else>
                            <p v-if="itme.sDescription">
                                <ul class="shs-ul" v-if="itme.sDescription">
                                    <li><p style="padding-bottom:20px">{{itme.sDescription}}</p></li>
                                </ul>
                            </p>
                        </template>
                    </div>
                </div>
            </li>
        </ul>
        <van-image-preview v-model="show" :start-position="index" :images="images" @change="onChange">
            <!-- <template v-slot:index>第{{ index }}页</template> -->
        </van-image-preview>
    </section>
</template>
<script>
import {addArbitration,getArbitrationList,getOrderDetail,getShareOrderDetail} from "@/api/otc";
import moment from 'dayjs'
import {problemArr} from '@/utils/problem'
import {getSignedUrlArr} from "@/api/oss";

export default {
    data() {
        return {
            activeNames: ['1'],
            appealList:[],
            current:1,
            pageSize:10,
            total:0,
            show: false,
            index: 0,
            images:[],
            detailData:{},
            loading:true
        };
    },
    created(){
        getShareOrderDetail({sOrderId:this.$route.query.orderId})
        .then(res=>{
            this.detailData = res.data.stOrderData
        })
        this.init()
    },
    methods:{
        init(){
            const params = {
                sOrderId: this.$route.query.orderId||'',//'a944b3ef660f467899eb67fa9176cb91',//'7d5a494dfcc342c1b4a5c835af96a017',//this.sOrderId,
                iPageNo:this.current - 1,
                iPageSize:this.pageSize,
            }
            getArbitrationList(params)
            .then(res=>{
                let data = res.data.vList,arr=[];
                for(let item of data){
                    if(item.sProof){
                        let sProof = item.sProof.split(','),iarr=[];
                        for(let img of sProof){
                            // getSignedUrl(img).then((res) =>{
                            //     iarr.push(res)
                            // })
                            iarr.push(getSignedUrlArr(img))
                        }
                        console.log(iarr)
                        Promise.all(iarr).then((data)=>{
                            // 执行成功
                            console.log('请求成功:');
                            item.sProof = data
                            this.loading = false
                        }, (error)=>{
                            console.log('请求失败：')
                            console.log(error);
                        })
                    }else{
                        item.sProof = [item.sProof]
                        this.loading = false
                    }
                    arr.push(item)
                }
                this.appealList = arr
                this.activeNames = [""+data[0].iId+""]
                //format('YYYY-MM-DD HH:mm:ss')
                //this.visible = true
                //this.loading = false
            })
        },
        onClickLeft() {
            if (this.$route.query.goindex === 'true') {
                this.$router.push('/');
            } else {
                this.$router.back(-1);
            }
        },
        onChange(index) {
            this.index = index;
        },
        preview(arr,index){
            this.show = true
            this.images = arr
            this.index = index;
        }
    },
    filters:{
      time(v){
          return moment(v).format('YYYY-MM-DD HH:mm:ss')
      },
      qType(v){
          let data = problemArr.find((i) => i.iId == v)
            //客服接受 101 客服留言 102 客服放币 103 客服取消订单104
            if(!data){
                switch(v) {
                    case 101:
                        return "平台客服已介入，请等待处理"
                        break;
                    case 102:
                        return "客服留言"
                        break;
                    case 103:
                        return "协商结果"//"客服放币"
                        break;
                    case 104:
                        return "协商结果"//"客服取消订单"
                        break;
                    case 4001:
                      return '未收到转账'
                    case 4002:
                      return '支付金额不对'
                    case 4100:
                      return '其他'
                    case 5001:
                      return "支付金额不对"
                    case 5100:
                      return  '其他'

                    default:
                        '-'
                }
            }else{
                return data.dec
            }
          //return data?data.dec:'111'
      },
    //   iQuestion(v){//iQuestionType iQuestionDetail
    //     for(let p of problemArr){
    //         for(let ch of p.children){
    //             if(ch.iId == v){
    //                 console.log('09999999')
    //                 return ch.dec
    //             }
    //         }
    //     }
    //   },
      iType(v){
          switch(v) {
            case 1:
                return "提交申诉"
                break;
            case 2:
                return "补充描述"
                break;
            case 3:
                return "处理结果-取消订单"
                break;
            case 4:
                return "处理结果-放币"//"对方处理放币"
                break;
            case 5:
                return "处理结果-放币超时"//"对方处理超时"
                break;
            case 11:
                return "客服留言"
                break;
            case 103:
                return "协商结果"
                break;
            case 104:
                return "协商结果"
                break;
            case 101:
                return "平台客服已介入，请等待处理"
                break;
            case 102:
                return "客服留言"
                break;
            case 110:
                return "协商结果"//"客服关闭订单"
                break;
            // case 103:
            //     return "客服放币"
            //     break;
            // case 104:
            //     return "客服取消订单"
            //     break;
            default:
                '-'
          }
      },
  }
}
</script>
<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    /deep/.nk-icon .van-icon{
        font-size: .42rem;
        margin-top: -6px;
        margin-right: .06rem;
        .van-icon__image{
            height: auto;
        }
    }
    .list-cont{
        padding:0 6px;
        background: #f5f5f5;
        li{
            padding: 0 12px;
            .time{
                margin: 8px 0;
            }
            .title{
                padding-left: 12px;
                padding-top: 10px;
                padding-bottom: 10px;
            }
            .conts > p{
                border-top: 1px solid #f0f2f5;
                padding-top: 12px;
                margin-bottom: 0;
            }
            .content{
                background: #fff;
                border-radius: 5px;
            }
        }
        li:last-child{
            padding-bottom: 1rem;
        }
        .shs-ul{
            margin: 0;
            li{
                padding-bottom: 10px;
                color: #999;
                padding-left: 5px;
            }
            p{
                color: #333;
                padding-top: 5px;
                margin: 0;
                word-wrap:break-word;
                padding-left: 10px;
                font-size: 13px;
                img{
                    width: 160px;
                    height: 160px;
                }
            }
        }
    }
</style>