<template>
    <div>
        <van-nav-bar
            title="关于我们"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :border="false"
            :fixed="fixed"
        />
        <!-- <router-view/> -->
        <div class="about_us">
            <div class="logo">
                <van-image
                    round
                    width="2rem"
                    height="2rem"
                    :src="require('../../assets/img/applogo192.png')"
                />
                <h5>一荷包 OTC</h5>
            </div>
            <div class="banben">
                <h4>V{{appversion}}</h4>
            </div>
        </div>
        <van-cell-group>
            <van-cell class="verc" title="版本更新" @click="getVersion" is-link>
                <template v-if="buildid() > version()">
                    <van-badge :dot="buildid() > version()">
                        点击更新到{{buildid()}}
                    </van-badge>
                </template>
                <template v-else>
                    已是最新版
                </template>
            </van-cell>
        </van-cell-group>
        <!-- <van-dialog v-model="show" title="" show-cancel-button confirmButtonText="立即更新" cancelButtonText="暂不更新" confirmButtonColor="#3D7EFF" :before-close="beforeClose">
            <div class="cont">
                <img src="../../assets/logo.png" />
                <h2>发现新版本V{{newVersion}}</h2>
                <dl>
                    <dd>{{sUpdateDescription}}</dd>
                </dl>
            </div>
        </van-dialog> -->
        <version-comp ref="version"></version-comp>
    </div>
</template>

<script>
    import {getAppVersion} from '@/api/udc'
    import {mapGetters} from 'vuex'
    import VersionComp from "../../views/version"

    export default {
        name: 'user-about-us',
        components: {
            // [Toast.name]: Toast,
            VersionComp
        },
        data() {
            return {
                fixed:true,
                appversion: '',
                //newVersion: '',
                lastappversion: '',
                show: false,
                sUpdateDescription: [],
                bntText: '',
                dowUrl: ''
            };
        },
        created() {
            this.appversion = this.version();
            //this.newVersion = this.version();
        },
        methods: {
            ...mapGetters(['isLogin', 'version', 'buildid']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            getVersion() {
                let that = this;
                const param = {
                    sAppOS: 'android'
                }
                getAppVersion(param).then((res) => {
                    function Version(curV, reqV) {
                        let arr1 = curV.toString().split('.');
                        let arr2 = reqV.toString().split('.');
                        //将两个版本号拆成数字
                        let minL = Math.min(arr1.length, arr2.length);
                        let pos = 0; //当前比较位
                        let diff = 0; //当前为位比较是否相等
                        let flag = false;
                        //逐个比较如果当前位相等则继续比较下一位
                        while (pos < minL) {
                            diff = parseInt(arr1[pos]) - parseInt(arr2[pos]);
                            if (diff == 0) {
                                pos++;
                                continue;
                            } else if (diff > 0) {
                                flag = true;
                                break;
                            } else {
                                flag = false;
                                break;
                            }
                        }
                        return flag;
                    }

                    this.lastappversion = res.data.sNewVersion;
                    this.appversion = that.version();
                    let test_v = Version(this.lastappversion, this.appversion);
                    //alert('旧版号:'+this.appversion)
                    //alert('新版号:'+this.lastappversion)
                    //alert(JSON.stringify(res))
                    if (test_v) {
                        console.log(res.data)
                        let sApkUrl = res.data.sApkUrl;//下载地址
                        //this.dowUrl = encodeURI(sApkUrl); // 可以从服务端获取更新APP的路径
                        that.sUpdateDescription = res.data.sUpdateDescription//更新内容描述
                        //去做弹窗和下载
                        
                        //this.newVersion = res.data.sNewVersion;
                        this.$refs.version.newVersion = res.data.sNewVersion;
                        this.$refs.version.show = true
                        this.$refs.version.cancel = true
                        this.$refs.version.dowUrl = encodeURI(sApkUrl);
                        this.$refs.version.sUpdateDescription = res.data.sUpdateDescription
                        //that.show = true
                    }else{
                        this.$toast("已是最新版本");
                    }
                });
            },
        }
    };
</script>


<style lang="less" scoped>
    .about_us{
        padding-top: 50px;
        background: @gpay-bg3D7EFF;
        .logo {
            padding:5% 0;
            text-align: center;
            img{
                width:192px;
            }
            h5{
                font-size:12px;
                color: #fff;
                padding-top: .1rem;
            }
        }
        .banben{
            text-align: center;
            h4{
                font-size:14px;
                color: #fff;
                padding-bottom: .2rem;
            }
            p{
                font-size:10px;
                opacity: 0.5;
            }
        }
    }
    .user_header {
        position: relative;
        background-repeat: no-repeat;
        background-size: cover;
        height: 130px;
        box-sizing: border-box;
        text-align: center;
        color: #fff;
        padding-top: 30px;
    }

    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }

    .user_avatar {
        margin-bottom: 10px;

        img {
            border: 0;
            border-radius: 50%;
        }
    }
    .us-logo{
        background: @gpay-bg3D7EFF;
    }
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    /deep/.van-badge--dot{
        left: 0;
        top: 50%;
    }
    /deep/.van-badge__wrapper{
        padding-left: 16px;
    }
    .cont{
        text-align: center;
        img{
            width: 80px;
            height: 80px;
            margin-top: 20px;
        }
        h2{
            font-size: 15px;
            padding-top: 15px;
        }
        dl{
            font-size: 13px;
            padding: 0 25px;
            text-align: left;
            margin-top: 20px;
        }
    }
    .pro{
        text-align: left;
        padding: 10px 30px;
        font-size: 12px;
        padding-bottom: 20px;
    }
    .verc .van-cell__value{
        overflow: initial;
    }
</style>

