<template>
    <div class="app-gapy-user">
        <router-view/>
        <div class="phoneLogin" v-if="showPage">
            <van-row class="flex-wrapper" type="flex" justify="end">
                <van-col class="flex-right" @click="goLogin">
                    登录
                </van-col>
                <!-- <van-col class="flex-left" v-if="active>=1" @click="active= active - 1">
                    <van-icon name="arrow-left" />
                </van-col> -->
            </van-row>
            <div class="flex-logo">
                <img src="../../assets/img/applogo192.png" alt="">
            </div>
            <div class="login-form-box">
                <div class="mlr">
                    <ul>
                        <li v-if="active==0">
                            <div class="label-text">手机号码将成为你的账号</div>
                            <div class="input-cont cprv" :class="{'focus':isFocus.phoneNum,'phone_empty':errorFlag.phone_empty,'phone_error':errorFlag.phone_err}">
                                <button class="country-code" @click="chooseArea()">+{{countryTel}}</button>
                                <input autocomplete="off" style="width: 68%;margin-left: 58px;" @input="phone" v-model="input_info.phone" ref="phoneNum" maxlength="13" autofocus class="van-field__control" type="text"/>
                                <!-- <input autocomplete="off" style="width: 68%;margin-left: 58px;" @focus="focus('phoneNum')" v-model="input_info.phone" @blur="blur('phoneNum')" ref="phoneNum" @keyup="inputPhone" maxlength="13" autofocus class="van-field__control" type="text"/> -->
                            </div>
                        </li>
                        <li v-if="active==1">
                            <div class="label-text">输入您收到的验证码</div>
                            <div class="input-cont cprv" :class="{'focus':isFocus.code,'code_empty':errorFlag.code_empty,'code_error':errorFlag.code_err}">
                                <input autocomplete="off" class="van-field__control" style="width: 60%;" ref="code" @input="code" placeholder="请输入验证码" v-model="input_info.code" :maxlength="mConfigs.code_length" type="text"/>
                                <!-- <input autocomplete="off" class="van-field__control" style="width: 60%;" @focus="focus('code')" @blur="blur('code')" @keyup="activeLogin()" ref="code" placeholder="请输入验证码" v-model="input_info.code" :maxlength="mConfigs.code_length" type="text"/> -->
                                <button class="phone-code" :class="{'vanh':getCodeDisabled}" @click="getVerifyCode()" ref="getCode" :disabled="getCodeDisabled">{{getCodeBtnText}}</button>
                            </div>
                        </li>
                        <li v-if="active==2">
                            <div class="label-text">输入您的密码</div>
                            <div class="input-cont" :class="{'focus':isFocus.password,'password_error':errorFlag.password}">
                                <input autocomplete="off" type="password" class="van-field__control" @input="pwd" ref="password" placeholder="请输入密码" v-model="input_info.password"/>
                            </div>
                            <div class="label-text">输入您的推荐码</div>
                            <div class="input-cont">
                                <input autocomplete="off" type="text" class="van-field__control" ref="referId" placeholder="请输入推荐码" v-model="input_info.referId"/>
                            </div>
                        </li>
                        <li v-if="active>=3">
                            <div class="sus-text">
                                恭喜注册成功！ 
                                <p>立即开启你的数字OTC之旅吧</p>
                            </div>
                        </li>
                        <!-- <li class="err-msg">
                            
                        </li> -->
                        <li class="next-conts" v-if="active<3">
                            点击“下一步/去注册”，即表示你同意<b @click="to_protocol">《用户注册协议》</b>
                        </li>
                    </ul>
                    <van-button round block type="info" class="next-bnt" :disabled="errorFlag.nextBntFlag" native-type="submit" v-if="active<3" @click="clicknext">
                        {{active>=2?'去注册':'下一步'}}
                    </van-button>
                    <van-button round block type="info" class="next-bnt" native-type="submit" v-if="active>=3" @click="goLogin">
                        好的
                    </van-button>
                    <div class="yu-box" v-if="active<3">
                        <a @click="goLogin">登录已有账号</a>
                    </div>
                </div>
            </div>
        </div>
        <div class="select_code" v-if="!showPage">
            <bm_select_code v-on:choose="selectCountry($event)"></bm_select_code>
        </div>
    </div>
</template>

<script>
    import bm_select_code from '@/components/bm_select_code/bm_select_code'
    // import {Step, Steps, NavBar, Dialog, Image as VanImage} from 'vant';
    import {mapActions,mapGetters} from 'vuex'
    // import Toast from 'muse-ui-toast';
    import {getSMSVerifyCode,checkRegistered,userRegister} from '@/api/auth'
    import {udcUrl} from '@/api/udc'
    import {regPwd,isMobile} from "@/utils/validate"
    import axios from 'axios'

    export default {
        name: "Login",
        components: {
            // [VanImage.name]: VanImage,
            // [NavBar.name]: NavBar,
            // [Step.name]: Step,
            // [Steps.name]: Steps,
            [bm_select_code.name]: bm_select_code,
            // [Toast.name]: Toast,

        },
        props:['baseConfig','iconsBase64'],
        computed:{
            //合并父组件的传入值与默认值
            mConfigs(){
                return Object.assign({
                    accountLogin:true,
                    changedPhone: true,
                    protocol: true,
                    otherLoginWays: true,
                    code_length:6,
                    login_btn_value:'登录'
                },this.baseConfig)
            },
            icons(){
                return Object.assign({
                    username_icon:"data:image/png;base64,",
                    username_blur:"data:image/png;base64,",
                    username_active:"data:image/png;base64,",
                    password_icon:"data:image/png;base64,",
                    password_active:"data:image/png;base64,",
                    password_hidden:"data:image/png;base64,",
                    password_visiable:"data:image/png;base64,",
                },this.iconsBase64)
            }
        },
        data() {
            return {
                showPage: true,
                showCode: false,
                active: 0,
                countryTel: "86",
                //用户输入信息
                input_info: {
                    phone: "",
                    code: "",
                    password:"",
                    referId:''
                },
                //倒计时
                countDownTime: 60,
                timeOut: true,
                timer: null,
                //聚焦（切换样式）
                isFocus: {
                    phoneNum: false,
                    code: false,
                    username:false,
                    password:false
                },
                //错误信息
                errorFlag: {
                    username:false,
                    password:false,
                    phone_empty: false,
                    phone_err: false,
                    code_empty: false,
                    code_err: false,
                    nextBntFlag: true
                },
                //登录按钮禁用
                isLoginForbidden: true,
                // 获取验证码按钮禁用
                getCodeDisabled: false,
                // 获取验证码按钮文字
                getCodeBtnText: "获取验证码",
                //解决安卓输入框将fixed布局顶上的问题
                hideFooter: true,
                docmHeight: document.documentElement.clientHeight || document.body.clientHeight,
                showHeight: document.documentElement.clientHeight || document.body.clientHeight,
            };
        },
        methods: {
            ...mapActions(['Login', 'GetUserExten', 'GetUserInfo']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            clicknext() {
                //this.activeLogin();
                if(this.active==0){
                    //this.inputPhone();
                    if(this.errorFlag.phone_empty || this.errorFlag.phone_err){
                        return
                    }
                    //this.input_info.phone = this.input_info.phone.replace(/\s*/g,"");
                    this.active++;
                } else if(this.active == 1){
                    if(!this.getCodeDisabled){
                        this.$toast('请先获取验证码');
                        return
                    }
                    this.active++;
                    this.errorFlag.nextBntFlag = true
                }else if(this.active==2){
                    // let password = this.input_info.password;
                    // if( password === "" || password === null || password === undefined){
                    //     this.errorFlag.password = true;
                    //     return
                    // }
                    // if(!regPwd(password)){
                    //     this.errorFlag.password = true;
                    //     return
                    // }
                    this.register();
                }
            },
            phone(val){
                let phone_num = this.input_info.phone;
                if(!isMobile(this.input_info.phone) && this.countryTel=='86'){
                    //this.errermsg = '请正确输入正确格式手机号'
                    this.errorFlag.phone_err = true
                    console.log('09999')
                }else if(this.input_info.phone.length < 5){
                    //this.errermsg = '请正确输入正确格式手机号'
                    this.errorFlag.phone_err = true
                } else{
                    this.errorFlag.phone_err = false
                    this.errorFlag.nextBntFlag = false
                }
            },
            code(){
                if(!this.input_info.code){
                    this.errorFlag.nextBntFlag = true
                    this.errorFlag.code_empty = true
                }else{
                    this.errorFlag.code_empty = false
                    this.errorFlag.nextBntFlag = false
                }
            },
            pwd(){
              // debugger
                if(!regPwd(this.input_info.password)){
                    this.errorFlag.password = true;
                  this.errorFlag.nextBntFlag = true
                    return
                }else{
                  this.errorFlag.password = false;
                    this.errorFlag.nextBntFlag = false
                }
            },
            inputPhone() {
                //输入手机号时，keyup事件
                let value = this.input_info.phone.replace(/\D/g, '').substr(0, 11); // 不允许输入非数字字符，超过11位数字截取前11位
                let len = value.length;
                //判空
                let phone_num = this.input_info.phone;
                this.errorFlag.phone_empty = phone_num === "" || phone_num === null || phone_num === undefined;

                console.log(111,this.countryTel,this.errorFlag.phone_empty)
                //判断手机号格式是否正确
                this.errorFlag.phone_err = this.countryTel=='86'?!(/^1[3456789]\d\s\d{4}\s\d{4}$/.test(phone_num)) && !this.errorFlag.phone_empty:false;
                if (len > 3 && len < 8) {
                    value = value.replace(/^(\d{3})/g, '$1 ')
                } else if (len >= 8) {
                    value = value.replace(/^(\d{3})(\d{4})/g, '$1 $2 ')
                    if (len === 11) {
                        if (!(this.errorFlag.phone_empty || this.errorFlag.phone_err) && this.timeOut) {
                            //激活获取验证码按钮
                            this.getCodeDisabled = false;
                        } else {
                            this.getCodeDisabled = true;
                        }
                    } else {
                        this.getCodeDisabled = true;
                    }
                }
                this.input_info.phone = value;
            },
            inputPwd(){
                if(!regPwd(this.input_info.password)){
                    this.errorFlag.password = true;
                    return
                }else{
                    this.errorFlag.password = false;
                }
            },
            chooseArea() {
                this.$refs.phoneNum.blur();
                this.showPage = false;
            },
            focus(type) {
                this.isLoginForbidden = true;
                if (type === "phoneNum") {
                    this.isFocus.phoneNum = true;
                    this.errorFlag.phone_empty = false;
                }
                if (type === "code") {
                    this.isFocus.code = true;
                    this.errorFlag.code_empty = false;
                }
                if (type === "password"){
                    this.isFocus.password = true;
                    this.icons.password_icon = this.icons.password_active;
                    this.errorFlag.password = false;
                }
            },
            selectCountry(event) {
                // let country_tel = event.tel;
                this.countryTel = event.iCode;
                this.showPage = true;
            },
            blur(type) {
                if (type === "phoneNum") {
                    this.isFocus.phoneNum = false;
                }
                if (type === "code") {
                    this.isFocus.code = false;
                }
                if (type === "password"){
                    this.isFocus.password = false;
                    this.icons.password_icon = this.icons.password_hidden;
                    this.$refs.password.setAttribute("type","password");
                    
                    //判空
                    let password_value = this.$refs.password.value;
                    this.errorFlag.password = password_value === "" || password_value === null || password_value === undefined;
                }
            },
            /**************************************************************获取验证码**************************************************************/
            getVerifyCode(){
                let that = this;
                let input_phone = this.input_info.phone.replace(/\s*/g,"");      //去除空格
                this.timeOut = false;
                //倒计时
                if (!this.errorFlag.phone_err && !this.errorFlag.phone_empty){
                    //Todo 获取验证码之前验证手机号
                    const checkParams = {
                        iRegisterType:1,
                        sSMSAreaCode:this.countryTel,//国家区号
                        sCellphone:input_phone//手机号
                    }
                    checkRegistered(checkParams).then(()=>{
                        const params={
                            iSource:2,
                            iSendMethod:2,
                            iVerifyBusi:1,
                            sSMSAreaCode:this.countryTel,
                            sCellphone:input_phone
                        }
                        getSMSVerifyCode(params).then(()=>{
                            that.$toast('验证码已发送');
                            that.timer = setInterval(()=>{
                                that.countDownTime--;
                                // that.getCodeBtnText = this.countDownTime + "s后重新获取";
                                that.getCodeBtnText = this.countDownTime + "秒后重新获取";
                                that.getCodeDisabled = true;
                                if(that.countDownTime <= 0){
                                    clearInterval(this.timer);
                                    that.countDownTime = 60;
                                    that.timeOut = true;
                                    that.getCodeDisabled = false;
                                    // that.$refs.getCode.removeAttribute("disabled");
                                    // that.$refs.getCode.value = "获取验证码";
                                    that.getCodeBtnText = "获取验证码";
                                }
                            },1000)
                        })
                    }).catch((err) =>{
                        if(err.code === -804){
                            that.active = 0
                            that.getCodeDisabled = false;
                        }
                    })
                } else {
                    that.errorFlag.phone_empty = true;
                }
            },
            activeLogin(){
                //判空
                let code = this.input_info.code;
                //this.errorFlag.code_empty = code === "" || code === null || code === undefined;
                if (code && code.length === this.mConfigs.code_length) {
                    //激活登录按钮
                    this.isLoginForbidden = this.errorFlag.phone_empty || this.errorFlag.phone_err || this.errorFlag.code_empty;
                }
            },
            to_protocol(){
                this.$router.push('/agreement')
                return
                //用户协议
                this.$dialog.alert({
                    message: '用户协议...',
                }).then(() => {
                // on close
                });
            },
            goLogin(){
                this.$router.replace({path: '/'});
                // this.$router.push('/');
                //location.reload();
            },
            register(){
                const params = {
                    iSource:2,//app
                    iRegisterType:1,
                    sPassword:this.input_info.password,
                    sSMSAreaCode:this.countryTel,
                    sCellphone:this.input_info.phone,
                    sSMSVerifyCode:this.input_info.code,
                    sInvitateCode:this.input_info.referId,
                }
                userRegister(params)
                    .then((res)=>{
                        axios({
                            url: udcUrl.RegisterUserExten,
                            method:'post' ,
                            data: {iUserType:3},
                            headers:{
                                'Content-Type':'application/json;charset=UTF-8',
                                Authorization:res.data.sSessionKey
                            }
                        }).then((res)=>{
                            if(res.data.iRspCode === 0){
                                this.$message.success('恭喜您，注册成功！')
                                //this.$router.push('/')
                            }
                            //this.GetUserExten(0)
                            this.active = 3;
                        })
                    }).catch((err) =>{
                        if(err.code === -804 || err.code === -823){
                            this.active = 0
                            this.getCodeDisabled = false
                            clearInterval(this.timer);
                            this.getCodeBtnText = "获取验证码";
                          this.input_info.referId=''
                          this.input_info.password=''

                        }else if(err.code === -926 || err.code === -920){
                            this.active = 1
                            this.getCodeDisabled = false
                            clearInterval(this.timer);
                            this.getCodeBtnText = "获取验证码";
                          this.input_info.referId=''
                          this.input_info.password=''
                        }
                    })
            }
        },
        watch:{
            active(nVal,oVal){
                if(nVal == 0){
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    @import url('../../assets/style/userStyle.less');
    .flex-wrapper {
        width: 100%;
        /*height: 56px;*/
        margin-top: 8px;
    }

    .flex-wrapper:first-child {
        margin-top: 0;
    }

    .flex-demo:first-child {
        margin-left: 0;
    }
    .yu-box{
        margin-top: 12px;
        text-align: center;
        a{
            font-size: 14px;
            font-family: Adobe Heiti Std;
            font-weight: normal;
            color: #3D7EFF;
            line-height: 18px;
        }
    }
    .input-cont{
        background: #FFFFFF;
        border: 1px solid #C8CBCE;
        border-radius: 45px;
        padding: 12px 16px;
    }
    .next-bnt{
        margin-top: 30px;
    }
    .error {
        transform: scaleY(1);
        font-size: 12px;
        color: red;
        text-align: right;
    }
    @mainColor: #007cdc;    
    .focus::after {
        //background: @mainColor;
    }
    .password_error::after{
        content: '密码需包含数字和大小写字母中至少两种字符的8-10位字符';
        transform: scaleY(1);
        font-size: 12px;
        color: red;
        text-align: right;
    }
    .phone_empty::after {
        content: '手机号不能为空';
        .error();
        margin-left: 52px;
    }

    .phone_error::after {
        content: '请填入正确的手机号';
        .error();
        margin-left: 52px;
    }

    .code_empty::after {
        content: '验证码不能为空';
        .error();
    }

    .code_error::after {
        content: '验证码错误';
        .error();
    }
    .cprv{
        position: relative;
    }
    .phone-code{
        position: absolute;
        top: 0;
        right: 10px;
        height: 100%;
        background: none;
        border: 0;
    }
    .country-code{
        position: absolute;
        top: 0;
        left: 10px;
        height: 100%;
        background: none;
        border: 0;
        font-family: Adobe Heiti Std;
        font-weight: normal;
        color: #212833;
    }
    .vanh{
        color: #C8CBCE;
    }
    .sus-text{
        font-size: 26px;
        font-family: Adobe Heiti Std;
        font-weight: normal;
        color: #3D7EFF;
        text-align: center;
        margin-top: 50px;
        p{
            font-size: 16px;
            color: #212833;
            margin: 0;
            padding: 0;
            margin-top: 12px;
            margin-bottom: 30px;
        }
    }
    .next-conts{
        font-family: Adobe Heiti Std;
        font-weight: normal;
        color: #333;
        margin-top: 15px;
        font-size: 12px;
        b{
            color: #3D7EFF;
        }
    }
</style>
