        <safe-verify-form
          :iVerifyBusi="iVerifyBusi"
          :needCellphone="needC"
          :needEmail="needE"
          :needGoogle="needG"
          :phoneVal="phone"
          :emailVal="email"
          :areaVal="area"
          :needType="needTypes"
          ref="childs"
          @ok="apply"
        />
    </van-dialog>

