<script>
import SafeVerifyForm from "@/components/SafeVerifyModal/SafeVerifyForm";

export default {
name: "SafeVerifyModal",
  components:{
    SafeVerifyForm,
  },
  props:{
    iVerifyBusi:Number,
    needCellphone: {
      Number,                     //是否需要手机验证，0为强制不需要，1为强制需要，2为跟随验证配置
      default:2
    },
    needEmail: {
      Number,                     //是否需要手机验证，0为强制不需要，1为强制需要，2为跟随验证配置
      default:2
    },
    needGoogle: {
      Number,                     //是否需要手机验证，0为强制不需要，1为强制需要，2为跟随验证配置
      default:2
    },
    phoneVal: {
      String,                     //手机号
      default:''
    },
    emailVal: {
      String,                     //邮箱
      default:''
    },
    areaVal: {
      String,                     //区号
      default:'86'
    },
    needType: {
      Boolean,                     //是否获取用户信息
      default:true
    },
  },
  computed:{
    needC(){
      return this.needCellphone
    },
    needE(){
      return this.needEmail
    },
    needG(){
      return this.needGoogle
    },
    phone(){
      return this.phoneVal
    },
    email(){
      return this.emailVal
    },
    area(){
      return this.areaVal
    },
    needTypes(){
      return this.needType
    }
  },

  data(){
    return{
      visible:false,
      loading:false,
      showCancelButton:true,
      showConfirmButton:false,
      closeOnClickOverlay:false
    }
  },
  methods:{
    apply(codes){
      this.$emit('ok',codes,true)
    },
    childBefore(action, done){
      if (action === 'cancel') {
        done();
        this.$refs.childs.childClear()
      }
    }
  },
  watch:{
    visible(nVal){
      console.log(nVal)
      if(nVal){
        this.$nextTick(() =>{
          this.$refs.childs.childClear()
        });
      }
    }
  }
}
</script>

