<template>
    <div class="pay-list">
        <van-nav-bar
            title="支付管理"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :fixed="true"
        />
        
        <div class="user-pay-cont" v-if="pLoading">
            <van-empty v-if="payModes.length === 0" description="您暂未设置收款方式" :image="require('../../assets/img/empty-image-default.png')" />
               <van-swipe-cell class="plist border-bm" :before-close="beforeClose" v-for="item in payModes" :key="item.iId" :name="item.iId">
                    <div class="p-flex">
                        <div class="imgs">
                            <img v-if="item.iPayDictId==13" class="pay-icon" src="../../assets/img/zfb.png">
                            <img v-if="item.iPayDictId==14" class="pay-icon" src="../../assets/img/wx.png">
                            <img v-if="item.iPayDictId==15" class="pay-icon" src="../../assets/img/yhk.png">
                        </div>
                        <div class="p-cont">
                            <p class="p-title">{{item.sName}} </p>
                            <div class="card-info">
                              <div>{{item.sAccount | bankNum}}</div>
                              <div>{{item.sBankName}}</div>
                            </div>
                        </div>
                        <div v-if="item.iStatus ==1" class="r-info">
                          审核中...
                        </div>
                        <div v-if="item.iStatus ==3"  class="r-info">
                          审核未通过
                        </div>
                        <div class="r-bnt"  v-if="item.iStatus ==2">
                            <van-switch v-model="item.iIsDisplay" @change="iIsDisplay(item)" size="20px" style="margin-right:12px" />
                            <!-- <van-button type="danger" size="mini" style="margin-right:12px">删除</van-button> -->
                        </div>

                    </div>
                    <template #right>
                        <van-button square text="删除" type="danger" style="height: 100%;" class="delete-button" />
                    </template>
                </van-swipe-cell>
                <br><br>
            <template>
                <van-button icon="plus" type="default" size="mini" style="width:50%;margin:0 auto;padding:15px 0" round block @click="addPay">添加收款方式</van-button>
            </template>
        </div>
        <pay-add ref="payType" @refPay="refData"></pay-add>
    </div>
</template>
<script>
import {Dialog} from 'vant'
import {getPayMode,getPayDictionary,delPayMode,updateDisplay} from '@/api/otc'
import PayAdd from '@/components/pay/addPay'
export default {
    components:{
        PayAdd
    },
    data(){
        return{
            payModes:[],
            openType: true,
            pLoading: false,
            refreshing:false,
            loadedall:false,
            initInterval:null
        }
    },
    created(){
        this.init()
        this.initInterval = setInterval(()=>{
          this.init()
        },2000)
    },
    beforeDestroy(){
        clearInterval(this.initInterval)
    },
    methods: {
        init(){
            getPayMode({iIdle:0})
            .then((res)=>{
                // debugger
                this.payModes = res.data.vPayModeList
                this.payModes.forEach(i=>{
                    i.iIsDisplay = i.iIsDisplay ? true:false
                })
            })
            getPayDictionary({iDictType:3})
                .then((res)=>{
                    this.payDictionarys = res.data.vPayDictionary
                    this.pLoading = true
                })
        },
        // position 为关闭时点击的位置
        // instance 为对应的 SwipeCell 实例
        beforeClose({ position, instance, name, id }) {
            console.log(name,'name');
            const _this = this
            switch (position) {
                case 'left':
                case 'cell':
                case 'outside':
                instance.close();
                break;
                case 'right':
                Dialog.confirm({
                    message: '确定删除吗？',
                }).then(() => {
                    _this.del(name)
                    instance.close();
                });
                break;
            }
        },
        del(id){
            console.log(id);
            delPayMode({iId:id})
                .then(()=>{
                    //this.$message.success('删除成功')
                    getPayMode({iIdle:0})
                    .then((res)=>{
                        this.payModes = res.data.vPayModeList
                        this.payModes.forEach(i=>{
                            i.iIsDisplay = i.iIsDisplay ? true:false
                        })
                    })
                })
        },
        getPayModeTypeName(id){
            if(this.payDictionarys.length > 0){
                return this.payDictionarys.find(i=> i.iId ==id).sItemDesc
            }
        },
        onClickLeft() {
            if (this.$route.query.goindex === 'true') {
                this.$router.push('/');
            } else {
                this.$router.back(-1);
            }
        },
        refData(){
            this.init()
        },
        addPay(){
            this.$refs.payType.init()
        },
        iIsDisplay(item){
            const params ={
                iId:item.iId,
                iIsDisplay:item.iIsDisplay ? 1:0
            }
            updateDisplay(params)
            .then(()=>{
                this.$message.success('设置成功');
            })
            .catch( () =>{
              item.iIsDisplay = !item.iIsDisplay
            })
        },
    },
}
</script>
<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    .card-info{
      font-size: 0.14rem;
      color: #b8b8b8;
    }
    .p-flex{
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
        padding-top: 8px;
        padding-bottom: 8px;
        .p-cont{
            -webkit-box-flex: 1;
            -webkit-flex: 1;
            flex: 1;
            min-width: 0;
        }
        .imgs{
            width: 50px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            img{
                width: 30px;
            }
        }
        .p-title{
            font-weight: 400;
            //font-size: .22rem;
            line-height: 1.4;
            color: rgba(0,0,0,0.9);
            color: var(--weui-FG-0);
            width: auto;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            word-wrap: normal;
            word-wrap: break-word;
            -webkit-hyphens: auto;
            hyphens: auto;
            margin-bottom: 0;
        }
        .p-dec{
            color: rgba(0,0,0,0.3);
            color: var(--weui-FG-2);
            font-size: 14px;
            line-height: 1.4;
            padding-top: 4px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            word-wrap: break-word;
            -webkit-hyphens: auto;
            hyphens: auto;
        }
    }
    .user-pay-cont{
        padding-top: 46px;
    }
    .r-bnt{
        display: flex;
    }
    .r-info{
        padding-right: 0.3rem;
        color:darkgray;
    }
</style>