<template>
  <div class="ads-list-box">
    <van-nav-bar
        title="提币抢单"
        class="header_title"
        safe-area-inset-top
        placeholder
        :fixed="true"
    />
    <div style="height:1rem"></div>
    <div class="list-cont-box">
      <van-empty description="暂无待抢订单数据" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length==0&&loadedall" />
      <van-pull-refresh v-else style="min-height: 65vh;background:none;padding-bottom: 12px;" v-model="refreshing" @refresh="refreshData">
        <van-list
            v-model="loading"
            loading-text=" "
            :finished="loadedall"
            finished-text="没有更多了"
            :error.sync="error"
            error-text="请求失败，点击重新加载"
            @load="loadData"
        >
          <div class="warp-box">
            <div v-for="(item,index) in dataSource" :key="index" style="background:#fff;" class="order-list-cont">
              <van-cell :value="moment(item.iCreateTime).format('MM-DD HH:mm:ss')" class="cust-hed" >
                <template #title>
                  <span class="bs-span">
<!--                    <van-tag type="warning" v-if="item.iMerchantOrder === 1">提</van-tag>-->
                  </span>
                  <span style="color:#FF5763;font-size: 16px"> {{ Number(item.dTradeAmount).toFixed(2) }}</span>
                  <span style="color:darkgray;font-size: 12px"> {{item.sUnit}}</span>
                </template>
              </van-cell>
              <!-- <van-cell :title="item.iOrderType === 2 ? '购买'+item.sToken :'出售'+item.sToken" is-link @click="$router.push({name:'orderdetail',params:{id:item.sOrderId}})" /> -->
              <div class="list-cont">
                <div class="flex-wrapper" align-items="start">
<!--                  <span class="flex-demo ord_title" justify-content="start" fill>时间</span>-->
                  <span class="flex-demo ord_title" justify-content="start" fill>订单数量</span>
                  <span class="flex-demo ord_title" justify-content="start" fill>奖励</span>
<!--                  <span class="flex-demo ord_title" justify-content="start" fill>交易总额({{item.sUnit}})</span>-->
                </div>
                <div class="flex-wrapper" align-items="start">
<!--                  <span class="flex-demo ord_cont" justify-content="start" fill>{{ moment(item.iCreateTime).format('MM:DD HH:mm:ss') }}</span>-->
                  <span class="flex-demo ord_cont" justify-content="start" fill>
                    {{ Number(item.dTradeQuantity).toFixed(2)}}
                    <span class="ord_title"> USDT</span>
                  </span>
                  <span class="flex-demo ord_cont suss-pays" justify-content="start" fill>
                    {{ initRewards(item.dTradeQuantity)}}
                    <span class="ord_title "> USDT</span>
                  </span>
<!--                  <span class="flex-demo ord_cont" justify-content="start" fill>{{ Number(item.dTradeAmount).toFixed(2) }}</span>-->
                </div>
                <div align-items="start" style="line-height: 24px;padding-left: 8px">
                  <span style="font-weight: 600;">总计：{{ (Number(item.dTradeQuantity) + Number(initRewards(item.dTradeQuantity))).toFixed(2)}} USDT</span>
<!--                  <span class="flex-demo user-buy" justify-content="start" fill>{{ item.iOrderType === 1 ? item.sBuyNickName+"(买方)" : item.sSellNickName+"(卖方)" }} </span>-->
                  <button class="go-detail" @click="robOrder(item.sOrderId)">抢单</button>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import {getShareOrderList,getRobOrderList,robShareOrder, getAdsConfigList,otcUrl} from '@/api/otc'

import {mapGetters,mapActions} from 'vuex'
import moment from 'dayjs'
import {GpayListMixin} from '@/mixins/GpayListMixin'
export default {
  name: "RobOrder",
  mixins: [GpayListMixin],
  data(){
    return {
      queryParam:{
        iOrderProcess:4,
        sToken:'USDT',
      },
      resultName: 'vList',
      url: {
        list: otcUrl.GetShareOrderList,
      },
      getTradingOrderInterval: null,
      rewards:[],
    }
  },
  created() {
    //查询数据
    //this.initParam();
    // this.refreshData();
    const params = this.queryParam
    params.iPageNo = 0
    params.iPageSize = 100
    params.sColumn = "iCreateTime"
    params.sOrder = "desc"
    getRobOrderList(params)
        .then(res=>{
          // console.log('---------------定时跑')
          if(res.data.vList&&res.data.vList.length >=1){
            this.dataSource = res.data.vList
          }
        })
    this.getTradingOrderInterval = setInterval(()=>{
      const params = this.queryParam
      params.iPageNo = 0
      params.iPageSize = 100
      params.sColumn = "iCreateTime"
      params.sOrder = "desc"
      getRobOrderList(params)
          .then(res=>{
            // console.log('---------------定时跑')
            if(res.data.vList&&res.data.vList.length >=1){
              this.dataSource = res.data.vList
            }
          })
    },1000)
    getAdsConfigList({iIdle:0})
    .then(res=>{
      // debugger
      this.rewards = res.data.vList
    })
  },
  beforeDestroy(){
    clearInterval(this.getTradingOrderInterval)
  },
  methods:{
    moment,
    ...mapGetters(['iCreditLevel','iAdsStatus']),
    initState(state){
      switch (state) {
        case 1:
          return '未支付'
        case 2:
          return '已支付 等待放币'
        case 3:
          return '申诉中'
        case 4:
          return '放币超时'
        case 10:
          return '已完成'
        case 11:
          return '已取消'
        case 12:
          return '支付超时'
      }
    },
    initRewards(q){
      const index = this.rewards.findIndex(i=>i.iLevel ==this.iCreditLevel())
      return index >-1 ? (Number(q) * Number(this.rewards[index].iAdsReward)).toFixed(4) : 0
    },
    robOrder(id){
      if(this.iAdsStatus()!==2){
        this.$dialog.confirm({
          title:'未认证',
          message: '您需要先通过承兑商认证，才可以抢单，要现在去认证吗？',
          messageAlign: 'left',
          confirmButtonText:'去认证',
          confirmButtonColor: '#3D7EFF',
          cancelButtonText:'我再看看'
        })
        .then(()=>{
          this.$router.push('/my')
        })
      }else{
        this.$dialog.confirm({
          title:'抢单确认',
          message: '抢单后请在规定的时间内完成付款，否则订单将自动取消！您确定要抢此单吗？',
          messageAlign: 'left',
          confirmButtonText:'确定抢单',
          confirmButtonColor: '#3D7EFF',
          cancelButtonText:'我再看看'
        }).then(() =>{
          robShareOrder({sOrderId:id})
              .then(()=>{
                this.$router.push('/orderdetail?id='+id)
              })
        })
      }


    }
  }
}
</script>

