<template>
    <section>
        <van-nav-bar
            :title="appealTitle"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :fixed="true"
        />
        <div class="warp-loading" v-if="upImgLoading">
            <van-loading class="upFile" size="24px" vertical color="#0094ff">
                <span style="color:#0094ff;font-size:12px">图片上传中...</span>
            </van-loading>
        </div>
        <div class="cont-box" style="padding-top:46px;">
            <template v-if="appealSteps==1">
                <van-notice-bar
                    wrapable
                    :scrollable="false"
                    left-icon="info-o"
                    :text="isBuy==2?'请注意，您的资产将一直被锁定在平台，直至订单完成或取消':'请放心，您购买的资产将一直被锁定在平台，直至订单完成或取消'"
                />
                <!-- <div class="tips-box">
                    {{isBuy==1?'请注意，您的资产将一直被锁定在平台，直至订单完成或取消':'请放心，您购买的资产将一直被锁定在平台，直至订单完成或取消'}}
                </div> -->
                <div class="lei"><b>问题类型</b></div>
                <van-radio-group v-model="iQuestionType.iId">
                    <van-cell-group>
                        <van-cell :title="p.dec" v-for="p in problem" :key="p.iId" clickable @click="proHand(p)">
                            <template #right-icon>
                                <van-radio :name="p.iId" />
                            </template>
                        </van-cell>
                    </van-cell-group>
                </van-radio-group>
                <van-button class="f-bnt-true" style="width:88%;margin:20px auto;" round block type="info" @click="next">
                    下一步
                </van-button>
            </template>
            <template v-if="appealSteps==2">
                <!-- <div class="tips-box">
                    提交申诉后，申诉专员将介入此次交易，直至申诉结束。您提交的申诉原因和凭证将发在申诉历史中展示
                </div> -->
                <van-notice-bar
                    wrapable
                    :scrollable="false"
                    background="#ecf9ff"
                    color="#1989fa"
                    left-icon="info-o"
                    text="提交申诉后，申诉专员将介入此次交易，直至申诉结束。您提交的申诉原因和凭证将发在申诉历史中展示"
                />
<!--                <div class="lei">申诉原因</div>-->
<!--                <a class="iqw-dec" @click="iqType = true">{{iQuestionType.dec}}</a>-->
<!--                <van-action-sheet v-model="iqType" title="申诉原因">-->
<!--                    <van-cell style="text-align: center;" v-for="p in problem" :key="p.iId" :title="p.dec" @click="handleChangePro(p)" :class="{'font3D7EFF':iQuestionType.iId == p.iId}">-->
<!--                    </van-cell>-->
<!--                </van-action-sheet>-->
                <template>
                    <div class="lei mt-20">申诉原因</div>
                    <a class="iqw-dec" :class="{'font-c8c':!iQuestionDetail}" @click="spType = true">{{iQuestionDetail && iQuestionDetail!=='' ? initDetail(iQuestionDetail):"请选择具体问题"}}</a>
                    <van-action-sheet v-model="spType" title="具体问题">
                      <template v-if="orderType ==2">
                        <van-cell style="text-align: center;" title="未收到转账" @click="handleChangeSpecific(4001)" />
                        <van-cell style="text-align: center;" title="支付金额不对" @click="handleChangeSpecific(4002)" />
                        <van-cell style="text-align: center;" title="其他" @click="handleChangeSpecific(4100)" />
                      </template>
                      <template v-if="orderType ==1">
                        <van-cell style="text-align: center;" title="支付金额不对" @click="handleChangeSpecific(5001)" />
                        <van-cell style="text-align: center;" title="其他" @click="handleChangeSpecific(5100)" />
                      </template>
                    </van-action-sheet>
                </template>
                <template v-if="iQuestionType.iId!=1009">
                    <div class="lei mt-20">上传凭证</div>
                    <a class="iqw-dec">
                        <div class="van-uploader">
                            <div class="van-uploader__wrapper">
                                <div class="van-uploader__preview" v-for="(url,index) in imgUrl" :key="url">
                                    <div class="van-image van-uploader__preview-image">
                                        <!-- <img :src="url" class="van-image__img" style="object-fit: cover;"> -->
                                        <van-image style="width:30%;height:22vw;margin:0 10px;" :src="url">
                                            <template v-slot:loading>
                                                <van-loading type="spinner" size="20" />
                                            </template>
                                        </van-image>
                                    </div>
                                    <div class="van-uploader__preview-delete" @click="del(url,index)">
                                        <i class="van-icon van-icon-cross van-uploader__preview-delete-icon"></i>
                                    </div>
                                </div>
                                <div v-if="imgUrl.length < 5" class="van-uploader__upload" style="margin-bottom:0" @click="photoshow = true">
                                    <i class="van-icon van-icon-photograph van-uploader__upload-icon"></i>
                                </div>
                            </div>
                        </div>
                    </a>
                </template>
                <div class="lei mt-20">详细描述(选填)</div>
                <a class="iqw-dec npd">
                    <van-field
                        v-model="decCont"
                        rows="2"
                        autosize
                        label=""
                        type="textarea"
                        maxlength="100"
                        placeholder="描述一下您遇到的问题，可以帮助我们更快定位问题"
                        show-word-limit
                    />
                </a>
                <van-button class="f-bnt-true" style="width:88%;margin:0 auto;" round block type="info" @click="handleOk">
                    提交申诉
                </van-button>
            </template>
        </div>
        <!--上传图片-->
        <van-action-sheet
            v-model="photoshow"
            cancel-text="取消"
            close-on-click-action
            :actions="actions"
            @select="takePhoto"
        />
    </section>
</template>
<script>
import {addArbitration,getArbitrationList,getOrderDetail} from "@/api/otc";
import moment from 'dayjs'
import {problemArr} from '@/utils/problem'
import {ossUrl,getSignedUrl} from '@/api/oss'

let problemData = []
const file_url = process.env.VUE_APP_FILE_API_URL
export default {
    data() {
        return {
            ossUrl,
            ssk: '',
            iQuestionType: null,
            iQuestionDetail: null,
            isBuy: 2,
            orderType:null,
            appealSteps: 2,
            problem: [],
            specific:[],
            iqType: false,
            spType: false,
            actions: [
                {name: '从相册选择'},
                {name: '拍照'},
            ],
            photoshow: false,
            imgUrl: [],
            sProof: [],
            upImgLoading: false,
            iType: '',
            decCont: '',
            sOrderId: ''
        };
    },
    created(){
        this.ssk = this.$ls.get('SSK')
        this.sOrderId = this.$route.query.orderId
        this.orderType = this.$route.query.iOrderType
        this.iType = this.$route.query.iType
        this.iQuestionType =this.orderType == 2 ? 400 : 500
        let nr = []
        for(let n of problemArr){
          if(n.isBuy == this.isBuy){
            nr.push(n)
          }
        }
        this.problem = nr
        problemData = nr
    },
    computed:{
        appealTitle(){
            switch(this.appealSteps) {
                case 1:
                    return "您遇到了什么问题？"
                    break;
                case 2:
                    return "提交申诉"
                    break;
                default:
                    '申诉'
            }
        },
    },
    methods:{
        // 上传凭证图片处理
        takePhoto(action, index) {
            let that = this;
            function setOptions(srcType) {
                let options = {
                    // Some common settings are 20, 50, and 100
                    quality: 80,
                    destinationType: Camera.DestinationType.FILE_URI,
                    // In this app, dynamically set the picture source, Camera or photo gallery
                    sourceType: srcType,
                    encodingType: Camera.EncodingType.JPEG,
                    mediaType: Camera.MediaType.PICTURE,
                    correctOrientation: true  //Corrects Android orientation quirks
                }
                return options;
            }

            if (index === 0) {
                let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
                let options = setOptions(srcType);
                navigator.camera.getPicture(function cameraSuccess(imageUri) {
                    that.fileUpload(imageUri)

                }, function cameraError(error) {
                    console.debug("Unable to obtain picture: " + error, "app");

                }, options);
            }
            if (index === 1) {
                let srcType = Camera.PictureSourceType.CAMERA;//拍照
                let options = setOptions(srcType);
                navigator.camera.getPicture(function cameraSuccess(imageUri) {
                    that.fileUpload(imageUri)

                }, function cameraError(error) {
                    console.debug("Unable to obtain picture: " + error, "app");

                }, options);
            }
        },
        // 使用cordova FileUpload上传图片
        fileUpload: function (imageURI) {
            function getCaption(obj, state) {
                let index = obj.lastIndexOf("?");
                if (index > 0) {
                    if (state == 0) {
                        obj = obj.substring(0, index);
                    } else {
                        obj = obj.substring(index + 1, obj.length);
                    }
                    return obj;
                } else {
                    return obj;
                }
            }

            imageURI = getCaption(imageURI, 0)

            let that = this
            that.upImgLoading = true

            let FileUploadOptions = window.FileUploadOptions
            let FileTransfer = window.FileTransfer
            let options = new FileUploadOptions()
            options.fileKey = 'file'
            let path = imageURI.substr(imageURI.lastIndexOf('/') + 1);
            options.fileName = path
            options.mimeType = 'image/jpeg'
            let headers = {'Authorization': ' ' + that.ssk}
            options.headers = headers;
            let ft = new FileTransfer()

            ft.upload(imageURI, encodeURI(that.ossUrl.PrivateUpload), async function (data) {

                function wait(ms) {
                    return new Promise(resolve => setTimeout(() => resolve(), ms));
                };

                let resString = data.response
                let resObject = JSON.parse(resString) // 字符串转对象
                that.upImgLoading = false
                if (resObject.iRspCode === 0) {
                    let imgUrl = resObject.data.vUrlPath[0];
                    // 处理图片
                    getSignedUrl(imgUrl).then(res=>that.imgUrl.push(res))
                    // that.imgUrl.push(file_url + imgUrl)
                    that.sProof.push(imgUrl)
                    //alert(imgUrl)
                } else {
                    that.upImgLoading = false
                that.$toast('上传失败稍后重试')
                }
            }, function (err) {
                console.log(err)
                that.upImgLoading = false
                that.$toast('上传失败稍后重试')
            }, options)
        },
        onClickLeft() {
            // if(this.appealSteps >1) return this.appealSteps = 1
            if (this.$route.query.goindex === 'true') {
                this.$router.push('/');
            } else {
                this.$router.back(-1);
            }
        },
        onChange(index) {
            this.index = index;
        },
        preview(arr,index){
            this.show = true
            this.images = arr
            this.index = index;
        },
        proHand(item){// 问题类型
            this.iQuestionType = item
            this.specific = problemData.find((i) => i.iId == item.iId).children
        },
        next(){
            if(!this.iQuestionType.iId){
                this.$toast('请选择一个您遇到的问题！')
                return
            }
            this.appealSteps = 2
        },
        onConfirm(values) {

        },
      initDetail(v){
        switch (v){
          case 4001:
            return '未收到转账'
          case 4002:
            return '支付金额不对'
          case 4100:
            return '其他'
          case 5001:
            return "支付金额不对"
          case 5100:
            return  '其他'
        }
      },
        del(url,idx){
            console.log(idx,this.imgUrl.indexOf(url))
            this.sProof.splice(idx,1);
            console.log(this.sProof);
            console.log('-------------------------------------')
            this.imgUrl.splice(idx,1);
            console.log(this.imgUrl);
        },
        handleChangePro(value) {
            console.log(value)
            this.iqType = false
            this.iQuestionType = value
            this.specific = problemData.find((i) => i.iId == value.iId).children
        },
        handleChangeSpecific(value) {
            this.iQuestionDetail = value
            this.spType = false
        },
        handleOk(){
            // if(!this.decCont && this.iQuestionType.iId == 1009){
            //     this.$message.warning('请填写详细描述，方便处理问题！')
            //     return
            // }
            // if(this.iQuestionType.iId == 1002 || this.iQuestionType.iId == 1009){
            //     this.iQuestionDetail = this.iQuestionType
            // }
            // if(this.iQuestionType.iId != 1009  && this.iQuestionType.iId != 1002){
            //     if(!this.iQuestionDetail.iId){
            //         this.$message.warning('请选择一个具体问题')
            //         return
            //     }
            // }
            // if(this.iQuestionType.iId == 1002&&this.sProof.length<1){
            //     this.$message.warning('请至少上传一个凭证')
            //     return
            // }
            const params = {
                sOrderId:this.sOrderId,//'a944b3ef660f467899eb67fa9176cb91',//this.sOrderId,
                iType:parseInt(this.iType),
                iQuestionType:this.iQuestionType,
                iQuestionDetail:this.iQuestionDetail,
                sProof:this.sProof.join(','),
                sDescription: this.decCont
            }
            console.log(params)
            addArbitration(params)
            .then(res=>{
                this.$message.success('申诉成功！')
                this.$router.replace('/orderdetail?id='+this.sOrderId+'')
            })
        }
    },
    filters:{
      time(v){
          return moment(v).format('YYYY-MM-DD HH:mm:ss')
      },
      qType(v){
          let data = problemArr.find((i) => i.iId == v)
            //客服接受 101 客服留言 102 客服放币 103 客服取消订单104
            if(!data){
                switch(v) {
                    case 101:
                        return "平台客服已介入，请等待处理"
                        break;
                    case 102:
                        return "客服留言"
                        break;
                    case 103:
                        return "协商结果"//"客服放币"
                        break;
                    case 104:
                        return "协商结果"//"客服取消订单"
                        break;
                    default:
                        '-'
                }
            }else{
                return data.dec
            }
          //return data?data.dec:'111'
      },
      iType(v){
          switch(v) {
            case 1:
                return "提交申诉"
                break;
            case 2:
                return "补充描述"
                break;
            case 3:
                return "处理完成"
                break;
            case 4:
                return "处理结果-放币"//"对方处理放币"
                break;
            case 5:
                return "处理结果-放币超时"//"对方处理超时"
                break;
            case 11:
                return "客服留言"
                break;
            case 103:
                return "协商结果"
                break;
            case 104:
                return "协商结果"
                break;
            case 101:
                return "平台客服已介入，请等待处理"
                break;
            case 102:
                return "客服留言"
                break;
             case 110:
                return "协商结果"//"客服关闭订单"
                break;
            default:
                '-'
          }
      },
  }
}
</script>
<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    /deep/.nk-icon .van-icon{
        font-size: .42rem;
        margin-top: -6px;
        margin-right: .06rem;
        .van-icon__image{
            height: auto;
        }
    }
    .cont-box{
        padding-bottom: 1rem;
    }
    .tips-box{
      padding: 6px;
      margin: 16px;
      border: 1px solid #eae8e8;
      border-radius: 4px;
      font-size: 12px;
    }
    .lei{
       margin: 16px;
       font-size: 15px;
       font-weight: bold;
    }
    .iqw-dec{
        border: 1px solid #eae8e8;
        border-radius: 4px;
        margin: 0 16px;
        padding: 8px 12px;
        margin-bottom: 16px;
        display: block;
        position: relative;
        .upFile{
            position: absolute;
            top: 0;
            width: 93%;
            height: 100%;
        }
    }
    .npd{
        padding:0;
    }
    .list-cont{
        padding:0 6px;
        background: #f0f2f5;
        padding-bottom: 1rem;
        li{
            padding: 0 12px;
            .time{
                margin: 8px 0;
            }
            .title{
                border-bottom: 1px solid #f0f2f5;
                padding-left: 12px;
                padding-top: 10px;
                padding-bottom: 10px;
            }
            .conts{
                padding-top: 12px;
            }
            .content{
                background: #fff;
                border-radius: 5px;
            }
        }
        .shs-ul{
            margin: 0;
            li{
                padding-bottom: 10px;
                color: #999;
            }
            p{
                color: #333;
                padding-top: 5px;
                margin: 0;
                word-wrap:break-word;
                .sp-img{
                    border: 1px solid #c6d0de;
                    display: inline-block;
                    margin-right: 20px;
                    border-radius: 5px;
                    padding: 5px;
                }
                img{
                    width: 160px;
                    height: 160px;
                }
            }
        }
    }
</style>