<!--                </div>-->
              <div class="detail-info" >
                <img :src="require('../../assets/img/ic_usdt.png')" width="28px"/>
              </div>
              <span class="detail-info">USDT</span>
            </van-row>
<!--            <van-action-sheet v-model="show" title="选择币种">-->
<!--                <van-cell-group >-->
<!--                    <van-cell  v-for="item in tokenNames" :key="item.sToken" :title="item.sToken" @click="selectToken(item)"  />-->
<!--                </van-cell-group>-->
<!--            </van-action-sheet>-->
        </div>
<!--        <div style="height: 0.2rem;"></div>-->
        <div class="sub-title">选择主网</div>
        <div v-if="protocols.length > 1">
          <span  v-for="item in protocols" :key="item.sProtocol" >
            <div :class="{chain:true,active:chain === item.sChain }" v-if="item.bWithdrawSwitch" @click="chainChange(item)">
              {{item.sProtocol}}
            </div>
          </span>

        </div>
        <div>
          <div class="sub-title">提币地址</div>
<!--            <van-row class="card-box" type="flex" >-->
<!--                <van-col class="detail-info">提币地址</van-col>-->
<!--            </van-row>-->
            <van-row>
                <van-col style="width: 100%;">
                    <van-field size="large"
                        class="border-style"
                        v-model="withdrawAddress"
                        center
                        clearable
                        label=""
                        :border="true"
                        placeholder="输入或长按粘贴地址"
                    >
                        <template #right-icon>
                            <van-icon name="scan" size="2em" @click="scanQr"/>
                        </template>
                        <template #extra>
                            <van-action-sheet v-model="showPopover" title="提币地址">
                                <van-cell-group style="padding:20px 0" v-if="adds.length>0">
                                    <van-cell v-for="item in adds" :key="item.sAddress" :title="item.sAddress" @click="addSel(item.sAddress)"  />
                                </van-cell-group>
                                <div v-else>
                                    <van-empty image-size="50" description="没有数据" :image="require('../../assets/img/empty-image-default.png')" />
                                    <van-button style="width:46%;display: block; margin: 0 auto; margin-bottom: 20px;" round block type="info" class="bnts" @click="goAdds">添加提币地址</van-button>
                                </div>
                            </van-action-sheet>
                            <van-icon style="margin-left: 10px" name="hotel-o" size="2em" @click="showPopover=true"/>
                        </template>
                    </van-field>
                </van-col>
            </van-row>
        </div>
        <div>
          <div class="sub-title" style="margin-top: 0.5rem">提现数量</div>
<!--            <van-row  class="card-box" type="flex" >-->
<!--                <van-col class="detail-info" >提现数量</van-col>-->
<!--            </van-row>-->
            <van-row>
                <van-col style="width: 100%;">
                    <van-field size="large"
                        class="border-style"
                        v-model="withdrawNumber"
                        center
                        clearable
                        label=""
                        type="number"
                        :border="true"
                        :formatter="formatterNum"
                        @input="qtyNumb"
                        :min="Number(withdrawLimit.sMinWithdrawQty)" :max="Number(withdrawLimit.sUsableWithdrawQty)"
                        placeholder="请输入提现数量"
                    >
                    <!-- :placeholder="'最小提币数量'+Number(withdrawLimit.sMinWithdrawQty)" -->
                        <template #right-icon>
                            <!-- @click="withdrawNumber = Number(withdrawLimit.sUsableWithdrawQty) " -->
                            <span class="fontColor">{{selToken.sToken}}</span>|<span style="color:black;padding-left:5px;font-size: 15px;color:#666666" @click="setAll">全部</span>
                        </template>
                    </van-field>
                </van-col>
            </van-row>
            <div class="err" v-if="errorTips">{{errorTips}}</div>
            <div class="kt-numb">{{'可提数量：'+Number(withdrawLimit.sUsableWithdrawQty)}}</div>
            <div class="kt-numb" style="display: inline-block;width: 50%">{{'24小时限额：'+Number(withdrawLimit.sMaxWithdrawQty)}}</div>
            <div class="kt-numb" style="display: inline-block;width: 50%">{{'24小时已提：'+Number(withdrawLimit.sCurrWithdrawQty)}}</div>
<!--            <van-row>-->
<!--                <van-col style="width: 50%;">-->
<!--                    <van-cell :value=" '24小时限额：'+Number(withdrawLimit.sMaxWithdrawQty)" >-->
<!--                    </van-cell>-->
<!--                </van-col>-->
<!--                <van-col style="width: 50%;">-->
<!--                    <van-cell :value=" '24小时已提：'+Number(withdrawLimit.sCurrWithdrawQty)" >-->
<!--                    </van-cell>-->
<!--                </van-col>-->
<!--            </van-row>-->
        </div>
        <!-- <div class="kt-numb">{{'提币手续费：'+(Number(withdrawLimit.sWithdrawFee)*100).toFixed(2) +'%'}}</div> -->
      <div class="sub-title" style="margin-top: 0.5rem">提币手续费</div>
      <div style="width: 100%;">
          <van-row>
            <van-col style="width: 100%;">
              <van-field size="large"
                         class="border-style"
                         :value="withdrawNumber ? withdrawNumber*withdrawLimit.sWithdrawFee : null"
                         center
                         type="number"
                         :border="true"
                         disabled
                         :min="Number(withdrawLimit.sMinWithdrawQty)" :max="Number(withdrawLimit.sUsableWithdrawQty)"
                         :placeholder="(Number(withdrawLimit.sWithdrawFee)*100).toFixed(2) +'%'"
              >
                <!-- :placeholder="'最小提币数量'+Number(withdrawLimit.sMinWithdrawQty)" -->
                <template #right-icon>
                  <!-- @click="withdrawNumber = Number(withdrawLimit.sUsableWithdrawQty) " -->
                  <span >{{selToken.sToken}}</span>
                </template>
              </van-field>
            </van-col>

<!--            <van-col style="width: 100%;">-->
<!--              <van-cell  :value="(Number(withdrawLimit.sWithdrawFee)*100).toFixed(2) +'%'">-->
<!--              </van-cell>-->
<!--            </van-col>-->
          </van-row>
        </div>
        <div class="dec-box">
            <p>温馨提示:</p>
            <p>最小提币数量为：<span>{{ Number(withdrawLimit.sMinWithdrawQty) +'  '+ selToken.sToken }}</span></p>
            <p>请务必确认手机系统安全，防止信息被篡改或泄露。</p>
        </div>
      <div style="height: 110px"></div>
      <div class="bottom-box">
        <van-row>
          <van-col style="width: 100%;">
            <van-cell title="到账数量" :value=" receivedNumber()+'  '+selToken.sToken">
            </van-cell>
          </van-col>
        </van-row>
        <div class="tb-bnt">
          <van-button block type="info" class="bnts" :disabled="canSubmit" @click="withdraw">提 币</van-button>
        </div>
      </div>

        <div style="height:.2em"></div>
        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi = '17'
            @ok="applyVerify"
        />
      <div style="height: 1rem"></div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import moment from 'dayjs'
    import {getMerchantTokens,userWithdraw,getWithdrawLimit,getWalletAddress} from '@/api/wallet'
    import SafeVerifyModal from "@/components/SafeVerifyModal/index";
    import VueQrcode from 'vue-qrcode'
    export default {
        name: 'user-balance-withdraw',
        components: {
            [Toast.name]: Toast,
            SafeVerifyModal,
        },
        data() {
            return {
                showPopover:false,
                show:false,
                type:0,
                chain: 'Tron',
                fixed:true,
                detail:null,
                merchantTokens:[],
                withdrawLimit:[],
                address:[],
                selToken:{},
                selTokens:[],
                depositInfo:{},
                selChain:{},
                withdrawAddress:'',
                message:'成功',
                withdrawNumber:null,
                errorTips:'',
                tokenNames:[],
                tokens: []
            }
        },
        computed:{
            canSubmit(){
                if(this.withdrawAddress ==='' || !this.withdrawNumber){
                    return true;
                }else{
                    if(this.withdrawNumber<Number(this.withdrawLimit.sMinWithdrawQty)||this.withdrawNumber>Number(this.withdrawLimit.sUsableWithdrawQty)){
                        return true;
                    }else{
                        return false;
                    }
                }
            },
            adds(){
                const tmp = []
                this.address.forEach(item=>{
                    if(item.sChain === this.chain){
                        tmp.push(item)
                    }
                })
                return tmp
            },
            protocols(){
                const protocols =[]
                this.tokens.forEach(token=>{
                  if(token.sToken === this.selToken.sToken){
                    if(token.sProtocol ==='TRC20'){
                      protocols.unshift(token)
                    }else{
                      protocols.push(token)
                    }
                  }
                })
                return protocols
            }
        },
        created() {
            getMerchantTokens({iIdle: 0})
                .then((res) => {
                    this.tokenNames = []
                    this.tokens = res.data.vTokens
                    this.tokens.forEach(token =>{
                        if(this.tokenNames.findIndex(t=> t.sToken === token.sToken)<0) {
                          if (token.bWithdrawSwitch) {
                            this.tokenNames.push(token)
                          }
                        }
                    })
                    this.selToken = this.tokenNames[0]
                  // this.chain = this.tokenNames[0].sChain
                  this.selChain = this.protocols[0]

                    getWithdrawLimit({
                      sToken:this.selToken.sToken,
                      sChain:this.chain,
                      iAcctType:this.$route.query.iAcctType || 0
                    })
                        .then(res=>{
                            this.withdrawLimit =res.data
                        })
                    getWalletAddress({iPageNo:0,iPageSize:100,sToken:this.selToken.sToken})
                        .then(res=>{
                            this.address = res.data.vAddress
                        })
                    return
                    let tokens = [];
                    res.data.vTokens.forEach(item => {
                        // debugger
                        if (item.bDepositSwitch) {
                            tokens.push(item);
                        }
                    });
                    console.log(tokens)
                    if (tokens.length > 0) {
                        this.merchantTokens = unique4(tokens);//去重后的tokens
                        this.selToken = this.merchantTokens[0];//默认选中第一个TOKEN
                        this.selTokens = this.selToken,tokens;
                        this.chain = this.selToken.tokens[0].sChain;
                        this.selChain = this.selToken.tokens[0];

                        getWithdrawLimit({
                          sToken:this.selToken.sToken,
                          sChain:this.selToken.tokens[0].sChain,
                          iAcctType:this.$route.query.iAcctType || 0
                        })
                            .then(res=>{
                                this.withdrawLimit =res.data
                            })
                        getWalletAddress({iPageNo:0,iPageSize:100,sToken:this.selToken.sToken})
                            .then(res=>{
                                this.address = res.data.vAddress
                            })
                    }
                    function unique4(arr) {
                        let hash = [];
                        for (let i = 0; i < arr.length; i++) {
                            let obj = {
                                sToken: '',
                                tokens: [],
                            };
                            obj.sToken = arr[i].sToken;
                            obj.tokens.push(arr[i]);
                            for (let j = i + 1; j < arr.length; j++) {
                                if (arr[i].sToken === arr[j].sToken) {
                                    obj.tokens.push(arr[j]);
                                    ++i;
                                }
                            }

                            hash.push(obj);
                        }
                        return hash;
                    }
                })
        },
        beforeRouteLeave(to, from, next) {
            next();
        },
        methods: {
            moment,
            ...mapGetters(['switchEmailVerify','switchCellphoneVerify','switchGoogleVerify']),
            ...mapActions(['GetUserExten']),
            goAdds(){
                this.$router.replace({path: '/my/walletAddress'});
            },
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            initType(type){
                switch (type) {
                    case 1:
                        return '充值'
                    case 2:
                        return '提币'
                }
            },
            returnStatus(index){
                switch (index){
                    case 5 :
                        return '提币完成'
                    case 6 :
                        return '拒绝提币'
                    case 8 :
                        return '用户取消'
                    case 13 :
                        return '充值完成'
                    default:
                        return '进行中'
                }
            },
            canDeposit(token){
                // debugger
                let tmpBoolean = false
                this.merchantTokens.forEach(item=>{
                    // debugger
                    if(item.sToken === token && item.bDepositSwitch)tmpBoolean=true
                })
                return tmpBoolean
            },
            onSelect(){
                this.show=true;
            },
            selectToken(item){
                this.selToken = item
                this.chain = this.protocols[0].sChain
                getWithdrawLimit({sToken:this.selToken.sToken,sChain:this.chain,iAcctType:this.$route.query.iAcctType || 0})
                    .then(res=>{
                        this.withdrawLimit =res.data
                    })
                getWalletAddress({iPageNo:0,iPageSize:100,sToken:this.selToken.sToken})
                    .then(res=>{
                        this.address = res.data.vAddress
                    })
                this.show = false;
            }
            ,
            chainChange(item){
                this.chain = item.sChain;
                this.selChain = item;
                getWithdrawLimit({sToken:this.selToken.sToken,sChain:this.selChain.sChain,iAcctType:this.$route.query.iAcctType || 0})
                    .then(res=>{
                        this.withdrawLimit =res.data
                        // debugger
                    })
                getWalletAddress({iPageNo:0,iPageSize:100,sToken:this.selToken.sToken})
                    .then(res=>{
                        this.address = res.data.vAddress
                    })
            },
            receivedNumber(){
                return this.withdrawNumber ? (this.withdrawNumber*(1-this.withdrawLimit.sWithdrawFee)).toFixed(8) : 0
            },
            formatterNum(value){
                return value.replace( /^1[6-9]$|^bai[2-9]\d$|^1\d{2}$/, '');
            },
            addSel(adds){
                this.showPopover = false;
                this.withdrawAddress =adds;
            },
            qtyNumb(){
                if(Number(this.withdrawLimit.sMinWithdrawQty) > this.withdrawNumber){
                    this.errorTips = '不能小于最小提币数量' + Number(this.withdrawLimit.sMinWithdrawQty) +'  '+ this.selToken.sToken
                    return
                }
                if(this.withdrawNumber > Number(this.withdrawLimit.sUsableWithdrawQty)){
                    this.errorTips = '不能大于可提数量'
                    return
                }
                this.errorTips = ''
            },
            setAll(){
                this.withdrawNumber = Number(this.withdrawLimit.sUsableWithdrawQty)
                this.qtyNumb()
            },
            withdraw(){
                //初始化
                this.$refs.safeVerifyModal.visible = true
            },
            applyVerify(codes){
                const params = {
                    iMethod:1,
                    sWithdrawAmount:this.withdrawNumber.toString(),
                    sAddress:this.withdrawAddress,
                    sChain:this.chain,
                    iAcctType:this.$route.query.iAcctType || 0,
                    sToken:this.selToken.sToken,
                    iSource:2
                }
                if(this.switchEmailVerify())params.sEmailVerifyCode = codes.sEmailVerifyCode
                if(this.switchCellphoneVerify())params.sSMSVerifyCode = codes.sSMSVerifyCode
                if(this.switchGoogleVerify())params.sGoogleVerifyCode = codes.sGoogleVerifyCode
                userWithdraw(params)
                    .then(()=>{
                        //去成功界面
                        this.message = "提币申请成功";
                        this.$router.replace({name:"balancewithdrawsucess",query:{message: this.message,decs:'预计24小时内审核完成！'}});
                        //Toast('提交成功,等待审核！')
                        this.$refs.safeVerifyModal.visible = false
                    })
                    .finally(()=>{
                        //this.$refs.safeVerifyModal.visible = false
                    })
            },
            //扫码
            scanQr(){
                let that = this
                cordova.plugins.gizscanqrcode.scan(
                    {//全部参数
                        "baseColor": "#4e8dec",             //(边框、按钮、导航栏等背景颜色，优先级最低，单独设置可覆盖)

                        //bar
                        "title": "请扫描地址二维码",                 //(标题文字)
                        "barColor": "4e8dec",               //(导航栏颜色)
                        "statusBarColor": "white",          //(状态栏字体颜色 white为白，不填为默认)

                        //describe string
                        "describe": "请扫描地址二维码",            //(提示用户文字，支持 \n 换行，多行文字需注意小屏幕设备适配问题)
                        "describeFontSize": "15",          //(字体大小)
                        "describeLineSpacing": "8",        //(行间距)
                        "describeColor": "ffffff",         //(文字颜色)

                        //scan border
                        "borderColor": "4e8dec",           //(扫描框颜色)
                        "borderScale": "0.6",              //(边框大小，0.1 ~ 1)

                        //choose photo button
                        "choosePhotoEnable": "true",       //(支持相册选取, 默认false)
                        "choosePhotoBtnTitle": "相册",      //(选取按钮文字)
                        "choosePhotoBtnColor": "4e8dec",   //(选取按钮颜色)

                        //flashlight
                        "flashlightEnable": "true"         //(支持手电筒, 默认false)
                    },
                    function (result) {
                        let res = JSON.parse(result);
                        that.withdrawAddress =res.result;
                        console.log(result);//二维码数据
                    },
                    function (error) {
                        console.log(error);//原因
                    }
                );
            }
        }
    };

</script>

