<template>
    <div>
        <keep-alive >
            <router-view v-if="$route.meta.keepAlive"/>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"/>
        <van-nav-bar
            :title="fdetail.sToken"
            class="header_title"
            left-arrow
            @click-left="onClickLeft"
        />
        <div>
            <van-cell-group>
                <!-- <van-cell :value="fdetail.sToken" class="f_header" /> -->
                <van-cell title="类型" :value="initType(fdetail.iType) "  />
                <van-cell title="状态" :value="returnStatus(fdetail.iStatus)"  />
                <van-cell title="区块链交易ID" :value="fdetail.sAddress"  >
                    <template #right-icon>
                        <!-- <van-icon name="description" v-clipboard:copy="fdetail.sAddress" v-clipboard:success="()=>Toast('已复制到剪切板')" v-clipboard:error="()=>Toast('复制失败')" /> -->
                        <a v-clipboard:copy="fdetail.sAddress" v-clipboard:success="success" v-clipboard:error="error"><img width="15" src="../../assets/img/copy.png"></a>
                    </template>
                </van-cell>
                <van-cell title="时间" :value="moment(fdetail.iCreateTime).format('YYYY-MM-DD HH:mm:ss')"  />
            </van-cell-group>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {walletUrl} from '@/api/wallet'
    import {udcUrl} from '@/api/udc'
    import {getMerchantTokens} from '@/api/wallet'

    import bg_default from '../../assets/img/ic_asset_bg.png';
    import moment from 'dayjs'

    export default {
        name: 'user-financial-detail',
        components: {
            [Toast.name]: Toast,
        },
        data() {
            return {
                background_image: bg_default,

            }
        },
        mounted() {

        },
        created() {
            const vRecords = Vue.ls.get("vRecords");
            this.fdetail = vRecords;
        },
        methods: {
            moment,
            ...mapGetters(['isLogin', 'userName', 'cellphone', 'email']),
            ...mapActions(['GetUserExten']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            initType(type){
                switch (type) {
                    case 2:
                        return '充值'
                    case 1:
                        return '提币'
                }
            },
            returnStatus(index){
                switch (index){
                    case 5 :
                        return '提币完成'
                    case 6 :
                        return '拒绝提币'
                    case 8 :
                        return '用户取消'
                    case 13 :
                        return '充值完成'
                    default:
                        return '进行中'
                }
            },
            success(){
                this.$toast('已复制到剪切板')
            },
            error(){
                this.$toast('复制失败')
            }
        }
    };

</script>

<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    .f_header {
        font-weight: 700;
        font-size: 18px;
        margin-bottom: 5px;
        color:darkblue;
    }
    /deep/ .van-cell__value--alone{
        color: @gpay-color3D7EFF;
        font-size: .3rem;
        font-weight: bold;
    }
    .fin_list{
        position: relative;
        text-align: left;
        background-color: white;
        margin-left: 20px;
        margin-bottom: 20px;
    }

    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }
    .user_avatar {
        margin-bottom: 10px;
        img {
            border: 0;
            border-radius: 50%;
        }
    }
</style>
