<template>
    <div>
        <!--上传图片-->
        <van-action-sheet
            v-model="photoshow"
            cancel-text="取消"
            close-on-click-action
            :actions="actions"
            @select="takePhoto"
        />
        <van-loading v-if="loading" size="24px" vertical color="#0094ff">
            <span style="color:#0094ff;font-size:12px">图片上传中...</span>
        </van-loading>
    </div>
</template>
<script>
    import Vue from 'vue'
    import {chgUserExten} from '@/api/udc'
    import {ossUrl} from '@/api/oss'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'

    export default {
        name: 'pubUpImg',
        data() {
            return {
                photoshow: false,
                ssk: Vue.ls.get('SSK'),
                actions: [
                    {name: '从相册选择'},
                    {name: '拍照'},
                ],
                loading:false
            }
        },
        methods: {
            ...mapActions(['GetUserExten', 'GetUserInfo']),
            init() {
                this.photoshow = true
            },
            takePhoto(action, index) {
                let that = this;

                function setOptions(srcType) {
                    let options = {
                        // Some common settings are 20, 50, and 100
                        quality: 80,
                        destinationType: Camera.DestinationType.FILE_URI,
                        // In this app, dynamically set the picture source, Camera or photo gallery
                        sourceType: srcType,
                        encodingType: Camera.EncodingType.JPEG,
                        mediaType: Camera.MediaType.PICTURE,
                        correctOrientation: true  //Corrects Android orientation quirks
                    }
                    return options;
                }

                if (index === 0) {
                    let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
                    let options = setOptions(srcType);
                    navigator.camera.getPicture(function cameraSuccess(imageUri) {
                        that.fileUpload(imageUri)

                    }, function cameraError(error) {
                        console.debug("Unable to obtain picture: " + error, "app");

                    }, options);
                }
                if (index === 1) {
                    let srcType = Camera.PictureSourceType.CAMERA;//拍照
                    let options = setOptions(srcType);
                    navigator.camera.getPicture(function cameraSuccess(imageUri) {
                        that.fileUpload(imageUri)

                    }, function cameraError(error) {
                        console.debug("Unable to obtain picture: " + error, "app");

                    }, options);
                }
            },
            // 使用cordova FileUpload上传图片
            fileUpload: function (imageURI) {

                function getCaption(obj, state) {
                    let index = obj.lastIndexOf("?");
                    if (index > 0) {
                        if (state == 0) {
                            obj = obj.substring(0, index);
                        } else {
                            obj = obj.substring(index + 1, obj.length);
                        }
                        return obj;
                    } else {
                        return obj;
                    }
                }

                imageURI = getCaption(imageURI, 0)

                let that = this
                that.loading = true
                let FileUploadOptions = window.FileUploadOptions
                let FileTransfer = window.FileTransfer
                let options = new FileUploadOptions()
                options.fileKey = 'file'
                let path = imageURI.substr(imageURI.lastIndexOf('/') + 1);
                options.fileName = path
                options.mimeType = 'image/jpeg'
                let headers = {'Authorization': ' ' + this.ssk}
                options.headers = headers;
                let ft = new FileTransfer()
                
                ft.upload(imageURI, encodeURI(ossUrl.PublicUpload), async function (data) {

                    function wait(ms) {
                        return new Promise(resolve => setTimeout(() => resolve(), ms));
                    };

                    let resString = data.response
                    let resObject = JSON.parse(resString) // 字符串转对象
                    if (resObject.iRspCode === 0) {
                        that.sAvatar = resObject.data.vUrlPath[0];
                        //alert(that.sAvatar)
                        that.setInfo(resObject.data.vUrlPath[0]||'')
                    } else {
                        //that.$toast(resObject.msg)
                        that.$toast.fail('上传出错稍后重试！')
                        that.loading = false
                    }
                }, function (err) {
                    console.log(err)
                    this.loading = false
                    this.$toast.fail('上传出错稍后重试！')
                }, options)
            },
            setInfo(url) {
                //alert(JSON.stringify({'sAvatar': url}))
                chgUserExten({sAvatar: url})
                    .then(() => {
                        this.$toast('修改成功！');
                        this.GetUserExten()
                        this.loading = false
                    })
            }
        }
    }
</script>
