<template>
    <div>
        <router-view/>
<!--        <van-nav-bar-->
<!--            title="安全设置"-->
<!--            left-text=""-->
<!--            class="header_title"-->
<!--            @click-left="onClickLeft"-->
<!--            safe-area-inset-top-->
<!--            placeholder-->
<!--            left-arrow-->
<!--            :fixed="fixed"-->
<!--        />-->
        <div class="user-conts">
            <van-cell-group>
                <van-cell title="手机号" :value="cellphone()| phone">
                </van-cell>
                <van-cell v-if="isHasEmail()" title="邮箱" :value="email()|email" isLink @click="setEmail(10)">
                </van-cell>
                <van-cell v-else title="邮箱" isLink>
                    <template #right-icon>
                        <a class="link-color" @click="setEmail(7)">绑定邮箱</a>
                    </template>
                </van-cell>
                <van-cell title="登录密码" value="修改密码" is-link @click="goEditPsw"></van-cell>
                <van-cell title="资金密码" :value="iDrawalStatus() === 1 ? '重置' : '设置'" is-link @click="goEditDrawaPsw"></van-cell>
                <van-cell title="收款银行卡管理" value="" is-link @click="goPayList"></van-cell>
<!--                <van-cell v-if="iCertificateStatus() === 3 " title="实名认证" value="已驳回(重新认证)" is-link @click="goAuthCode">-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iCertificateStatus() === 0" title="实名认证">-->
<!--                    <template #right-icon>-->
<!--                        <a class="link-color" @click="goAuthCode">去认证</a>-->
<!--                    </template>-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iCertificateStatus() === 1" title="实名认证" value="审核中" is-link @click="goAuthSuccess">-->
<!--                </van-cell>-->
<!--                 &lt;!&ndash; is-link @click="goAuth" &ndash;&gt;-->
<!--                <van-cell v-if="iCertificateStatus() === 2" title="实名认证">-->
<!--                    <template #right-icon>-->
<!--                        <div class="pass-style"><van-icon name="passed" />已认证</div>-->
<!--                    </template>-->
<!--                </van-cell>-->

<!--                <van-cell v-if="iCertificateStatus() === 2&&(iLiveCertificateStatus() === 0 || iLiveCertificateStatus() === 3)" title="高级认证">-->
<!--                    <template #right-icon>-->
<!--                        <a class="link-color" @click="goLiveAuth">去认证</a>-->
<!--                    </template>-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iCertificateStatus() === 2&&(iLiveCertificateStatus() === 1)" title="高级认证" value="审核中" is-link  @click="goLiveAuth">-->
<!--                </van-cell>-->
<!--                  &lt;!&ndash; value="已认证" is-link @click="goLiveAuth" &ndash;&gt;-->
<!--                <van-cell v-if="iCertificateStatus() === 2&&(iLiveCertificateStatus() === 2)" title="高级认证">-->
<!--                    <template #right-icon>-->
<!--                        <div class="pass-style"><van-icon name="passed" />已认证</div>-->
<!--                    </template>-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iLiveCertificateStatus() === 2&&(iAdsStatus() === 0 ||iAdsStatus() === 3)" title="广告认证">-->
<!--                    <template #right-icon>-->
<!--                        <a class="link-color" @click="goAdsAuth">去认证</a>-->
<!--                    </template>-->
<!--                </van-cell>-->
              <van-cell v-if="iAdsStatus() === 3 " title="承兑商认证" value="已驳回(重新认证)" is-link @click="goAdsAuth"></van-cell>
                <van-cell v-if="iAdsStatus() === 0 " title="承兑商认证">
                    <template #right-icon>
                        <a class="link-color" @click="goAdsAuth">去认证</a>
                    </template>
                </van-cell>
                <van-cell v-if="iAdsStatus() === 1" title="承兑商认证" value="审核中">
                </van-cell>
                <!-- is-link @click="goAdsAuth" -->
                <van-cell v-if="iAdsStatus() === 2" title="承兑商认证">
                    <template #right-icon>
                        <div class="pass-style"><van-icon name="passed" />已认证</div>
                    </template>
                </van-cell>

            </van-cell-group>
        </div>
        <!-- <van-dialog
                v-model="show"
                title="设置昵称"
                show-cancel-button
                :beforeClose="closeDialog"
        >
            <van-field v-model="message" :label="label" placeholder="请输入"/>
        </van-dialog> -->
        <!--设置昵称-->
        <van-popup v-model="show"
            :closeable="false"
            round
            title="设置昵称"
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            position="bottom"
            :style="{ height: '35%', }"
        >   
            <div class="popup-title-hed">设置昵称</div>
            <van-field
                class="nk-icon"
                v-model="message"
                center
                clearable
                label=""
                type="text"
                :maxlength="10"
                :left-icon="require('../../assets/img/nk-icon.png')"
                placeholder="请输入昵称"
            >
            </van-field>
            <div style="margin: 36px 16px; 0 16px">
                <van-button style="width:47%;display: inline-block;background: #FFFFFF;border: 1px solid #C8CBCE;color: #919DAF;" round block type="info" @click="closes">
                    取消
                </van-button>
                <van-button style="width:47%;display: inline-block; margin-left:.4rem;" round block type="info" @click="closeDialog">
                    确认
                </van-button>
            </div>
        </van-popup>

        <!-- 修改密码 -->
        <van-dialog v-model="showPsw" title="修改密码" confirmButtonColor="#3D7EFF" show-cancel-button :beforeClose="editPsw">
            <van-field
                v-model="password"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label="原密码"
                placeholder="请输入原密码"
            >
            </van-field>
            <van-field
                v-model="newpassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label="新密码"
                placeholder="请输入新密码"
            >
            </van-field>
            <van-field
                v-model="repassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label="确认新密码"
                placeholder="请再次输入新密码"
            >
            </van-field>
        </van-dialog>
        <!-- 设置资金密码 -->
        <van-dialog v-model="drawalShow" :title="(this.iDrawalStatus() === 1 ? '重置' : '设置') +'资金密码'" confirmButtonColor="#3D7EFF" show-cancel-button :beforeClose="drawalPsw">
            <van-field
                v-model="drawal.sDrawalPassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label="资金密码"
                placeholder="请输入资金密码"
            >
            </van-field>
            <van-field
                v-model="drawal.sDpassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label="确认密码"
                placeholder="请再次输入资金密码"
            >
            </van-field>
        </van-dialog>
        <email-modal ref="emailModal"/>
        <!--安全验证-->
        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi='iVerifyBusi'
            @ok="applyVerify"
        />
        <!--初级认证-->
        <van-action-sheet
            v-model="goAuthShow"
            :actions="actions"
            cancel-text="取消"
            close-on-click-action
            :close-on-click-overlay="false"
            @select="goAuth"
            @cancel="goAuthShow=false"
        />
    </div>
</template>

<script>
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {changePassword, emailVerify, sendEmailVerifyCode} from '@/api/auth'
    import {resetDrawalPassword} from '@/api/udc'
    import SafeVerifyModal from "@/components/SafeVerifyModal/index";
    import EmailModal from "@/views/my/user_setting_email";
    import {regPwd} from "@/utils/validate"
    import {getAdsConfigList, robShareOrder} from "@/api/otc";

    export default {
        name: 'user-setting',
        components: {
            [Toast.name]: Toast,
            SafeVerifyModal,
            EmailModal,
        },
        data() {
            return {
                fixed: true,
                message: '',
                show: false,
                showPsw: false,
                showEmail: false,
                getEmailCodeFlag: true,
                label: '',
                password: '',
                emailCode: '',
                iVerifyBusi: 3,
                newpassword: '',
                repassword: '',
                action: null,
                emailCountTime: 60,
                goAuthShow: false,
                actions: [{ name: '使用身份证进行验证' }, { name: '使用护照进行验证' }],
                drawalShow: false,
                drawal:{
                    sDrawalPassword: '',
                    sDpassword: ''
                },
                pwdType: '' // 密码，资金密码
            }
        },
        mounted() {

        },
        created() {
            
        },
        methods: {
            ...mapGetters(['iDrawalStatus','iAdsStatus','iLiveCertificateStatus','iCertificateStatus', 'isHasEmail', 'isLogin', 'cellphone', 'email', 'userName', 'iIsChangeNameStatus', 'switchEmailVerify', 'switchCellphoneVerify', 'switchGoogleVerify','isHasGoogleSecretKey']),
            ...mapActions(['GetUserInfo','GetUserExten']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            closes(){
                this.show = false
                this.message = ''
            },
            closeDialog() {
                // if (action === 'cancel') {//取消按钮
                //     this.message = '';
                //     done();
                // } else if (action === 'confirm') {//确定按钮
                //     if (this.message == '' || this.message == null || this.message == undefined) {
                //         this.$toast("请输入内容");
                //         done(false)
                //     } else {
                //         this.action();
                //         this.show = false;
                //         done();
                //     }
                // }
                if (this.message == '' || this.message == null || this.message == undefined) {
                    this.$toast("请输入用户昵称");
                } else {
                    this.action();
                    this.show = false;
                }
            },
            goEditPsw() {
                this.pwdType = 1
                this.showPsw = true;
                this.iVerifyBusi = 3;
            },
            goEditDrawaPsw(){
                this.pwdType = 2
                this.drawal = {
                    sDrawalPassword: '',
                    sDpassword: ''
                }
                this.drawalShow = true;
                this.iVerifyBusi = this.iDrawalStatus() === 1 ?	19 :18;
            },
            goPayList(){
                if(this.iAdsStatus() !==2){
                    this.$message.warning('请先进行承兑商认证！')
                    return
                }
                if(this.iDrawalStatus() !==1){
                  this.$message.warning('请先设置资金密码！')
                  return
                }
                this.$router.push('/pay/list')
            },
            editPsw(action, done) {
                if (action === 'cancel') {//取消按钮
                    done();
                    this.password = ''
                    this.newpassword = ''
                    this.repassword = ''
                } else if (action === 'confirm') {//确定按钮
                    //向后端传值并关闭dialog弹出框
                    done(false);
                    this.showPsw = true;
                    if (!this.password) {
                        this.$toast('请输入原密码！');
                        return
                    }
                    if (!this.newpassword) {
                        this.$toast('请输入新密码！');
                        return
                    }
                    if(!regPwd(this.newpassword)){
                        this.$toast('密码需包含数字和大小写字母中至少两种字符的8-10位字符');
                        return
                    }
                    if (!this.repassword) {
                        this.$toast('请再次输入新密码！');
                        return
                    }
                    if (this.newpassword !== this.repassword) {
                        this.$toast('请输入两次相同的密码！');
                        return
                    }
                    this.$refs.safeVerifyModal.visible = true
                }
            },
            drawalPsw(action, done){
                if (action === 'cancel') {//取消按钮
                    done();
                    this.drawal = {
                        sDrawalPassword: '',
                        sDpassword: ''
                    }
                } else if (action === 'confirm') {//确定按钮
                    //向后端传值并关闭dialog弹出框
                    done(false);
                    if (!this.drawal.sDrawalPassword) {
                        this.$toast('请输入资金密码！');
                        return
                    }
                    if(!regPwd(this.drawal.sDrawalPassword)){
                        this.$toast('密码需包含数字和大小写字母中至少两种字符的8-10位字符');
                        return
                    }
                    if (!this.drawal.sDpassword) {
                        this.$toast('请再次输入新密码！');
                        return
                    }
                    if (this.drawal.sDrawalPassword !== this.drawal.sDpassword) {
                        this.$toast('两次密码不一致！');
                        return
                    }
                    this.$refs.safeVerifyModal.visible = true
                }
            },
            setEmail(iVerifyBusi) {
                this.$refs.emailModal.iVerifyBusi = iVerifyBusi
                this.$refs.emailModal.visible = true
            },
            applyVerify(codes) {
                if(this.pwdType==1){
                    const params = {
                        iSource: 2,
                        sPassword: this.password,
                        sNewPassword: this.newpassword,
                    }
                    if (this.switchEmailVerify()) params.sEmailVerifyCode = codes.sEmailVerifyCode
                    if (this.switchCellphoneVerify()) params.sSMSVerifyCode = codes.sSMSVerifyCode
                    if (this.switchGoogleVerify()) params.sGoogleVerifyCode = codes.sGoogleVerifyCode
                    
                    changePassword(params)
                        .then(() => {
                            this.$toast('密码修改成功！')
                            this.showPsw = false
                            this.GetUserInfo()
                            this.$refs.safeVerifyModal.visible = false
                        })
                }else{
                    const params ={
                        iSource:1,
                        sDrawalPassword:this.drawal.sDrawalPassword,
                    }
                    if (this.switchEmailVerify()) params.sEmailVerifyCode = codes.sEmailVerifyCode
                    if (this.switchCellphoneVerify()) params.sSMSVerifyCode = codes.sSMSVerifyCode
                    if (this.switchGoogleVerify()) params.sGoogleVerifyCode = codes.sGoogleVerifyCode
                    // 设置资金密码
                    resetDrawalPassword(params)
                    .then(()=>{
                        this.$message.success((this.iDrawalStatus() === 1 ? '重置' : '设置') +'成功！')
                        this.drawalShow = false
                        this.$refs.safeVerifyModal.visible = false
                        this.GetUserExten()
                    })
                }
            },
            goAuthCode(){
                if(this.iIsChangeNameStatus()===0){
                    this.$toast('请先设置昵称！')
                    this.$router.push('/my/setting')
                    return
                }else if(!this.isHasEmail()){
                    this.$toast('请先绑定邮箱！')
                    return
                }
                this.goAuthShow = true
            },
            goAuth(action,index){
                this.$router.push({name:index==0?"authsetup":"authsetupf"});
            },
            goLiveAuth(){
                // if(this.email() ===''){
                //     this.$toast('请先绑定邮箱！')
                //     return
                // }else if(this.iCertificateStatus() != 2){
                //     this.$toast('请先完成初级认证！')
                //     return
                // }else{
                //     this.$router.push({name:"authlive",query:{type: '2'}});
                // }
                this.$router.push({name:"authlive",query:{type: '2'}});
            },
            goAdsAuth(){
              if(this.iIsChangeNameStatus()===0){
                this.$toast('请先设置昵称！')
                this.$router.push('/my/setting')
                return
              }else if(!this.isHasEmail()){
                this.$toast('请先绑定邮箱！')
                return
              }
                // if(this.email() ===''){
                //     this.$toast('请先绑定邮箱！')
                //     return
                // }else if(this.iCertificateStatus() != 2){
                //     this.$toast('请先完成初级认证！')
                //     return
                // }else if(this.iLiveCertificateStatus() != 2){
                //     this.$toast('请先完成高级认证！')
                //     return
                // }else{
                //     this.$router.push({name:"authads",query:{type: '3'}});
                // }
              getAdsConfigList({iIdle: 0})
                  .then(res => {
                    // debugger
                    const tmp = res.data.vList.length > 0
                        ? Number(res.data.vList.find(i => i.iLevel === 1).iLowerDeposit)
                        : 0
                    this.$dialog.confirm({
                      title:'保证金确认',
                      message: '进行承兑商认证需要冻结 '+tmp+'USDT 作为保证金，请确保您的账户资产足够！',
                      messageAlign: 'left',
                      confirmButtonText:'立即认证',
                      confirmButtonColor: '#3D7EFF',
                      cancelButtonText:'等等再去'
                    }).then(() =>{
                      this.$router.push({name:"authads",query:{type: '3'}});
                    })
                  })
            },
            goAuthSuccess(){
                this.$router.push({name:"authsetupsucess",query:{message: '审核中',decs:''}});
            },
        }
    };

</script>

<style lang="less" scoped>
    .user-conts{
        //padding-top: 46px;
    }
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    /deep/.nk-icon .van-icon{
        font-size: .42rem;
        margin-top: -6px;
        margin-right: .06rem;
        .van-icon__image{
            height: auto;
        }
    }
    .-flat-button{
        height: auto !important;
        line-height: normal !important;
        min-width: auto !important;
    }
    .avatar-box{
        justify-content: center;
        align-items: center;
    }
    .popup-title-hed{
        font-size: .32rem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #000000;
        margin: .32rem 0 .24rem .42rem;
    }
    .pass-style{
        color: seagreen;
        i{
            margin-top: 3px;
            font-size: 16px;
            position: relative;
            top: 4px;
            margin-right: 6px;
        }
    }
</style>
