  padding-top: .5rem;
  background: #fff;
  .title{
    font-size: .24rem;
    line-height: 18px;
    //margin-left: .5rem;
    text-align: center;
    color:gray;
  }
  .conts{
    font-size: .24rem;
    line-height: 18px;
    p{
      //margin-left: .7rem;
      //margin-right: .7rem;
      margin: auto;
      margin-bottom: .1rem;
      margin-top: .2rem;
    }
    text-align: center;
  }
  .copys{
    background-color: #f8f8f8;
    //border: 1px solid #C8CBCE;
    border-radius: 2px;
    font-family: Adobe Heiti Std;
    font-weight: normal;
    color: gray;
    padding: 6px .3rem;
    margin: .32rem 0;
    display: inline-block;
    font-size: 13px;
  }
}
.code-box{
  width: 164px;
  height: 164px;
  border-radius: 0px;
  margin: 0 auto;
