                    {{' ≈ ' +cnyAmount.toFixed(2)}}<i class="font12" style="font-size:.18"> CNY</i>
                </div>
                <div class="bnt-box">
                    <button class="bal-bnt" @click="goPage(null,'balancedeposit')">充币</button>
                    <button class="bal-bnt wm-bal-bnt" @click="goPage(null,'balancewithdraw')">提币</button>
                </div>
            </div>
        </div>
        <van-pull-refresh v-if="tokens.length>0" class="contents" v-model="refreshing" style="background:#fff;" @refresh="refreshData">
