<!--              </van-cell>-->
<!--              <van-cell title="可用余额" :value="Number(item.iAvailableAmount) +' USDT'"></van-cell>-->
<!--              <van-cell title="交易冻结中" :value="Number(item.iFrozenAmount) - iDepositAmount()+' USDT'"></van-cell>-->
<!--&lt;!&ndash;              <van-cell title="法币估值(CNY)" :value="item.sToken ==='USDT' ? depositCnyAmount(item) : Number(item.iFiat).toFixed(2)"/>&ndash;&gt;-->
