                  <span class="sp-color">USDT 账户</span>
                </template>
                <van-icon name="balance-list-o" style="font-size: 20px;line-height: 24px"/>
              </van-cell>
              <van-cell title="可用余额" :value="Number(tokens[0].iAvailableAmount) +' USDT'"></van-cell>
              <van-cell title="交易冻结中" :value="Number(tokens[0].iFrozenAmount) - iDepositAmount()+' USDT'"></van-cell>
              <van-cell title="保证金冻结" :value="Number(iDepositAmount())+' USDT'"></van-cell>
            </div>
          </div>
          <div class="fin-warp" >
