                <van-icon name="balance-list-o" style="font-size: 20px;line-height: 24px"/>
              </van-cell>
              <van-cell title="总佣金" :value="commissionAmount +' USDT'"></van-cell>
              <van-cell title="已提现" :value="(Number(commissionAmount) - Number(tokens[1].iAvailableAmount) - Number(tokens[1].iFrozenAmount)).toFixed(8) +  ' USDT'"></van-cell>
              <van-cell title="待提现" :value="Number(tokens[1].iAvailableAmount) + ' USDT'"></van-cell>
              <van-cell title="提现中" :value="Number(tokens[1].iFrozenAmount) + ' USDT'"></van-cell>
              <van-cell title="" >
                <van-button size="mini" type="info" @click="$router.push({name:'balancewithdraw',query:{iAcctType:1}});"> 佣 金 提 现 </van-button>
