        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi = '17'
            @ok="applyVerify"
        />
    </div>
</template>

<script>
    import Vue from 'vue'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {getMerchantTokens} from '@/api/wallet'
    import {udcUrl,getUserBalance} from '@/api/udc'
    import {getUserOrderStat} from '@/api/otc'
    import SafeVerifyModal from "@/components/SafeVerifyModal"

    import bg_default from '../../assets/img/ic_asset_bg.png';
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import moment from 'dayjs'

    export default {
        name: 'user-balance',
        mixins: [GpayListMixin],
        components: {
            [Toast.name]: Toast,
            SafeVerifyModal
        },
        data() {
            return {
                balances:[],
                background_image: bg_default,
                type:0,
                fixed:true,
                merchantTokens:[],
                queryParam:{
                  iAcctType: -1
                },
                resultName:'vBalance',
                url: {
                    list: udcUrl.GetUserBalance,
                },
                openSafeVerifyType:'',
                iVerifyBusi:null,
                needCellphone:2,
                needEmail:2,
                needGoogle:2,
                getFiatQuoteInterval:null,
                commissionAmount:0
            }
        },
        mounted() {

        },
        created() {
            getMerchantTokens({iIdle:0})
                .then((res)=>{
                    this.merchantTokens = res.data.vTokens
                    // debugger
                })
            getUserBalance({iAcctType: -1})
            .then((res)=>{
                this.balances = res.data.vBalance
            })
            getUserOrderStat({iUserId:this.uid()})
              .then(res=>{
                this.commissionAmount = res.data.stStat.dTotalRewardAmount || 0
                // debugger
              })
            this.GetFiatQuote('BTC')
            this.getFiatQuoteInterval = setInterval(()=>{
                this.GetFiatQuote('BTC')
            },60000)
            this.initParam()
            this.refreshData();

        },
        beforeDestroy(){
            clearInterval(this.getFiatQuoteInterval)
        },
        computed:{
            amount(){
                const fiatQuote = this.fiatQuote()
                let btcAmount =0
                let cnyAmount =0
                let usdtAmount =0
                if(fiatQuote && this.dataSource.length >0){
                    // debugger
                    const valueCNY = fiatQuote.find(item=>item.sName ==='Chinese Yuan').dValue
                    this.dataSource.forEach(item=>{
                    if(item.sToken !=='USDT'){
                        const value = fiatQuote.find(i=>item.sToken === i.sUnit).dValue
                        const btcValue = (Number(item.iAvailableAmount)  + Number(item.iFrozenAmount)) / value
                        item.iFiat =btcValue * valueCNY
                        btcAmount +=btcValue
                        cnyAmount +=item.iFiat
                    }else{
                        const value = fiatQuote.find(i=>i.sName === 'US Dollar').dValue
                        const btcValue = (Number(item.iAvailableAmount)  + Number(item.iFrozenAmount))/ value
                        item.iFiat = btcValue * valueCNY
                        btcAmount +=btcValue
                        cnyAmount +=item.iFiat
                    }
                    })
                    return {btcAmount,cnyAmount,usdtAmount}
                }else{
                    return {btcAmount,cnyAmount,usdtAmount}
                }
            },
            usdtAmount(){
              let usdtAmount =0
                if(this.dataSource.length >0){
                  const index = this.dataSource.findIndex(item=> item.sToken ==='USDT')
                  usdtAmount = Number(this.dataSource[index].iAvailableAmount)  + Number(this.dataSource[index].iFrozenAmount) - Number(this.iDepositAmount())
                }
                return usdtAmount
            },
            cnyAmount(){
              const usdtPrice = this.getMarketdPrice()('USDT') || 0
              return this.usdtAmount * usdtPrice
            },
            tokens(){
              const arr = []
              this.dataSource.forEach(item=>{
                if(item.sToken ==='USDT') {
                  arr.push({
                    ...item
                  })
                }
              })
              return arr.sort((a,b) => a.iAcctType-b.iAcctType)
            }
        },
        methods: {
            moment,
            ...mapGetters(['isLogin', 'userName', 'cellphone', 'email','fiatQuote','iDepositAmount','getMarketdPrice','uid']),
            ...mapActions(['GetUserExten','GetFiatQuote']),
            initParam() {
                this.queryParam.iIdle=0
            },
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            goPage(item,page){
                if(item){
                    Vue.ls.set("vBalance", item);
                }
                if(page==='balancedeposit'){
                    //充币

                }
                if(page==='balancewithdraw'){
                    //提币

                }

                this.$router.push({name:page});
            },
            canDeposit(token){
                // debugger
                let tmpBoolean = false
                this.merchantTokens.forEach(item=>{
                    // debugger
                    if(item.sToken === token && item.bDepositSwitch)tmpBoolean=true
                })
                return tmpBoolean
            },
            canWithdraw(token){
                let tmpBoolean = false
                this.merchantTokens.forEach(item=>{
                    if(item.sToken === token && item.bWithdrawSwitch)tmpBoolean=true
                })
                return tmpBoolean
            },
            test(){
                this.$refs.safeVerifyModal.visible = true
            },
            applyVerify(){

            },
            depositCnyAmount(item){
                const fiatQuote = this.fiatQuote()
                if(fiatQuote){
                    // debugger
                    const valueCNY = fiatQuote.find(i=>i.sName ==='Chinese Yuan').dValue
                    const value = fiatQuote.find(i=>i.sName === 'US Dollar').dValue
                    const amount =Number(item.iAvailableAmount) + Number(item.iFrozenAmount) - Number(this.iDepositAmount())
                    return (amount * valueCNY / value).toFixed(2)
                }else{
                    return 0.00000000
                }
            }
        }
    };

</script>

