    }
    .fin_list.hed{
        font-size: .24rem;
        margin-top: 10px;
        color: #919DAF;
    }
    .fin_list.numb{
        color: #212833;
        font-size: .3rem;
        font-family: PingFang SC;
        font-weight: 400;
    }
    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }
    .user_avatar {
        margin-bottom: 10px;
        img {
            border: 0;
            border-radius: 50%;
        }
    }
    .balance-list-o{
      line-height: 24px;
      font-size: 20px;
      color:gray
    }
    /deep/ .balance-hed .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
</style>
