<template>
    <div>
        <van-nav-bar
            title="提币地址"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            right-text="添加地址"
            @click-right="showModal"
            left-arrow
            :fixed="true"
        />
        <div class="ads-list-cont">
            <van-empty description="您还没有设置提币地址，请添加！" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length==0&&loadedall" />
            <van-pull-refresh v-else v-model="refreshing" style="min-height: 66vh;" @refresh="refreshData">
                <van-list
                    v-model="loading"
                    :finished="loadedall"
                    finished-text="没有更多了"
                    @load="loadData"
                    :border="true"
                >
                        
                    <div class="warp-box">
                        <div class="ads-list" v-for="(item,index) in dataSource" :key="index">
<!--                            <div class="info-cont gflex mbmt">-->
<!--                                <div style="flex:1">-->
<!--                                    <div class="box__title">币种: <i>{{ item.sToken }}</i></div>-->
<!--                                </div>-->
<!--                                <div class="rbox"><van-button style="width:70px" size="mini" round type="danger" @click="delAddress(item.iId)">删除</van-button>-->
<!--                                </div>-->
<!--                            </div>-->
                          <div class="info-cont gflex mbmt">
<!--                            <div style="width:40px">-->
<!--                              <div class="box__title">备注:</div>-->
<!--                            </div>-->
                            <div style="flex:1;font-size: 14px;font-weight: 600">
                              {{item.sRemark}}
                              <van-tag plain type="primary">{{ mapChain(item.sChain) }}</van-tag>
                              <van-icon name="delete-o" @click="delAddress(item.iId)" style="font-size: 18px;color: #d2d2d2;float: right;line-height: 20px" />
                            </div>
                          </div>
                            <div class="info-cont gflex mbmt">
<!--                                <div style="width:40px">-->
<!--                                    <div class="box__title">地址:</div>-->
<!--                                </div>-->
                                <div style="flex:1;color: #a1a1a1">{{item.sAddress}}</div>
                            </div>

                        </div>
                    </div>
                </van-list>
            </van-pull-refresh>
        </div>
        <add-modal ref="add-modal" @aplOK="setOK" />
    </div>
</template>

<script>
    // import {NavBar} from 'vant';
    import {walletUrl,getWalletAddress,delWalletAddress} from '@/api/wallet'
    import {mapGetters} from 'vuex'
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import AddModal from "@/components/wallet/addModal";

    export default {
        name: "walletAddress",
        mixins: [GpayListMixin],
        components: {
            // [NavBar.name]: NavBar,
            AddModal
        },
        data() {
            return {
                myAdsList: [],
                myAdsParam: {},
                payDictionarys: [],
                iSideType: 1,
                iBlockType: 1,
                activeSCoinCode: 'USDT',
                resultName: 'vAddress',
                queryParam: {
                    iPageNo: 0,
                    iPageSize: 10,
                },
                url: {
                    list: walletUrl.GetWalletAddress,
                }
            };
        },
        created() {
            //查询数据
            //this.initParam();
            //this.refreshData();
        },
        methods: {
            mapChain(chain){
              switch (chain){
                case 'Tron':
                  return 'TRC20'
                case 'Ethereum':
                  return 'ERC20'
                case 'Bitcoin':
                  return 'OMNI'
              }
            },
            setOK(){
                console.log(12)
                this.queryParam = {
                    iPageNo: 0,
                    iPageSize: 10,
                },
                this.refreshData();
            },
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            showModal(type,record){
                this.$refs['add-modal'].init()
                this.$refs['add-modal'].record=record
                this.$refs['add-modal'].type=type
            },
            delAddress(id){
                this.$dialog.confirm({
                    message: '您确定删除该地址吗？',
                    confirmButtonText:'确定',
                    confirmButtonColor: '#3D7EFF',
                    cancelButtonText:'取消'
                }).then(() =>{
                    this.loading = true
                    delWalletAddress({iId:id})
                    .then((res=>{
                        this.$message.success('删除成功！')
                        // this.dataSource = []
                        this.queryParam = {
                            iPageNo: 0,
                            iPageSize: 10,
                        }
                        setTimeout(()=>{
                          this.refreshData();
                        },100)

                    }))
                    .finally(()=>{
                        this.loading =false
                    })
                })
            },
        },
    }
</script>

<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__right .van-nav-bar__text{
            color: @gpay-color3D7EFF;
            background: #ffffff;
            border-radius: 20px;
            padding: 1px 6px;
            font-size: 12px;
        }
    }
    .ads-list{
        background: #fff;
        position: relative;
        padding-bottom: 8px;
        margin-bottom: 10px;
    }
    .ads-list::after {
        position: absolute;
        box-sizing: border-box;
        content: ' ';
        pointer-events: none;
        right: 16px;
        bottom: 0;
        left: 16px;
        border-bottom: 1px solid #ebedf0;
        -webkit-transform: scaleY(.5);
        transform: scaleY(.5);
    }
    .info-top,.info-cont{
        padding: 0 .28rem;
    }
    .border-top{
        //border-top: 1px solid #e4e5e6;
        padding-top: 12px;
    }
    .border-bm{
        //border-bottom: 1px solid #e4e5e6;
        padding-bottom: 12px;
        margin-bottom: .25rem;
        position: relative;
    }
    .gflex{
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        // -webkit-box-align: center;
        // -webkit-align-items: center;
        // align-items: center;
    }
    .box__hd{
        margin-right: 16px;
        width: 42px;
        height: 42px;
        background: #FFFFFF;
        border-radius: 50%;
        .avatar{
            width: 100%;
            border-radius: 50%;
        }
    }
    .box__bd{
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        flex: 1;
        min-width: 0;
    }
    .box__dec{
        font-size: 14px;
        line-height: 1.4;
        padding: 0;
        margin: 0;
        padding-top: 4px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
    }
    .info-cont{
        .box__title{
            font-size: 12px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #919DAF;
            i{
                color: @gpay-color3D7EFF;
                font-size: .3rem;
                font-style: normal;
                font-family: PingFangSC-Regular;
                margin-left: .26rem;
            }
            span{
                color: #212833;
                font-size: 12px;
                margin-left: .26rem;
            }
        }
        .rbox{
            margin-right: 3px;
        }
        .mc-bnt{
            min-width: 120px;
            background: @gpay-bg3D7EFF;
            border-radius: 24px;
            padding: 3px 18px;
            font-size: 14px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #FFFFFF;
            line-height: 36px;
        }
        .pay-type{
            width: 61px;
            img{
                width: 20px;
                margin-right: 8px;
            }
        }
    }
    .mbmt{
        margin-bottom: 6px;
    }
    .qtright{
        float: right;
        margin-right: 20px;
    }
    /deep/ .van-tabs__nav{
        background: #F3F5FD;
    }
    /deep/.van-tabs__line{
        display: none;
    }
    /deep/ .van-tabs--line .van-tabs__wrap{
        padding-top: .3rem;
        height: auto;
    }
    /deep/ .van-tab--active{
        color: @gpay-color3D7EFF;
    }
    .ads-list-cont{
        //background: #F3F5FD;
        //padding-top: .3rem;
        padding-bottom: 1.1rem;
        top: 1.214rem;
        position: relative;
    }
    .ads-two-mun{
        color: #212833;
        padding-top: 2px;
        background: #F3F5FD;
    }
    /deep/ .ads-two-mun{
        .van-tab{
            //color: #FEFEFE;
            color: #212833;
            font-size: .24rem;
        }
        .van-tab--disabled{
            color: #c8c9cc;
        }
    }
    /deep/.ads-two-mun .van-tab--active{
        span{
            font-family: PingFang SC;
            font-weight: 400;
            color: #fff;
            background: @gpay-bg3D7EFF;
            border-radius: 15px;
            padding: 1px 14px;
        }
    }
    /deep/ .ads-nav-name .van-tab{
        font-size: .3rem;
        line-height: normal;
    }
</style>
