<script>
import {Form,Input,Button,Row,Col,Spin} from 'ant-design-vue'
import {getSMSVerifyCode, sendEmailVerifyCode} from "@/api/auth";
import {mapGetters,mapActions} from 'vuex'
import {Toast} from 'vant';
export default {

  name: "SafeVerifyForm",
  components:{
    // [Form.name]:Form,
    // [Input.name]:Input,
    // [Button.name]:Button,
    // [Row.name]:Row,
    // [Col.name]:Col,
    [Toast.name]:Toast,
  },
  props:{
    iVerifyBusi:Number,
    needCellphone:Number,
    needEmail:Number,
    needGoogle:Number,
    needType:Boolean,
    phoneVal:String,
    emailVal:String,
    areaVal:String
  },
  data(){
    return {
      getCaptchable:true,
      getEmailCodeFlag: true,
      loading:false,
      cellphoneCode:null,
      emailCode:null,
      googleCode:null,
      countTime:60,
      emailCountTime: 60,
      errTips: ''
    }
  },
  created(){
    this.loading=true
    this.cellphoneCode= null;
    this.emailCode = null;
    this.googleCode = null;
    this.errTips = ''
    if(this.needType){
      this.loading=true
      this.GetUserInfo()
      .finally(()=>{this.loading=false})
    }

    // this.GetUserInfo()
    // .finally(()=>{this.loading=false})
  },
  computed:{
    showCellphone(){
      switch (this.needCellphone){
        case 0:
          return false
        case 1:
          return true
        case 2:
          return this.switchCellphoneVerify()
      }
    },
    showEmail(){
      // debugger
      switch (this.needEmail){
        case 0:
          return false
        case 1:
          return true
        case 2:
          return this.switchEmailVerify()
      }
    },
    showGoogle(){
      switch (this.needGoogle){
        case 0:
          return false
        case 1:
          return true
        case 2:
          return this.switchGoogleVerify()
      }
    },
  },
  methods:{
    ...mapGetters(['switchCellphoneVerify','isHasGoogleSecretKey','switchEmailVerify','switchGoogleVerify','cellphone','sSMSAreaCode','email']),
    ...mapActions(['GetUserInfo']),
    getEmailCode(){
      const params  = {
        iSource:2,
        iSendMethod:2,
        iVerifyBusi:this.iVerifyBusi,
        sEmail:this.needType?this.email():this.emailVal
      }
      sendEmailVerifyCode(params)
          .then(()=>{
            Toast('邮箱验证码已发送!')
            this.getEmailCodeFlag = false
            this.emailCaptchaInterval = setInterval(()=>{this.countEmail()},1000)
          })
    },
    getCellphoneCode(){
      const params={
        iSource:2,
        iSendMethod:2,
        iVerifyBusi:this.iVerifyBusi,
        sSMSAreaCode:this.needType?this.sSMSAreaCode():this.areaVal,
        sCellphone:this.needType?this.cellphone():this.phoneVal
      }
      getSMSVerifyCode(params)
          .then(()=>{
            Toast('短信验证码已发送!')
            this.getCaptchable = false
            this.captchaInterval = setInterval(()=>{this.countPhone()},1000)
          })
    },
    countPhone(){
      if(this.countTime <= 0){
        clearInterval(this.captchaInterval)
        this.countTime = 60
        this.getCaptchable = true
      }else{
        this.countTime -= 1
      }
    },
    countEmail(){
      if(this.emailCountTime <= 0){
        clearInterval(this.emailCaptchaInterval)
        this.emailCountTime = 60
        this.getEmailCodeFlag = true
      }else{
        this.emailCountTime -= 1
      }
    },
    apply(){
      //this.form.validateFields(['cellphoneCode'])
      if(!this.cellphoneCode&&this.showCellphone){
        this.errTips = '请输入短信验证码'
        return
      }
      if(!this.emailCode&&this.showEmail){
        this.errTips = '请输入邮箱验证码'
        return
      }
      if(!this.googleCode&&this.showGoogle){
        this.errTips = '请输入谷歌验证码'
        return
      }
      this.errTips = ''
      this.$emit('ok',{
        sSMSVerifyCode:this.cellphoneCode,
        sEmailVerifyCode:this.emailCode,
        sGoogleVerifyCode:this.googleCode,
      })
      //this.childClear()
      this.loading = false
    },
    childClear(){
      this.cellphoneCode = null
      this.emailCode = null
      this.googleCode = null

      this.countTime = 60,
      this.emailCountTime = 60,
      this.errTips = ''

      this.getCaptchable = true
      this.getEmailCodeFlag = true
      
      clearInterval(this.captchaInterval)
      clearInterval(this.emailCaptchaInterval)
    }
  }
}
</script>

