<template>
    <div>
        <van-nav-bar
            title="站内信"
            left-text=""
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :right-text="'全部已读('+mailUnreadCount()+')'"
            @click-right="onClickRight"
            :fixed="true"
        />
        <div class="msg-list-cont">
            <van-empty description="没有数据" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length==0&&loadedall" />
            <van-pull-refresh v-else v-model="refreshing" style="min-height: 66vh;" @refresh="refreshData">
                <van-list
                    v-model="loading"
                    :finished="loadedall"
                    finished-text="没有更多了"
                    @load="loadData"
                >
                    
                    <div class="warp-box">
                        <div class="msg-list" v-for="msg in dataSource" :key="msg.iId" @click="handMsg(msg)">
                            <div class="time"><span>{{ msg.iCreateTime | time }}</span></div>
                            <van-badge :dot="msg.iStatus==0">
                                <van-cell-group :border="false">
                                     <!-- :label="msg.sContent" -->
                                    <van-cell class="flex-tab" :border="false" :title="msg.sTitle" value=""></van-cell>
                                </van-cell-group>
                                <van-cell title="了解详情" :border="false" class="msgdl">
                                    <template #right-icon>
                                        <van-icon name="arrow" class="search-icon" />
                                    </template>
                                </van-cell>
                            </van-badge>
                        </div>
                    </div>
                </van-list>
            </van-pull-refresh>
        </div>
        <van-popup
            v-model="msgShow"
            closeable
            position="bottom"
            :close-on-popstate="true"
            :style="{ height: '50%' }"
        >
        <template>
            <div class="msg-detail">
                <h2>{{msgDetail.sTitle}}</h2>
                <span>{{msgDetail.iCreateTime | time}}</span>
                <div class="dec">{{msgDetail.sContent}}</div>
            </div>
        </template>
        </van-popup>
    </div>
</template>

<script>
    import {udcUrl,readUserMail,readUserAllMail} from '@/api/udc'
    import {mapActions,mapGetters} from 'vuex'
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import moment from 'dayjs'

    export default {
        name: "MsgList",
        mixins: [GpayListMixin],
        components: {
        },
        data() {
            return {
                resultName: 'vMail',
                queryParam: {
                    iPageNo: 0,
                    iPageSize: 10,
                },
                msgShow: false,
                msgDetail:{},
                url: {
                    list: udcUrl.GetUserMailList,
                }
            };
        },
        created() {
            //查询数据
            this.refreshData();
            this.GetMailUnreadCount()
        },
        methods: {
            ...mapActions(['GetMailUnreadCount']),
            ...mapGetters(['mailUnreadCount']),
            handMsg(item){
                this.msgShow = true
                this.msgDetail = item 
            },
            clearMsg(){
                let item = this.msgDetail
                if(item.iStatus==1)return
                readUserMail({vIdList:[item.iId]})
                .then(res=>{
                    //this.$message.success('消息标记为已读!')
                    this.GetMailUnreadCount()
                    this.refreshData();
                })
            },
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            onClickRight(){
                if(this.mailUnreadCount() <=0) return
                readUserAllMail({iIdle:0})
                .then(res=>{
                    this.$message.success('消息标记为已读!')
                    this.GetMailUnreadCount()
                    this.refreshData();
                })
            }
        },
        watch:{
            msgShow(nv,ov){
                if(nv){
                    setTimeout(() =>{
                        this.clearMsg()
                    },500)
                }
            }
        },
        filters:{
            time(v){
                return moment(v).format('YYYY-MM-DD HH:mm:ss')
            },
        }
    }
</script>

<style lang="less" scoped>
    .msg-list-cont{
        padding-bottom: 1rem;
        padding-top: 12px;
    }
    .warp-box{
        ul,li,p{
            padding: 0;
            margin: 0;
        }
    }
    .warp-box .flex-tab{
        display: inherit;
        padding-top: 0;
        padding-bottom: 0;
        padding:0;
    }
    /deep/ .van-badge--fixed{
        right: 14px;
        top: 14px;
    }
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__right .van-nav-bar__text{
            color: #e4e3e3;
        }
    }
    /deep/.nk-icon .van-icon{
        font-size: .42rem;
        margin-top: -6px;
        margin-right: .06rem;
        .van-icon__image{
            height: auto;
        }
    }
    /deep/ .van-nav-bar__right{
        font-size: 13px;
    }
    /deep/.van-cell__title{
        span{
            font-weight: bold;
        }
    }
    .van-badge__wrapper{
        padding: 10px 15px;
        padding-bottom: 6px;
        box-shadow: 0px 3px 5px 1px rgba(61, 126, 255, 0.2);
        border-radius: 6px;
        margin: 5px 10px;
        width: 95%;
    }
    .msg-list{
        .time{
            text-align: center;
            font-size: 13px;
            margin: 10px 0;
            span{
                color: #9cadd0;
                background: #f1f1f1;
                border-radius: 3px;
                padding: 2px 4px;
            }
        }
    }
    .msgdl .van-cell__title >span{
        color: #969799;
        font-weight: normal;
        font-size: 12px;
    }
    .msgdl.van-cell{
        padding: 0;
        padding-top: 16px;
    }
    .msgdl.van-cell::before{
        position: absolute;
        box-sizing: border-box;
        content: ' ';
        pointer-events: none;
        right: 0;
        top: 8px;
        left: 0;
        border-bottom: 1px solid #ebedf0;
        -webkit-transform: scaleY(.5);
        transform: scaleY(.5);
    }
    .msgdl .van-icon-arrow{
        color: #969799;
        top: 6px;
    }
    .msg-detail{
        padding: 26px 12px;
        h2{
            font-size: 18px;
            margin: 0;
            font-weight: bold;
        }
        span{
            color: #969799;
            font-size: 12px;
        }
        .dec{
            margin: 20px 0;
        }
    }
</style>
