<template>
    <div style="background: #fff;padding-bottom: .6rem;">
        <!-- <router-view /> -->
        <van-nav-bar
            title="充币"
            left-text=""
            @click-left="onClickLeft"
            :fixed="fixed"
            left-arrow
            safe-area-inset-top
            placeholder
        />
        <!-- <van-cell :title="selToken.sToken" value="选择币种" is-link style="margin-top:.4rem;" @click="show = true"/> -->
        <van-row class="card-box" type="flex" >
<!--            <van-col class="detail-info">选择币种</van-col>-->
<!--            <div class="r-cont bor-style">-->
<!--                <span class="custom-title">{{selToken.sToken}}</span>-->
<!--                <i @click="show = true">选择币种<van-icon name="arrow" style="font-size: 15px;margin-top: 2px;"/></i>-->
<!--            </div>-->
            <div class="detail-info" >
              <img :src="require('../../assets/img/ic_usdt.png')" width="28px"/>

            </div>
            <span class="detail-info">USDT</span>
        </van-row>
        <van-action-sheet v-model="show" title="选择币种">
            <van-cell-group >
                <van-cell  v-for="item in tokenNames" :key="item.sToken" :title="item.sToken" @click="selectToken(item)"  />
            </van-cell-group>
        </van-action-sheet>
        <div class="sub-title">选择主网</div>
        <div class="types" v-if="protocols.length > 1">
          <span  v-for="item in protocols" :key="item.sProtocol">
            <div v-if="item.bDepositSwitch" :class="{chain:true,active:chain === item.sChain }" @click="chainChange(item)">
              {{item.sProtocol}}
            </div>
          </span>

        </div>
        <div class="cont-box">
            <div class="code-box">
                <vue-qrcode v-if="depositInfo.sAddress" :value="depositInfo.sAddress" :width="160"/>
            </div>
            <div class="title">充值地址</div>
            <div class="conts">
                <p>{{depositInfo.sAddress}}</p>
                <a class="copys" v-clipboard:copy="depositInfo.sAddress" v-clipboard:success="()=>this.$toast('已复制到剪切板')" v-clipboard:error="()=>this.$toast('复制失败')">复制地址</a>
            </div>
        </div>
        <div class="dec-boc">
            <!-- 请勿向上述地址充值任何非{{selToken.sToken + '_' + selChain.sProtocol}}资产，否则资产将不可找回。<br>
            您充值至上述地址后,需要整个网络节点的确认，{{ depositInfo.iDepositConfirmations }}次网络确认后可提币。<br>
            最小充值金额:1USDT，小于最小金额的充值将不会上账且无法退回。<br>
            您的充值地址不会经常改变,可以重复充值;如有更改,我们会尽量通过网站公告或邮件通知您。<br>
            请务必确认电脑及浏览器安全,防止信息被篡改或泄露。<br> -->

            <p>温馨提示：</p>
            <p>• 请勿向上述地址充值任何非 <span>{{selToken.sToken + '_' + selChain.sProtocol}}</span> 资产，否则资产将不可找回。</p>
            <p>• 您充值后，需要整个网络节点的确认，<span>{{ depositInfo.iDepositConfirmations }}</span> 次网络确认后到账。</p>
            <p>• 您的充值地址不会经常改变，但请每次充值前核对地址。</p>
            <p>• 请务必确认电脑及浏览器安全，防止信息被篡改或泄露。</p>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import moment from 'dayjs'
    import {getMerchantTokens,getDepositAddress,userWithdraw} from '@/api/wallet'
    import VueQrcode from 'vue-qrcode'
    import SafeVerifyModal from "@/components/SafeVerifyModal/index";
    export default {
        name: 'user-balance-deposit',
        components: {
            [Toast.name]: Toast,
            VueQrcode,
            SafeVerifyModal
        },
        data() {
            return {
                show:false,
                type:0,
                chain: 'Tron',
                fixed:true,
                detail:null,
                merchantTokens:[],
                selToken:{},
                selTokens:[],
                depositInfo:{},
                selChain:{},
                tokenNames:[],
                tokens: []
            }
        },
        computed:{
            protocols(){
                const protocols =[]
                this.tokens.forEach(token=>{
                  // debugger
                    if(token.sToken === this.selToken.sToken){
                      if(token.sProtocol ==='TRC20'){
                        protocols.unshift(token)
                      }else{
                        protocols.push(token)
                      }
                    }
                })
                    return protocols
            }
        },
         created() {
             getMerchantTokens({iIdle: 0})
                .then((res) => {
                    this.tokenNames = []
                    this.tokens = res.data.vTokens
                    this.tokens.forEach(token =>{
                        if(this.tokenNames.findIndex(t=> t.sToken === token.sToken)<0) {
                          if (token.bDepositSwitch) {
                            this.tokenNames.push(token)
                          }
                        }
                    })
                    this.selToken = this.tokenNames[0]
                    // this.chain = this.tokenNames[0].sChain
                    this.selChain = this.protocols[0]

                    getDepositAddress({iChannel:0,sToken:this.selToken.sToken,sChain:this.chain})
                        .then(res=>{
                            this.depositInfo = res.data
                        })
                })
        },
        methods: {
            moment,
            ...mapGetters(['switchEmailVerify','switchCellphoneVerify','switchGoogleVerify']),
            ...mapActions(['GetUserExten']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            initType(type){
                switch (type) {
                    case 1:
                        return '充值'
                    case 2:
                        return '提币'
                }
            },
            returnStatus(index){
                switch (index){
                    case 5 :
                        return '提币完成'
                    case 6 :
                        return '拒绝提币'
                    case 8 :
                        return '用户取消'
                    case 13 :
                        return '充值完成'
                    default:
                        return '进行中'
                }
            },
            canDeposit(token){
                // debugger
                let tmpBoolean = false
                this.merchantTokens.forEach(item=>{
                    // debugger
                    if(item.sToken === token && item.bDepositSwitch)tmpBoolean=true
                })
                return tmpBoolean
            },
            onSelect(){
                this.show=true;
            },
            selectToken(item){
                this.selToken = item
                this.chain = this.protocols[0].sChain
                this.selChain = this.protocols[0]
                this.show = false
                getDepositAddress({iChannel:0,sToken:this.selToken.sToken,sChain:this.chain})
                    .then(res=>{
                        this.depositInfo = res.data
                    })
            }
            ,
            chainChange(item){
                this.chain = item.sChain;
                this.selChain = item;
                getDepositAddress({iChannel:0,sToken:this.selToken.sToken,sChain:this.chain})
                    .then(res=>{
                        // debugger
                        this.depositInfo = res.data
                    })
            }

        }
    }
    ;

</script>

<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }

    .card-box{
        margin: 0.2rem;
        margin-top: .3rem;
