<template>
    <div>
        <div class="ords-hed">
            <van-tabs v-model="iSideType" @change="tabchange" type="" class="types" style="padding:16px 0; font-size:16px" color="#fff" background="" title-active-color="#fff">
                <van-tab name="1" title="我要买"></van-tab>
                <van-tab name="2" title="我要卖"></van-tab>
            </van-tabs>
            <van-tabs v-model="iBlockType" @change="tabchange" type="" class="two-mun" background="" @disabled="onClickDisabled">
                <van-tab name="USDT" title="USDT"></van-tab>
                <van-tab name="BTC" disabled title="BTC"></van-tab>
                <van-tab name="ETH" disabled title="ETH"></van-tab>
            </van-tabs>
        </div>
        <div class="ads-list-cont">
            <van-empty description="暂无数据" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length==0&&loadedall" />
            <van-pull-refresh v-else v-model="refreshing" style="min-height: 66vh;" @refresh="refreshData">
                <van-list
                    v-model="loading"
                    :finished="loadedall"
                    finished-text="没有更多了"
                    @load="loadData"
                >
                    <div class="warp-box">
                        <div class="ads-list" :class="{'border-bm':idx+1 < dataSource.length}" v-for="(item,idx) in dataSource" :key="idx">
                            <div class="info-top gflex">
                                <div class="box__hd" @click="detail(item,idx)">
                                    <van-image class="avatar-img" v-if="item.sAvatar" :src="item.sAvatar" :error-icon="require('../../assets/img/avatar_default.png')">
                                        <template v-slot:loading>
                                            <van-loading type="spinner" size="20" />
                                        </template>
                                    </van-image>
                                    <span class="avatar-img" v-else :class="idx+1>7?'user-avatar-bg'+parseInt(Math.random()*7+1):'user-avatar-bg'+(idx+1)">{{item.sNickName.split('')[0]}}</span>
                                </div>
                                <div class="box__bd" @click="detail(item,idx)">
                                    <div class="box__title fontw">{{item.sNickName}} ( <span class="color">{{item.iTotalOrderCount}} | {{ item | numb}}</span>)<span class="qtright"></span></div>
                                    <!-- <p class="box__dec"> 1,533单/99.80%</p> -->
                                </div>
                            </div>
                            <div class="info-cont gflex">
                                <div style="flex:1">
                                    <div class="box__title">单价 <i>￥{{Number(item.dPrice).toFixed(2)+ ' '+item.sUnit}}</i></div>
                                </div>
                                <div class="rbox">
                                    <div v-if="uid() === item.iUserId">我的广告</div>
                                    <span v-else>
                                        <div v-if="Number(item.dTradeQuantity) >0">
                                            <a class="mc-bnt" v-if="iSideType ==1" @click="showModal('buy',item)">购买</a>
                                            <a class="mc-bnt" v-if="iSideType ==2" @click="showModal('sell',item)">卖出</a>
                                        </div>
                                        <div v-else>
                                            <a class="mc-bnt hs" v-if="iSideType ==1">购买</a>
                                            <a class="mc-bnt hs" v-if="iSideType ==2">卖出</a>
                                        </div>
                                    </span>
                                </div>
                            </div>
                            <div class="info-cont gflex mbmt">
                                <div style="flex:1">
                                    <div class="box__title">数量 <span>{{Number(item.dTradeQuantity)+ ' '+item.sToken}}</span></div>
                                    <div class="box__title">限额 <span>{{ Number(item.dMinAmount).toFixed(2) + ' - ' + Number(item.dMaxAmount).toFixed(2)  + ' '+item.sUnit}}</span></div>
                                </div>
                                <div class="pay-type rbox">
                                    <span v-for="sp in returnPayModes(item.sPayMode)" :key="sp">
                                        <img v-if="sp==13" class="pay-icon" src="../../assets/img/zfb.png">
                                        <img v-if="sp==14" class="pay-icon" src="../../assets/img/wx.png">
                                        <img v-if="sp==15" class="pay-icon" src="../../assets/img/yhk.png">
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </van-list>
            </van-pull-refresh>
        </div>
        <buy-sell-modal ref="buy-sell-modal" />
    </div>
</template>

<script>
    import {otcUrl, publishAds, getPayMode, getPayDictionary, getMyAdsList} from '@/api/otc'
    import {mapGetters} from 'vuex'
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import BuySellModal from "./BuySellModal";

    export default {
        name: "Ads",
        mixins: [GpayListMixin],
        components: {
            BuySellModal
        },
        data() {
            return {
                myAdsList: [],
                myAdsParam: {},
                payDictionarys: [],
                iSideType: 1,
                iBlockType: 1,
                activeSCoinCode: 'USDT',
                resultName: 'vAdsData',
                queryParam: {
                    // iAdsId: 0,
                    // sCoinCode: 'USDT',
                    // iSideType: 2,
                    iBlockType: 1,
                    iPageNo: 0,
                    iPageSize: 10,
                    iSideType: 1,
                },
                url: {
                    list: otcUrl.GetAdsList,
                }
            };
        },
        created() {
            //查询数据
            //this.initParam();
            this.refreshData();
            getPayDictionary({iDictType: 3})
                .then((res) => {
                    this.payDictionarys = res.data.vPayDictionary
                })
        },
        methods: {
            ...mapGetters(['uid','iCertificateStatus']),
            returnPayModes(ids) {
                const tmpIds = ids.split(',')
                const modes = []
                if (tmpIds.length > 0 && this.payDictionarys.length > 0) {
                    tmpIds.forEach(id => {
                        modes.push(this.payDictionarys.find(i => i.iId == id).iId)
                    })
                }
                return modes//modes.join(',')
            },
            tabchange(val) {
                this.queryParam= {
                    iBlockType: 1,//this.iBlockType,
                    iPageNo: 0,
                    iPageSize: 10,
                    iSideType: parseInt(this.iSideType),
                },
                this.refreshData();
            },
            onClickDisabled(name, title) {
                this.$toast(name + '功能完善中...');
            },
            showModal(type,record){
                if(this.iCertificateStatus() !==2){
                    this.$dialog.confirm({
                        message: '您还没有实名认证，请先进行认证！',
                        messageAlign: 'left',
                        confirmButtonText:'前往认证',
                        confirmButtonColor: '#3D7EFF',
                        cancelButtonText:'取消'
                    }).then(() =>{
                        this.$router.push({name:'security',params:{type:'3'}})
                    })
                }else{
                    this.$refs['buy-sell-modal'].init()
                    this.$refs['buy-sell-modal'].record=record
                    this.$refs['buy-sell-modal'].type=type
                }
                
                return
                if(this.iCertificateStatus() !==2){
                    this.$confirm({
                        content:'您还没有实名认证，请先进行认证！',
                        onOk:()=>{
                            this.$router.push({name:'auth',params:{type:'1'}})
                        },
                        okText:'前往认证',
                        cancelText:'取消'
                    })
                }else{
                    this.$refs['buy-sell-modal'].type=type
                    this.$refs['buy-sell-modal'].record=record
                    this.$refs['buy-sell-modal'].visible=true
                    this.$refs['buy-sell-modal'].init()
                }
            },
            detail(item,idx){
                let coid = (idx+1)>7?+parseInt(Math.random()*7+1):(idx+1)
                this.$router.push('/otcDetail?usid='+item.iUserId+'&coid='+coid)
            }
        },
        filters:{
            numb(item){
                let nms = (item.iTotalSuccessOrderCount / item.iTotalOrderCount)
                if(isNaN(nms)){
                    return 0 +'%'
                }else{
                    return (nms*100).toFixed(2) +'%'
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .home-page {
        text-align: center;
        margin-top: 60px;
    }

    .list-main {
        margin-left: 10px;
    }

    .detail-info {
        font-weight: 200;
        font-size: 16px;
        margin-bottom: 5px;
    }

    .detail-info-sub {
        font-weight: 100;
        font-size: 14px;
        margin-bottom: 5px;
    }
    .ads-list{
        background: #fff;
        padding-top: 12px;
    }
    .info-top,.info-cont{
        padding: 0 .28rem;
    }
    .color{
        color: @gpay-color3D7EFF;
        font-weight: normal;
    }
    .border-top{
        //border-top: 1px solid #e4e5e6;
        padding-top: 12px;
    }
