    }
    .info-cont{
        .box__title{
            font-size: 12px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #919DAF;
            i{
                color: @gpay-color3D7EFF;
                font-size: .3rem;
                font-style: normal;
                font-family: PingFangSC-Regular;
                margin-left: .26rem;
            }
            span{
                color: #212833;
                font-size: .3rem;
