            border-radius: 15px;
            padding: 0.008rem 0.16rem;
        }
    }
    /deep/ .ads-nav-name .van-tab{
        font-size: .3rem;
        line-height: normal;
    }
    .ads-hed{
        background: @gpay-bg3D7EFF;
        position: fixed;
        width: 100%;
        top: 0;
