<template>
  <div>
    <van-nav-bar
      title="短信模板管理"
      class="header_title"
      @click-left="$router.back(-1)"
      left-arrow
      safe-area-inset-top
      placeholder
      :fixed="true"
    />
    <div :style="listStyle">
       <van-empty v-if="dataSource.length === 0" description="您暂未设置短信模板" :image="require('../../assets/img/empty-image-default.png')" />
        <van-pull-refresh v-else v-model="refreshing" style="min-height: 66vh;" @refresh="refreshData">
        <van-list
            v-model="loading"
            :finished="loadedall"
            finished-text="没有更多了"
            @load="loadData"
          >
          <van-panel v-for="(item,idx) in dataSource" :key="idx" title="发件人" :status="item.sSenderNo" style="margin-bottom: 0.2rem">
            <div>{{ item.sTemplate }}</div>
            <template #footer>
              <div style="text-align: right">
                <van-button size="mini" type="danger" @click="confirm(item.iId)">删除</van-button>
              </div>
            </template>
          </van-panel>
<!--          <van-cell-group v-for="(item,idx) in dataSource" :key="idx" style="margin-bottom: 0.2rem">-->
<!--            <van-cell title="发件人" :value="item.sSenderNo" />-->
<!--            <van-cell title="银行" :value="item.sBankName" :label="item.sTemplate" />-->
<!--          </van-cell-group>-->
          <!--        <div class="warp-box">-->
          <!--          <div class="ads-list" :class="{'border-bm':idx+1 < dataSource.length}" v-for="(item,idx) in dataSource" :key="idx">-->

          <!--          </div>-->
          <!--        </div>-->
        </van-list>
        </van-pull-refresh>
    </div>
    <van-button icon="plus" type="info" size="mini" style="width:50%;margin:0 auto;padding:15px 0" round block @click="$router.push('/addSMSModel')">添加短信模板</van-button>
    <van-dialog
        v-model="showConfirm"
        theme="round"
        show-cancel-button
        confirmButtonText = '确定删除'
        cancelButtonText = '我再想想'
        :before-close = "delSMSTp"
        message="确定删除此条短信吗？"
    >
    </van-dialog>
  </div>
</template>

<script>
import {delUserSmsTpl,getUserSmsTplList,otcUrl} from '@/api/otc'
import {GpayListMixin} from "@/mixins/GpayListMixin";
import {postAction} from "@/api/manage";
export default {
name: "smsModels",
  mixins: [GpayListMixin],
  data(){
    return {
      url: {
        list: otcUrl.GetUserSmsTplList,
      },
      listStyle: {
        'padding-top': '1.2rem',
        height: 'calc('+ window.innerHeight +'px' + ' - 2rem)',
        overflow:'auto'
      },
      showConfirm:false,
      iId:null
    }
  },
  created(){
    this.loadData();
  },
  methods:{
    loadData() {
      if (this.loadedall) {
        console.log('已经加载到最后了')
        return
      }
      //加载数据 若传入参数1则加载第一页的内容

      let params = {iIdle:0}
      postAction(this.url.list, params).then((res) => {
        if (res.iRspCode === 0) {
          let data = res.data.vList
          if (data && data.length > 0) {
            this.dataSource = res.data.vList
            console.log('loadDataloadData------5---数据加载完成')
            this.loadedall = true;
          }
        } else {
          this.error = false
          this.$toast.fail(res.message)
        }
      }).catch(err => {
        console.log(err)
        this.error = false
        this.loading = false;
        this.refreshing = false;
        this.loadedall = true;
      }).finally(() => {
        this.loading = false;
        this.refreshing = false;
        console.log('finally')
      })
    },
    confirm(id){
      this.iId = id
      this.showConfirm = true
    },
    delSMSTp(action,done) {
      delUserSmsTpl({iId:this.iId})
      .then(res=>{
        if(res.iRspCode === 0){
          this.$message.success('已删除！')
          this.refreshData();
        }else {
          this.$message.error('删除失败,请稍后再试！')
        }
        done()
      })
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .van-panel__content{
  padding:10px 16px
}
/deep/ .van-nav-bar{
  background: @gpay-bg3D7EFF;
  .van-nav-bar__text{
    color: @gpay-colorffffff;
  }
  .van-nav-bar__title{
    color: @gpay-colorffffff;
  }
  .van-nav-bar__right .van-nav-bar__text{
    color: @gpay-color3D7EFF;
    background: #ffffff;
    border-radius: 20px;
    padding: 1px 6px;
    font-size: 12px;
  }
}
.ords-hed{
  background-color: @gpay-bg3D7EFF;
  text-align: center;
  color: white;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 6;
  padding-bottom: .18rem;
  padding-top: .18rem;
}
</style>