<template>
    <div>
        <router-view/>
        <van-nav-bar
            title="个人设置"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :fixed="fixed"
        />
        <div class="user-conts">
            <van-cell-group>
                <van-cell title="头像" class="avatar-box">
                    <van-image v-if="avatar" @click.native="upAvatar" :src="avatar" width="55" height="55" :error-icon="require('../../assets/img/avatar_default.png')">
                        <template v-slot:loading>
                            <van-loading type="spinner" size="20" />
                        </template>
                    </van-image>
                    <!-- <img v-if="avatar" width="42" height="42" style="border-radius: 50%;" @click="upAvatar" :src="avatar" alt=""> -->
                    <img v-else width="42" height="42" style="border-radius: 50%;" @click="upAvatar" src="../../assets/img/avatar_default.png" alt="">
                </van-cell>
                <van-cell v-if="iIsChangeNameStatus() === 0 " :title="nickname" isLink>
                    <template #right-icon>
                        <a class="link-color" @click="showDialog(setNickname)">更改昵称</a>
                    </template>
                </van-cell>
                
                <van-cell v-else title="昵称" :value="nickname"></van-cell>
                <van-cell title="zanliu" v-if="false">
                    <template #right-icon>
                        111
                    </template>
                </van-cell>

            </van-cell-group>
        </div>
        <!-- <van-dialog
                v-model="show"
                title="设置昵称"
                show-cancel-button
                :beforeClose="closeDialog"
        >
            <van-field v-model="message" :label="label" placeholder="请输入"/>
        </van-dialog> -->
        <!--设置昵称-->
        <van-popup v-model="show"
            :closeable="false"
            round
            title="修改昵称"
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            position="bottom"
        >   
            <div class="popup-title-hed">设置昵称</div>
            <div class="bzt">
                注：昵称只能修改一次，保存后将无法修改！
            </div>
            <van-field
                class="nk-icon"
                v-model="message"
                center
                clearable
                label=""
                type="text"
                :maxlength="10"
                :left-icon="require('../../assets/img/nk-icon.png')"
                placeholder="请输入昵称"
            >
            </van-field>
            <div style="margin: 36px 16px; 0 16px">
                <van-button style="width:47%;display: inline-block;background: #FFFFFF;border: 1px solid #C8CBCE;color: #919DAF;" round block type="info" @click="closes">
                    取消
                </van-button>
                <van-button style="width:47%;display: inline-block; margin-left:.4rem;" round block type="info" @click="closeDialog">
                    确认
                </van-button>
            </div>
        </van-popup>
        <pub-up-img ref="PubUpImgs"></pub-up-img>
    </div>
</template>

<script>
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {chgUserExten} from '@/api/udc'
    import {emailVerify, sendEmailVerifyCode} from '@/api/auth'
    import {regPwd} from "@/utils/validate"
    import PubUpImg from "@/components/fileAvatar";

    export default {
        name: 'user-setting',
        components: {
            [Toast.name]: Toast,
            PubUpImg
        },
        data() {
            return {
                fixed: true,
                //nickname: '',
                message: '',
                show: false,
                showEmail: false,
                getEmailCodeFlag: true,
                label: '',
                password: '',
                emailCode: '',
                action: null,
                emailCountTime: 60
            }
        },
        computed:{
            avatar(){
                return this.sAvatar()
            },
            nickname(){
                return this.userName()
            }
        },
        mounted() {

        },
        created() {
            //this.nickname = this.userName();
        },
        methods: {
            ...mapGetters(['iAdsStatus','iLiveCertificateStatus','isHasEmail', 'isLogin', 'userName', 'switchEmailVerify', 'switchCellphoneVerify', 'switchGoogleVerify','sAvatar','iIsChangeNameStatus']),
            ...mapActions(['GetUserExten']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            showDialog(action) {
                this.show = true;
                this.action = action;
            },
            closes(){
                this.show = false
                this.message = ''
            },
            closeDialog() {
                if (this.message == '' || this.message == null || this.message == undefined) {
                    this.$toast("请输入用户昵称");
                } else {
                    this.action();
                    this.show = false;
                }
            },
            setNickname() {
                const params = {
                    sNickName: this.message,
                }
                chgUserExten(params)
                    .then(() => {
                        this.$toast('修改成功！');
                        this.GetUserExten()
                    })
            },
            upAvatar(){
                this.$refs.PubUpImgs.init()
            },
        }
    };

</script>

<style lang="less" scoped>
    .user-conts{
        padding-top: 46px;
    }
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    /deep/.nk-icon .van-icon{
        font-size: .42rem;
        margin-top: -6px;
        margin-right: .06rem;
        .van-icon__image{
            height: auto;
        }
    }
    /deep/.van-image__error{
        border-radius: 50%;
        background: none;
        .van-icon__image{
            width: 38px;
            height: 38px;
            position: relative;
            top: -2px;
            border-radius: 50%;
        }
    }
    .mu-flat-button{
        height: auto !important;
        line-height: normal !important;
        min-width: auto !important;
    }
    .avatar-box{
        justify-content: center;
        align-items: center;
    }
    .popup-title-hed{
        font-size: .32rem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #000000;
        margin: .32rem 0 .24rem .42rem;
    }
    .pass-style{
        color: seagreen;
        i{
            margin-top: 3px;
            font-size: 16px;
            position: relative;
            top: 4px;
            margin-right: 6px;
        }
    }
    .bzt{
        color: red;
        padding-left: 20px;
        font-size: 12px;
    }
    /deep/.van-image__img{
        border-radius: 50%;
    }
</style>
