<template>
    <div>
        <van-popup v-model="visible"
            :closeable="false"
            round
            title=""
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            position="bottom"
        >
            <div class="popup-title-hed">{{type==='buy' ? '买入':'卖出'}} <img src="../../../assets/img/buy-icon.png" class="buy-icon" alt=""></div>
            <div class="buy-conts">
                <div class="buy-list">
                    <div class="info-top gflex border-bm border-top">
                        <!-- <div class="box__hd"><img class="avatar" src="../../../assets/img/avatar_default.png" alt=""></div> -->
                        <div class="box__bd">
                            <div class="box__title">广告方：<em>{{record.sNickName}}</em><span class="qtright"></span></div>
                        </div>
                    </div>
                </div>
            </div>
            <van-form @submit="onSubmit" ref="forms" class="forms" validate-trigger="onChange">
                <van-field clearable :border="false" label="下单金额" name="amount" :rules="[{ validator:handleVerifyPrice, type:'amount' }]">
                    <template #input>
                        <input :disabled="type==='sell'&&tokenBlalnces <=0" autocomplete="off" @input="priceChange(price)" style="width:100%;border:0" v-model="price" type="text" :placeholder="'限额 '+ Math.floor(dMinAmount * 10000) / 10000 + ' - ' + Math.floor(dMaxAmount * 10000) / 10000 + ' '+record.sUnit">
                    </template>
                </van-field>
                <van-field clearable :border="false" label="下单数量" name="numb" :rules="[{ validator:handleVerifyAmount, type:'numb' }]">
                    <template #input>
                        <input :disabled="type==='sell'&&tokenBlalnces <=0" autocomplete="off" @input="amountChange(amount)" style="width:100%;border:0" v-model="amount" type="text" :placeholder="'限额 '+ Math.floor(dMinQuantity * 10000) / 10000 + ' - ' + Math.floor(dMaxQuantity * 10000) / 10000  + ' '+record.sToken">
                    </template>
                </van-field>
                <div class="buy-conts">
                    <div class="buy-list">
                        <div class="info-cont gflex border-bm fnz">
                            <div v-if="dFeeRate">
                                交易费率:
                                <span class="fee-info">{{dFeeRate * 100 +'%'}}</span>
                                <span v-if="type==='buy'  && amount >0">
                                ，实际到账
                                <span class="fee-info">{{(amount * (1-dFeeRate)).toFixed(4) + ' ' + record.sToken}}</span>
                                </span>
                                <span v-if="type==='sell' && amount >0">
                                ，手续费
                                <span class="fee-info">{{ (amount * dFeeRate).toFixed(4) + ' ' + record.sToken + ', '}}</span>
                                <span>将从您的余额中扣除！</span>
                                <span v-if="tokenBlalnces < amount*( 1 + dFeeRate )" style="color:red"> 您的余额不足！</span>
                                </span>
                            </div>
                        </div>
                        <!-- <div v-if="type==='sell'">您的可用余额: <a @click="setAll">{{tokenBlalnces }}</a>{{ ' ' + record.sToken}}</div> -->
                        <div v-if="type==='sell'" class="info-cont gflex border-bm">
                            <div style="flex:1">
                                <div class="box__title">您的可用余额<span style="margin-left:.8rem"><a class="alls" @click="setAll">{{tokenBlalnces}}</a> {{ ' ' + record.sToken}}</span></div>
                            </div>
                        </div>
                        <div class="info-cont gflex border-bm">
                            <div style="flex:1">
                                <div class="box__title">单价<span style="margin-left:.8rem">{{ dPrice.toFixed(2)+ ' '+record.sUnit }}</span></div>
                            </div>
                        </div>
                        <div class="info-cont gflex border-bm">
                            <div style="flex:1">
                                <div class="box__title">剩余数量 <i>{{ dTradeQuantity + ' ' + record.sToken}}</i></div>
                            </div>
                        </div>
                        <div class="info-cont gflex border-bm">
                            <div style="flex:1">
                                <div class="box__title">单笔限额 <span>{{ dMinAmount.toFixed(2) + ' - ' + Number(record.dMaxAmount).toFixed(2)  + ' '+record.sUnit}}</span></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div style="margin: 16px;">
                    <template v-if="type==='sell'">
                        <div v-if="tokenBlalnces >0">
                            <div v-if="payModes.length ===0">
                                <div style="color:red;font-size:12px;margin-bottom:12px">您还未设置收款方式，无法下单，请先添加支付方式！</div>
                                <van-button class="can-bnt" native-type="button" round block type="info" @click="bnt(false)">
                                    取消
                                </van-button>
                                <van-button style="width:46%;display: inline-block; margin-left:.4rem;" native-type="button" round block type="info" @click="addPay">
                                    添加收款方式
                                </van-button>
                            </div>
                            <template v-else>
                                <van-button class="can-bnt" native-type="button" round block type="info" @click="bnt(false)">
                                    取消
                                </van-button>
                                <van-button style="width:46%;display: inline-block; margin-left:.4rem;" round block type="info" native-type="submit">
                                    下单
                                </van-button>
                            </template>
                        </div>
                        
                        <template v-else>
                            <div style="color:red;font-size:12px;margin-bottom:12px">抱歉您没有足够余额可用，无法进行交易！</div>
                            <van-button class="can-bnt" native-type="button" style="margin: 0 auto;display: block;" round block type="info" @click="bnt(false)">
                                取消
                            </van-button>
                        </template>
                    </template>
                    <template v-else>
                        <van-button class="can-bnt" native-type="button" round block type="info" @click="bnt(false)">
                            取消
                        </van-button>
                        <van-button style="width:46%;display: inline-block; margin-left:.4rem;" round block type="info" native-type="submit">
                            下单
                        </van-button>
                    </template>
                </div>
            </van-form>
        </van-popup>
        <pay-add ref="payType" @refPay="refData"></pay-add>
    </div>
</template>
<script>
import { getCertificate,getUserBalance } from '@/api/udc'
import { createOrder,getPayMode } from '@/api/otc'
import {mapGetters,mapActions} from "vuex"
import PayAdd from '@/components/pay/addPay'
import {accDiv,accMul} from '@/utils/validate'

export default {
    components:{
        PayAdd
    },
    data(){
        return {
            visible: false,
            record:{},
            type: '',
            price: null,
            amount: null,
            balances:[],
            payModes:[],
        }
    },
    computed:{
        dTradeQuantity(){
            return Number(this.record.dTradeQuantity)
        },
        dMinAmount(){
            return Number(this.record.dMinAmount)
        },
        dMaxAmount(){
            // if(this.record.dMaxQuantity == this.dMaxQuantity){
            //     return Number(this.record.dMaxAmount)
            // }
            // return this.dMaxQuantity * this.dPrice
            if(this.record.dMaxQuantity == this.dMaxQuantity){
                return Number(this.record.dMaxAmount)
            }
            return Number(this.record.dMaxAmount)
        },
        dMinQuantity(){// 数量最小
            let minVal = 0//accDiv(v,this.dPrice)
            if(this.record.dMinQuantity){
                minVal = accDiv(this.record.dMinAmount,this.record.dPrice)
            }
            return minVal
            //return Number(this.record.dMinQuantity)
        },
        dMaxQuantity(){// 数量最大
            // if(this.type==='buy'){
            //     return Math.min(this.dTradeQuantity , Number(this.record.dMaxQuantity) )
            // }else{
            //     return Math.min(this.dTradeQuantity , Number(this.record.dMaxQuantity) ,this.tokenBlalnces)
            // }
            let maxVal = 0//accDiv(v,this.dPrice)
            if(this.record.dMaxAmount){
                maxVal = accDiv(this.record.dMaxAmount,this.record.dPrice)
            }
            if(this.type==='buy'){
                return Math.min(this.dTradeQuantity , Number(maxVal))
                //return Math.min(this.dTradeQuantity , Number(this.record.dMaxQuantity) )
            }else{
                return Math.min(this.dTradeQuantity , Number(maxVal) ,this.tokenBlalnces)
                //return Math.min(this.dTradeQuantity , Number(this.record.dMaxQuantity) ,this.tokenBlalnces)
            }
        },
        tokenBlalnces(){
            if(this.balances.length>0){
                return Number(this.balances.find(i=>i.sToken === this.record.sToken).iAvailableAmount)
            }else{
                return 0
            }
        },
        dPrice(){
            return Number(this.record.dPrice)
        },
        dFeeRate(){
            if(this.commodityList() && this.commodityList().length > 0){
            // debugger
            const index =this.commodityList().findIndex(i=>i.sToken === this.record.sToken)
            if (index > -1){
                return Number(this.commodityList()[index].dFeeRate)
            }
            return 0
            }
            return 0
        },
    },
    methods:{
        ...mapGetters(['commodityList']),
        ...mapActions(['GetCommodityList']),
        init(){
            getPayMode({iIdle:0})
                .then((res)=>{
                    this.payModes = []
                    res.data.vPayModeList.forEach(i=>{
                    if(i.iIsDisplay ===1){
                        this.payModes.push(i)
                    }
                })
            })
            this.visible = true
            getUserBalance({iIdle:0})
            .then((res)=>{
                this.balances = res.data.vBalance
            })
            this.GetCommodityList()
        },
        bnt(t){
            if(t){

            }else{
                this.$refs.forms.resetValidation()//重置验证
                this.visible = false
                this.price = null
                this.amount = null
            }
        },
        refData(){
            this.init()
            console.log('---=')
        },
        setAll(){
            const blalnces = this.tokenBlalnces/(1+ this.dFeeRate)
            if(blalnces < this.dMinQuantity){
                const massage = this.dFeeRate >0 ? '扣除手续费后':''
                this.$toast('您的余额'+massage+'不足最低单笔限额！')
                return
            }

            const tmpQuantity = blalnces <this.dMaxQuantity ? blalnces : this.dMaxQuantity
            this.$refs.forms.validate('amount')
            this.$refs.forms.validate('numb')
            this.amount = parseInt(tmpQuantity*10000)/10000
            //this.form.setFieldsValue({amount:parseInt(tmpQuantity*10000)/10000})
            this.amountChange(parseInt(tmpQuantity*10000)/10000)
        },
        handleVerifyPrice(value,rule){
            let price = this.price!=null?(parseInt(this.price*10000))/10000:0//parseFloat(this.price).toFixed(2)
            if (parseFloat(price) > parseFloat(this.dMaxAmount) || parseFloat(price) < parseFloat(this.dMinAmount)) {
                rule.message = '请在 '+(parseInt(this.dMinAmount*10000))/10000 + ' - '+ (parseInt(this.dMaxAmount*10000))/10000 +'限额内下单！'
                return false
            }
            // if (this.price >this.dMaxAmount || this.price < this.dMinAmount) {
            //     rule.message = '请在 '+this.dMinAmount.toFixed(2) + ' - '+this.dMaxAmount.toFixed(2)+'限额内下单！'
            //     return false
            // }
        },
        handleVerifyAmount(value,rule){
            let amount = this.amount!=null?this.amount:0
            if (parseFloat(amount) > parseFloat(Math.floor(this.dMaxQuantity * 10000) /10000) || parseFloat(amount) < parseFloat(Math.floor(this.dMinQuantity * 10000) / 10000)) {
                rule.message = '请在 '+Math.floor(this.dMinQuantity * 10000) / 10000 + ' - '+ Math.floor(this.dMaxQuantity * 10000) /10000 +'限额内下单！'
                return false
            }
            if(this.type ==='sell' && this.amount > this.tokenBlalnces){
                rule.message = '您的余额不足！'
                return false
            }
            // if (parseInt(this.amount*10000)/10000 >this.dMaxQuantity || this.amount < this.dMinQuantity) {
            //     rule.message = '请在 '+this.dMinQuantity + ' - '+this.dMaxQuantity+'限额内下单！'
            //     return false
            // }
            // if(this.type ==='sell' && this.amount > this.tokenBlalnces){
            //     rule.message = '您的余额不足！'
            //     return false
            // }
        },
        priceChange(val){
            this.$refs.forms.validate('amount')
            this.$refs.forms.validate('numb')
            this.price = val
            this.amount = accDiv(val,this.dPrice)//val/this.dPrice
            this.amount = val?(parseInt(this.amount*10000))/10000:0//(parseInt(this.amount*10000))/10000
        },
        amountChange(val){
            this.$refs.forms.validate('numb')
            this.amount = val
            console.log(accMul(val,this.dPrice),'accMul(val,this.dPrice)')
            let acc = accMul(val,this.dPrice)
            this.price = acc?acc.toFixed(2):0//this.price = val * this.dPrice
            this.price = parseFloat(this.price).toFixed(2)
        },
        onSubmit(vals) {
            getCertificate({iIdle:0})
                .then(res=>{
                    const params={
                        iAdsId:this.record.iId,
                        dTradeAmount:""+this.price+"",//this.price.toString(),
                        sRealName:res.data.sSurname + res.data.sName,
                        sAccount:' ',
                        iIsFastTrading:0
                    }
                    createOrder(params)
                        .then(res=>{
                            console.log(res)
                            this.$router.push('/orderdetail?id='+res.data.sOrderId)
                        })
                })
        },
        addPay(){
            this.$refs.payType.init()
            //this.$refs.payType.showPay = true
            //this.$refs.payType.payList = this.payDictionarys
        }
    }
}
</script>
<style lang="less" scoped>
    .forms .van-cell{
        //align-items: center;
        height: 1.5rem;
    }
    .buy-list{
        background: #fff;
        margin-top: 12px;
        em,.alls{
            color: @gpay-color3D7EFF;
            font-style: normal;
            font-weight: bold;
        }
        .info-top,.info-cont{
            padding: 0 .28rem;
        }
        .border-top{
            border-top: 1px solid #e4e5e6;
            padding-top: 12px;
        }
        .border-bm{
            border-bottom: 1px solid #e4e5e6;
            padding-bottom: 12px;
            margin-bottom: .25rem;
        }
        .gflex{
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            -webkit-box-align: center;
            -webkit-align-items: center;
            align-items: center;
        }
        .box__hd{
            margin-right: 16px;
            width: 42px;
            height: 42px;
            background: #FFFFFF;
            border-radius: 50%;
            .avatar{
                width: 100%;
                border-radius: 50%;
            }
        }
        .box__bd{
            -webkit-box-flex: 1;
            -webkit-flex: 1;
            flex: 1;
            min-width: 0;
        }
        .box__dec{
            font-size: 14px;
            line-height: 1.4;
            padding: 0;
            margin: 0;
            padding-top: 4px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
        }
        .info-cont{
            .box__title{
                font-size: 12px;
                font-family: PingFang SC;
                font-weight: 400;
                color: #919DAF;
                i{
                    color: #000000;
                    font-size: .26rem;
                    font-style: normal;
                    font-family: PingFangSC-Regular;
                    margin-left: .26rem;
                    float: right;
                }
                span{
                    color: #000000;
                    font-size: .26rem;
                    margin-left: .26rem;
                    float: right;
                }
            }
            .rbox{
                margin-right: 3px;
            }
            .mc-bnt{
                min-width: 120px;
                background: @gpay-bg3D7EFF;
                border-radius: 24px;
                padding: 3px 18px;
                font-size: 14px;
                font-family: PingFang SC;
                font-weight: 400;
                color: #FFFFFF;
                line-height: 36px;
            }
            .pay-type{
                width: 61px;
                img{
                    width: 20px;
                    margin-right: 8px;
                }
            }
        }
        .fnz{
            font-size: 12px;
        }
    }
    
    .popup-title-hed{
        font-size: .36rem;
        font-family: PingFang SC;
        font-weight: bold;
        color: #000000;
        margin: 8px 0 8px .42rem;
    }
    /deep/.forms .van-field__body{
        background: #FFFFFF;
        border: 1px solid #C8CBCE;
        border-radius: 5px;
        padding: .1rem .15rem;
    }
    .buy-icon{
        width: 32px;
        margin-right: .34rem;
        float: right;
    }
    .can-bnt{
        width:46%;
        display: inline-block;
        background: #FFFFFF;
        border: 1px solid #C8CBCE;
        color: #919DAF;
    }
</style>