<template>
    <div>
        <van-nav-bar
            title="发布广告"
            left-text=""
            class="header_title"
            safe-area-inset-top
            placeholder
            left-arrow
            @click-left="onClickLeft"
            :fixed="true"
        />
        <div style="height:1rem"></div>
        <div class="ads-add-list">
            <div class="info-cont gflex mbmt">
                <div style="flex:1">
                    <div class="box__title">可用余额： <span>{{Number(iAvailableQuantity).toFixed(4)}}</span> USDT</div>
                </div>
            </div>
            <!-- <div class="info-cont gflex mbmt">
                <div style="flex:1">
                    <div class="box__title">最大限额： <span>{{amounts.dMaxQuantity}}</span> USDT</div>
                </div>
            </div> -->
            <div class="info-cont gflex">
                <div style="flex:1">
                    <div class="box__title">最小限额： <span>{{amounts.dMinQuantity}}</span></div>
                </div>
            </div>
            <div class="info-cont gflex border-bms mbmt">
                <div style="flex:1">
                    <div class="box__title">您当前等级每单广告限额(USDT) <span>{{iOrderLimit}}</span> USDT</div>
                </div>
            </div>
        </div>
        <div class="dec-hed">
            <van-tabs type="card" v-model="tyoeFlag" @change="mPrice" color="#3D7EFF" style="width:180px;height:30px">
                <van-tab title="市价单"></van-tab>
                <van-tab title="限价单"></van-tab>
            </van-tabs>
            <p style="padding:0 16px;margin-top:12px;">
                <span style="color:#c2bbbb">{{sToken + '当前市价： '}}</span>
                <span class="font18">{{getMarketdPrice()(sToken)}}</span>
                <span> CNY</span>
            </p>
            <p style="padding:0 16px">
                <span style="color:#c2bbbb">• 市价单将自动根据系统在市场采集的OTC均价进行动态调整</span>
            </p>
        </div>
        <div class="f-cont">
            <van-form @submit="onSubmit" ref="forms" validate-trigger="onChange">
                <van-field
                    label="币种"
                    value="USDT"
                >
                    <template #input>
                        <van-radio-group v-model="sToken" direction="horizontal">
                            <van-radio name="USDT">USDT</van-radio>
                        </van-radio-group>
                    </template>
                </van-field>
                <van-field name="radio" label="交易类型">
                    <template #input>
                        <van-radio-group v-model="iSideType" direction="horizontal">
                            <van-radio :name=1>卖出</van-radio>
                            <van-radio :name=2>买入</van-radio>
                        </van-radio-group>
                    </template>
                </van-field>
                <van-field
                    v-if="tyoeFlag==1"
                    v-model="dPrice"
                    name="pattern"
                    label="单价"
                    placeholder="请输入交易单价"
                    autocomplete="off"
                    :maxlength="20"
                    :rules="[{ required: true, message: ' ' }]"
                />
                <van-field
                    v-model="dTradeQuantity"
                    name="pattern"
                    label="数量"
                    placeholder="请输入交易数量"
                    :maxlength="20"
                    autocomplete="off"
                    :rules="[{ validator:numbVal, message: errMsg,type:'numb' }]"
                />
                <van-field
                    v-model="dMinAmount"
                    name="pattern"
                    label="最低限额"
                    :maxlength="20"
                    placeholder="请输入单笔最低限额"
                    autocomplete="off"
                    :rules="[{ validator:minVal, message: errMsg1,type:'dmin' }]"
                />
                <van-field
                    v-model="dMaxAmount"
                    name="pattern"
                    label="最高限额"
                    :maxlength="20"
                    placeholder="请输入单笔最高限额"
                    autocomplete="off"
                    :rules="[{ validator:maxVal, message: errMsg2,type:'dmxn' }]"
                />
                <van-field
                    v-model="payId"
                    name="picker"
                    label="支付方式"
                    :rules="[{ required: true, message: '请输选择支付方式' }]"
                >
                <template #input>
                    <a :class="{'no-style':!payId}" @click="showPicker = true">{{ payId?payId:'选择支付方式' }}</a>
                </template>
                </van-field>
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        确认添加
                    </van-button>
                </div>
            </van-form>
        </div>
        <van-popup v-model="showPicker" 
            :closeable="true"
            round
            title="支付方式"
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            @closed="cloesd"
            position="bottom">
            <template v-if="columns.length>0">
                <br><br>
                <van-checkbox-group v-model="vUserModeId">
                    <van-cell-group style="padding:20px 0">
                        <van-cell
                            v-for="(item, index) in columns"
                            clickable
                            :key="item.iId"
                            :title="` ${item.sItemDesc}：${item.sAccount}`"
                            @click="toggle(index)"
                        >
                        <template #right-icon>
                            <van-checkbox :name="item" :value="item.iId" ref="checkboxes" />
                        </template>
                        </van-cell>
                    </van-cell-group>
                </van-checkbox-group>
                <div style="margin: 26px 16px;text-align: center;">
                    <!-- <van-button class="f-bnt" round block type="info" @click="onConfirm(false)">
                        取消
                    </van-button> -->
                    <van-button class="f-bnt-true" style="width:88%" round block type="info" @click="onConfirm(true)">
                        确认
                    </van-button>
                </div>
            </template>
            <template v-else>
                <div style="margin: 26px 16px;text-align: center;">
                    <div class="npay">您未设置收款方式</div>
                    <van-button class="f-bnt-true" style="width:50%;height:38px" round block type="info" @click="addPay">
                        添加收款方式
                    </van-button>
                </div>
            </template>
        </van-popup>
        <pay-add ref="payType" @refPay="refData"></pay-add>
    </div>
</template>

<script>
    import {otcUrl, publishAds, getPayMode, getPayDictionary, getMyAdsList, adsUp, adsDown} from '@/api/otc'
    import {mapGetters,mapActions} from 'vuex'
    import moment from 'dayjs'
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import {getUserBalance,getCertificate} from '@/api/udc'
    import PayAdd from '@/components/pay/addPay'
    import {accMul} from '@/utils/validate'

    export default {
        name: "Ads",
        mixins: [GpayListMixin],
        components: {
            PayAdd
        },
        data() {
            return {
                iSideType: 1,
                dTradeQuantity:'',
                dMinAmount:'',
                dMaxAmount:'',
                sToken:'USDT',
                vUserModeId:[],
                payId:'',
                prices: 1,
                numbs: 1,
                dPrice:'',
                pattern: /\d{6}/,
                payModes:[],
                payDictionarys:[],
                showPicker: false,
                errMsg: '',
                errMsg1: '',
                errMsg2: '',
                commodityListArr: [],
                iAvailableQuantity:'0',
                amounts:{},
                realName:'',
                tyoeFlag: 0,
                iIsMarketPrice:1,
            };
        },
        computed:{
            columns(){
                let arr = []
                for(let m of this.payModes){
                    if(m.iIsDisplay){
                        if(this.payDictionarys.length > 0){
                            let _n = this.payDictionarys.find(i=> i.iId == m.iPayDictId)
                            m.sItemDesc = _n.sItemDesc
                            arr.push(m)
                        }
                    }
                }
                return arr
            },
            iOrderLimit(){
                return this.getAdsGradeVal()(this.iCreditLevel()).iOrderLimit||0
            }
        },
        created() {
            this.init()
        },
        methods: {
            ...mapGetters(['userName','cellphone','commodityList','getAdsGradeVal','iCreditLevel','getMarketdPrice']),
            ...mapActions(['GetCommodityList','GetAdsGrade','GetMarketPrice']),
            init(){
                // 支付方式
                getPayMode({iIdle:0})
                .then((res)=>{
                    this.payModes = res.data.vPayModeList
                })
                getPayDictionary({iDictType:3})
                .then((res)=>{
                    this.payDictionarys = res.data.vPayDictionary
                })
                getUserBalance({iIdle:0})
                .then((res)=>{
                //this.balances = res.data.vBalance
                    for(let ia of res.data.vBalance){
                        if(ia.sToken == "USDT"){
                            this.iAvailableQuantity = ia.iAvailableAmount
                        }
                    }
                })
                getCertificate({iIdle:0})
                .then(res=>{
                    this.realName = res.data.sSurname + res.data.sName
                })
                this.GetCommodityList().then((res)=>{
                    let data = this.commodityList(),_n =[];
                    for(let item of data){
                        if(item.sToken == "USDT"){
                        _n.push({
                            dFeeRate:Number(item.dFeeRate).toFixed(2),
                            dMaxQuantity:Number(item.dMaxQuantity).toFixed(4),
                            dMinQuantity:Number(item.dMinQuantity).toFixed(4),
                            sToken:item.sToken
                        })
                        }
                    }
                    this.amounts = _n[0]
                    this.commodityListArr = _n
                })
                this.GetMarketPrice()
                this.GetAdsGrade()
            },
            //数量限制
            numbValOld(value,rule){
                this.numbs = value ? value : 0
                // 主要就是添加一个对undefined和空串的判断
                if (!value) {
                    rule.message = '请输入数量'
                    return false
                }
                //如果是卖单的话
                if (this.iSideType === 1) {
                    // 最大不能超过 可用余额和最大限额的较小者，最小不能小于最小限额
                    const maxType = Number(this.iAvailableQuantity) > Number(this.iOrderLimit) ? true :false
                    const maxLimit = maxType ? Number(this.iOrderLimit): Number(this.iAvailableQuantity)
                    if(parseFloat(value) > parseFloat(maxLimit)){
                        rule.message = '卖出数量不能大于'+ (maxType ? '最大限额':'可用余额')
                        return false
                    }
                    // 最小不能小于最小限额
                    if(parseFloat(value) < parseFloat(this.amounts.dMinQuantity)){
                        rule.message = '卖出数量不能小于最小限额'
                        return false
                    }
                }
                //如果是买单的话
                if (this.iSideType === 2) {
                    // 最大不能超过最大限额
                    if(parseFloat(value) > parseFloat(this.iOrderLimit)){
                        rule.message = '买入数量不能大于最大限额'
                        return false
                    }
                    // 最小不能小于最小限额
                    if(parseFloat(value) < parseFloat(this.amounts.dMinQuantity)){
                        rule.message = '买入数量不能小于最小限额'
                        return false
                    }
                }
            },
            numbVal(value,rule){
                this.numbs = value ? value : 0
                // 主要就是添加一个对undefined和空串的判断
                if (typeof (value) === 'undefined' || value === '') {
                    //rule.message = '请输入数量'
                    return false
                }
                //如果是卖单的话
                if (this.iSideType === 1) {
                    // 最大不能超过 可用余额和最大限额的较小者，最小不能小于最小限额
                    const maxType = Number(this.iAvailableQuantity) > Number(this.iOrderLimit) ? true :false
                    const maxLimit = maxType ? Number(this.iOrderLimit): Number(this.iAvailableQuantity)
                    if(value > maxLimit){
                        rule.message = '卖出数量不能大于'+ (maxType ? '最大限额':'可用余额')
                        return false
                    }
                    // 最小不能小于最小限额
                    if(parseFloat(value) < parseFloat(this.amounts.dMinQuantity)){
                        rule.message = '卖出数量不能小于最小限额'
                        return false
                    }
                }
                //如果是买单的话
                if (this.iSideType === 2) {
                    // 最大不能超过最大限额
                    if(value > Number(this.iOrderLimit)){
                        rule.message = '买入数量不能大于最大限额'
                        return false
                    }
                    // 最小不能小于最小限额
                    if(value < this.amounts.dMinQuantity){
                        rule.message = '买入数量不能小于最小限额'
                        return false
                    }
                }
            },
            minValOld(value,rule){
                let prices = this.dPrice
                this.minAmount = value
                if (!value) {
                    rule.message = '请输入单笔最低限额'
                    return false
                }
                let adp = accMul(this.amounts.dMinQuantity,prices)
                //if (value < Number(this.amounts.dMinQuantity)*prices) {
                if (parseFloat(value) < parseFloat(adp)) {
                    rule.message = '最低限额不能小于最小额度'
                    return false
                }
                if (parseFloat(this.maxAmount) && this.maxAmount !==0 && parseFloat(value) > parseFloat(this.maxAmount)) {
                    rule.message = '最低限额不能大于最高限额'
                    return false
                }
                let nd = accMul(this.numbs,this.dPrice)
                //if (value > this.numbs * this.dPrice) {
                if (value > nd) {
                    rule.message = '最低限额不能大于广告总额'
                    return false
                }
            },
            minVal(value, rule){
                let prices = this.iIsMarketPrice === 1 ? this.getMarketdPrice()(this.sToken) : this.dPrice
                this.minAmount = value
                if (!value) {
                    //rule.message = '请输入单笔最低限额'
                    return false
                }
                let acm = accMul(this.amounts.dMinQuantity,prices)
                //if (value < Number(this.amounts.dMinQuantity)*prices) {
                if (parseFloat(value) < parseFloat(acm)) {
                    rule.message = '最低限额不能小于最小额度'
                    return false
                }
                if (parseFloat(this.maxAmount) && this.maxAmount !==0 && parseFloat(value) > parseFloat(this.maxAmount)) {
                    rule.message = '最低限额不能大于最高限额'
                    return false
                }
                console.log(this.iIsMarketPrice,this.prices,this.numbs,prices,'prices')
                let np = accMul(this.numbs,prices)
                if (parseFloat(value) > parseFloat(np)) {
                    rule.message = '最低限额不能大于广告总额'
                    return false
                }
            },
            maxValOld(value,rule){
                let prices = this.dPrice
                this.maxAmount = value
                if (!value) {
                    rule.message ='请输入单笔最高限额'
                    return false
                }
                let xnd = accMul(this.numbs,this.dPrice)
                //if ( value > this.numbs*prices) {
                if (value > xnd) {
                    rule.message ='最高限额不能超过最大额度'
                    return false
                }
                if (parseFloat(this.minAmount) && this.minAmount !==0 && parseFloat(value) < parseFloat(this.minAmount)) {
                    rule.message = '最高限额不能小于最低限额'
                    return false
                }
                let xadp = accMul(this.amounts.dMinQuantity,prices)
                //if (value < Number(this.amounts.dMinQuantity)*prices) {
                if (value < xadp) {
                    rule. message = '最高限额不能小于最小限额'
                    return false
                }
            },
            maxVal(value,rule){
                let prices = this.iIsMarketPrice===1 ? this.getMarketdPrice()(this.sToken) : this.dPrice
                this.maxAmount = value
                if (!value) {
                    //rule.message = '请输入单笔最高限额'
                    return false
                }
                let mxNP = accMul(this.numbs,prices)
                if (value > mxNP) {
                    rule. message = '最高限额不能超过广告总额'
                    return false
                }
                if (parseFloat(this.minAmount) && this.minAmount !==0 && parseFloat(value) < parseFloat(this.minAmount)) {
                    rule.message = '最高限额不能小于最低限额'
                    return false
                }
                let adp = accMul(this.amounts.dMinQuantity,prices)
                if (value < adp) {
                    rule.message = '最高限额不能小于最小限额'
                    return false
                }
            },
            addPay(){
                //this.$router.push('/pay/list');
                this.$refs.payType.init()
                //this.$refs.payType.showPay = true
                //this.$refs.payType.payList = this.payDictionarys
            },
            refData(){
                this.showPicker = false;
                this.init()
            },
            onConfirm(t) {
                if(t){
                    if(this.vUserModeId.length>=1){
                        this.showPicker = false;
                        this.payId = this.vUserModeId.map(i=> { return i.sItemDesc }).join('，')
                    }else{
                        this.$toast('请选择支付方式')
                    }
                }
            },
            cloesd(){
                console.log('vUserModeId')
                if(this.vUserModeId) return
                this.vUserModeId = []
            },
            onClickLeft() {
                this.$router.push('/ad/list');
            },
            onSubmit(values){
                this.loading = true
                let mId = this.vUserModeId.map(i=> { return i.iId })
                const params={
                    iCountryId:86,
                    sToken:this.sToken,
                    dPrice:this.iIsMarketPrice == 1 ? '0' : this.dPrice.toString(),
                    dMaxAmount:this.dMaxAmount.toString(),
                    dMinAmount:this.dMinAmount.toString(),
                    //dTradeAmount:(this.dTradeQuantity * this.dPrice).toString() ,
                    dTradeAmount: this.iIsMarketPrice == 1 ? accMul(this.dTradeQuantity, this.getMarketdPrice()(this.sToken)) : accMul(this.dTradeQuantity, this.dPrice),
                    iIsMarketPrice: this.iIsMarketPrice,
                    sUnit:'CNY',
                    iBlockType:1,
                    iSideType:this.iSideType,
                    iIscertificate:2,
                    iIsFloating:2,
                    dFloatingrate:'0',
                    iIsFastTrading:0,
                    sNickName:this.userName() === '' ? this.cellphone() : this.userName(),
                    sAvatar:'1',
                    sRemark:'',
                    vUserModeId:mId,
                    sDescription:this.sDescription ? this.sDescription:'',
                    sRealName:this.realName
                }
                params.dTradeAmount = ""+params.dTradeAmount+""
                console.log(params,this.dPrice,this.iIsMarketPrice)
                publishAds(params)
                .then(res=>{
                    this.$message.success('添加成功！')
                    this.$router.replace('/ad/list');
                    // getMyAdsList({iAdsId:0,sCoinCode:'USDT',iPageNo:this.current-1,iPageSize:this.pageSize})
                    //     .then(res=>{
                    //     this.myAdsList = res.data.vAdsData
                    //     })
                })
                .finally(()=>{
                    this.loading = false
                })
            },
            toggle(index) {
                this.$refs.checkboxes[index].toggle();
            },
            mPrice(){
                this.$refs.forms.resetValidation()
                this.iIsMarketPrice = this.tyoeFlag==0?1:0
                this.dMaxAmount = ''
                this.dMinAmount = ''
                this.dTradeQuantity = ''
            }
        },
    }
</script>

<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    .ads-add-list{
        background: #fff;
        padding-top: .28rem;
        .info-top,.info-cont{
            padding: 0 .28rem;
        }
        .border-top{
            border-top: 1px solid #e4e5e6;
            padding-top: 12px;
        }
        .border-bms{
            border-bottom: 1px solid #e4e5e6;
            padding-bottom: 12px;
            margin-bottom: .25rem;
        }
        .gflex{
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            -webkit-box-align: center;
            -webkit-align-items: center;
            align-items: center;
        }
        .box__hd{
            margin-right: 16px;
            width: 42px;
            height: 42px;
            background: #FFFFFF;
            border-radius: 50%;
            .avatar{
                width: 100%;
                border-radius: 50%;
            }
        }
        .box__bd{
            -webkit-box-flex: 1;
            -webkit-flex: 1;
            flex: 1;
            min-width: 0;
        }
        .box__dec{
            font-size: 14px;
            line-height: 1.4;
            padding: 0;
            margin: 0;
            padding-top: 4px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
        }
        .info-cont{
            .box__title{
                font-size: 12px;
                font-family: PingFang SC;
                font-weight: 400;
                color: #919DAF;
                i{
                    color: @gpay-color3D7EFF;
                    font-size: .36rem;
                    font-style: normal;
                    font-family: PingFangSC-Regular;
                    margin-left: .26rem;
                }
                span{
                    color: #212833;
                    font-size: .3rem;
                    margin-left: .26rem;
                }
            }
            .rbox{
                margin-right: 3px;
            }
            .mc-bnt{
                min-width: 120px;
                background: @gpay-bg3D7EFF;
                border-radius: 24px;
                padding: 3px 18px;
                font-size: 14px;
                font-family: PingFang SC;
                font-weight: 400;
                color: #FFFFFF;
                line-height: 36px;
            }
            .pay-type{
                width: 61px;
                img{
                    width: 20px;
                    margin-right: 8px;
                }
            }
        }
    }
    .f-cont{
        padding-bottom: 1rem;
    }
    .no-style{
        color:#e7e7e9;
    }
    .f-bnt{
        width:47%;
        display: inline-block;
        background: #FFFFFF;
        border: 1px solid #C8CBCE;
        color: #919DAF;
    }
    .f-bnt-true{
        width:47%;
        display: inline-block;
        margin-left:.4rem;
    }
    .npay{
        margin: .8rem 0;
        text-align: center;
    }
</style>
