<template>
    <div>
        <router-view/>
        <van-nav-bar
                :title="selKey==='1'?'初级认证':''"
                left-text="返回"
                class="header_title"
                @click-left="onClickLeft"
                safe-area-inset-top
                placeholder
                :fixed="fixed"
        />
        <div style="height: 1rem;"></div>
        <div v-if="certificateStatus === 0 || certificateStatus === 3" >
            <van-cell value="请选择地区类型" class="f_header"/>
            <div style="margin:16px; margin-bottom:30px">
                <van-row type="flex" justify="space-around">
                    <van-col>
                        <div class="imgs-box">
                            <p><van-image :src="require('../../assets/img/sfz.png')"/></p>
                        </div>
                        <!-- <van-row type="flex" class="card-box" justify="center">
                            <van-col>
                                <van-image :src="require('../../assets/img/sfz.png')"/>
                            </van-col>
                        </van-row> -->
                        <van-row class="card-box" type="flex" justify="center">
                            <van-col class="detail-info">中国大陆地区</van-col>
                        </van-row>
                        <van-row class="card-box" type="flex" justify="center" style="color: #666666">
                            <van-button type="info" round block @click="goAuthSetup">使用身份证进行验证</van-button>
                        </van-row>
                    </van-col>
                </van-row>
            </div>
            <div style="margin:16px; margin-bottom:30px">
                <van-row type="flex" justify="space-around">
                    <van-col>
                        <div class="imgs-box">
                            <p><van-image :src="require('../../assets/img/pass.png')"/></p>
                        </div>
                        <van-row class="card-box" type="flex" justify="center">
                            <van-col class="detail-info">其他国际及地区</van-col>
                        </van-row>
                        <van-row class="card-box" type="flex" justify="center" style="color: #666666">
                            <van-button type="info" round block @click="goAuthSetupf">使用护照进行验证</van-button>
                        </van-row>
                    </van-col>
                </van-row>
            </div>
        </div>
        <div v-if="certificateStatus === 2">
            <div style="height: 1rem;"></div>
            <div style="width: 100%;">
                <van-row type="flex" justify="space-around" >
                    <van-col>
                        <van-row type="flex" class="card-box" justify="center">
                            <van-col>
                                <van-icon name="passed" />
                            </van-col>
                        </van-row>
                        <div class="success-box">
                            恭喜您，已通过实名认证！
                        </div>
                    </van-col>
                </van-row>
            </div>
            <div style="margin: 16px;">
                <van-button round block type="info" @click="success">
                    完成
                </van-button>
            </div>
        </div>
        <div style="height: 1rem;"></div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {getMerchantTokens} from '@/api/wallet'
    import {udcUrl} from '@/api/udc'


    import bg_default from '../../assets/img/ic_asset_bg.png';
    import moment from 'dayjs'

    export default {
        name: 'auth',
        components: {
            [Toast.name]: Toast,
        },
        data() {
            return {
                type: '',
                selKey: '1',
                certificateStatus:0,
                fixed: true
            }
        },
        mounted() {

        },
        created() {
            this.type = this.$route.query.type;
            if (this.$route.query.type === '1') this.selKey = '1';
            if (this.$route.query.type === '2') this.selKey = '2';
            if (this.$route.query.type === '3') this.selKey = '3';
            this.certificateStatus = this.iCertificateStatus()
        },
        methods: {
            moment,
            ...mapGetters(['isLogin', 'userName', 'cellphone', 'email','iCertificateStatus']),
            ...mapActions(['GetUserExten']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            goAuthSetup(){
                this.$router.replace({name:"authsetup"});
            },
            goAuthSetupf(){
                this.$router.replace({name:"authsetupf"});
            },
            success() {
                this.$router.back(-1);
            }
        }
    }
</script>

<style lang="less" scoped>
    .f_header {
        font-weight: 500;
        font-size: 12px;
        margin-bottom: 5px;
        color: darkblue;
    }

    .card-box {
        margin-top: 20px;
        vertical-align: middle;
    }

    .user_header {
        position: relative;
        background-repeat: no-repeat;
        background-size: cover;
        height: 145px;
        box-sizing: border-box;
        text-align: left;
        color: #fff;
        padding-top: 30px;

        .text {
            margin-left: 20px;
        }

        .flex-type {
            margin-bottom: 20px;
        }
    }

    .fin_list {
        position: relative;
        text-align: left;
        background-color: white;
        margin-left: 20px;
        margin-bottom: 20px;
    }

    i.user_set {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 24px;
    }

    .user_avatar {
        margin-bottom: 10px;

        img {
            border: 0;
            border-radius: 50%;
        }
    }
    .success-box{
        font-size: .34rem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #212833;
        text-align: center;
        p{
            font-size: .3rem;
            margin-top: .2rem;
        }
    }
    /deep/.van-icon-passed{
        font-size: 1rem;
        color: @gpay-color3D7EFF;
        margin-top: .3rem;
    }
    .imgs-box{
        p{
            border: 1px rgb(233, 238, 245) dashed;
            border-radius: 5px;
            width: 285px;
            height: 165px;
            color: #c5c9d0;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
</style>
