                <!--                                您需要在-->
                <!--                                <div class="times">-->
                <!--                                    {{overtime}}-->
                <!--                                </div>-->
                <!--                                时间内向对方转账-->
                请您尽快向对方转账
                <span class="time-amount">{{Number(orderDetail.dTradeAmount).toFixed(2)}}</span> {{orderDetail.sUnit}}
                <span>，长时间未支付系统将取消您的订单，并影响您的信用!</span>
              </div>
            </div>
            <div class="order-time tpad" v-else>若买家未在 <span class="time-amount">{{overtime}}</span> 分钟内确认支付，订单将自动取消</div>
          </div>

          <div v-if="orderDetail.iOrderState ===2" class="tpad">
            <div class="order-time" v-if="orderDetail.iOrderType === 2">
