            <!-- </span> -->
          </div>
        </div>
      </div>
      <div v-if="orderDetailFlag" class="detail-box">
        <div class="detail-cont">
          <van-row type="flex" class="buy-nav" justify="left">
            <van-cell :class="{heds:true,}" title="订单类型" :value="orderDetail.iOrderType === 2?'充值订单':'提币订单'">
              <!--                            <template #icon>-->
