                          </span>
            </template>
          </van-cell>
          <van-cell title="总价" :value="Number(orderDetail.dTradeAmount).toFixed(2) + ' ' + orderDetail.sUnit" />
          <van-cell title="数量" :value="Number(orderDetail.dTradeQuantity).toFixed(4) + ' ' + orderDetail.sToken" />
          <van-cell title="单价" :value="Number(orderDetail.dPrice).toFixed(2) + ' ' + orderDetail.sUnit" />
          <!--                    <template v-if="orderDetail.iMerchantOrder === 2">-->
          <!--&lt;!&ndash;                        <van-cell title="Taker手续费" class="amout" v-if="!isPublisher && Number(orderDetail.dFeeRate)>0" :value="Number(orderDetail.dFeeRate)*100 +'%'" />&ndash;&gt;-->
          <!--                        <template :span="4" v-if="!isPublisher && Number(orderDetail.dFeeRate)>0">-->
