          <template v-if="orderDetail.iMerchantOrder === 1 ">
            <van-cell title="奖励费率" class="amout" :value="((Number(orderDetail.dAcceptanceReward))/(Number(orderDetail.dTradeQuantity))  * 100).toFixed(2) +' %'" />
            <van-cell title="奖励金额" class="amout" :value="(Number(orderDetail.dAcceptanceReward)) + ' '+ orderDetail.sToken" />
