          <van-cell title="订单时间" :value="moment(orderDetail.iCreateTime).format('YYYY-MM-DD HH:mm:ss')" />
          <!--                    <van-cell :title="orderDetail.iOrderType === 2 ? '卖家昵称' :'买家昵称'" :value="orderDetail.iOrderType === 2 ? orderDetail.sSellNickName : orderDetail.sBuyNickName " />-->
          <van-cell v-if="orderDetail.iOrderType === 2" title="支付账户名称" :value="orderDetail.sBuyRealName " />
          <!--                    <van-cell center title="支付方式" v-if="false">-->
          <!--                        <template>-->
          <!-- <span v-for="item in payModeList" :key="item.iId" style="margin-right:5px">
              <span v-if="item.iPayDictId==13"><img width="18" src="../../assets/img/zfb.png"> 支付宝</span>
              <span v-if="item.iPayDictId==14"><img width="18" src="../../assets/img/wx.png"> 微信</span>
              <span v-if="item.iPayDictId==15"><img width="18" src="../../assets/img/yhk.png"> 银行卡</span> -->
          <!-- {{payDictionarys.find(i=>i.iId === item.iPayDictId).sItemDesc}} -->
          <!-- </span>  -->
          <!--                        </template>-->
          <!--                    </van-cell>-->
          <van-cell v-if="!isTrading" center title="支付方式" value="双方不在交易中，不能查看支付方式" label=""></van-cell>
          <template v-if="orderDetail.iOrderType === 1">
            <van-cell title="银行账号" >
              <span>{{orderDetail.sSellAccount}} </span>
              <a v-clipboard:copy="orderDetail.sSellAccount" v-clipboard:success="()=>this.$toast('银行账号已复制到剪切板')" v-clipboard:error="()=>this.$toast('复制失败')"><img width="11" src="../../assets/img/copy.png"></a>
            </van-cell>
            <van-cell title="账户名"  :value="orderDetail.sSellRealName" >
              <span>{{orderDetail.sSellRealName}} </span>
              <a v-clipboard:copy="orderDetail.sSellRealName" v-clipboard:success="()=>this.$toast('账户名已复制到剪切板')" v-clipboard:error="()=>this.$toast('复制失败')"><img width="11" src="../../assets/img/copy.png"></a>
            </van-cell>
            <van-cell title="银行名"  :value="orderDetail.sBankName" >
              <span>{{orderDetail.sBankName}} </span>
              <a v-clipboard:copy="orderDetail.sBankName" v-clipboard:success="()=>this.$toast('银行名已复制到剪切板')" v-clipboard:error="()=>this.$toast('复制失败')"><img width="11" src="../../assets/img/copy.png"></a>

            </van-cell>
            <van-cell title="银行支行"  :value="orderDetail.sSubbranchBankName" >
              <span>{{orderDetail.sSubbranchBankName}} </span>
              <a v-clipboard:copy="orderDetail.sSubbranchBankName" v-clipboard:success="()=>this.$toast('银行支行已复制到剪切板')" v-clipboard:error="()=>this.$toast('复制失败')"><img width="11" src="../../assets/img/copy.png"></a>
            </van-cell>
            <van-cell title="转账凭证" >
              <van-button v-if="orderDetail.iOrderType===1 && (orderDetail.iOrderState ===1 || orderDetail.iOrderState ===3)" style="width:40%"  round @click="showTakePhoto(1)" type="info">上传</van-button>
            </van-cell>
            <van-cell v-if="sProofPath!=null&&sProofPath!=''">
              <van-uploader :file-list="[{url:sProofPath}]" :deletable="false" :show-upload="false">
<!--                <div v-show="false"></div>-->
              </van-uploader>
<!--              <van-image style="height:200px" :src="sProofPath"/>-->
            </van-cell>
          </template>





          <!--                    <van-cell center title="卖家支付方式" v-else-if="orderDetail.iOrderType === 1" label="" class="pay-cell">-->
          <!--                        <template #label>-->
          <!--                            <ul class="pay-info pay-bord" v-for="item in payModeList" :key="item.iId">-->
          <!--                                <li>-->
          <!--                                    <div class="type">支付类型：</div>-->
          <!--                                    <div class="cont">-->
          <!--                                        <span v-if="item.iPayDictId==13"><img width="18" src="../../assets/img/zfb.png"> 支付宝</span>-->
          <!--                                        <span v-if="item.iPayDictId==14"><img width="18" src="../../assets/img/wx.png"> 微信</span>-->
          <!--                                        <span v-if="item.iPayDictId==15"><img width="18" src="../../assets/img/yhk.png"> 银行卡</span>-->
          <!--                                    </div>-->
          <!--                                </li>-->
          <!--                                <li>-->
          <!--                                    <div class="type">账号名：</div>-->
          <!--                                    <div class="cont">{{item.sName}}</div>-->
          <!--                                </li>-->
          <!--                                <li>-->
          <!--                                    <div class="type">账号：</div>-->
          <!--                                    <div class="cont">{{item.sAccount}}</div>-->
          <!--                                </li>-->
          <!--                                <li v-if="item.iPayDictId==15">-->
          <!--                                    <div class="type">银行信息：</div>-->
          <!--                                    <div class="cont" style="margin-left: 10px;">{{item.sBankName}} {{item.sSubbranchBankName}}</div>-->
          <!--                                </li>-->
          <!--                                <li v-if="item.sAttachmentUrl !==''">-->
          <!--                                    <div class="type">二维码：</div>-->
          <!--                                    <div class="cont">-->
          <!--                                        <van-image style="width:60px;height:60px;margin:0 10px;" :src="item.sAttachmentUrl" @click="preview(item.sAttachmentUrl,0)">-->
          <!--                                            <template v-slot:loading>-->
          <!--                                                <van-loading type="spinner" size="20" />-->
          <!--                                            </template>-->
          <!--                                        </van-image>-->
          <!--                                    </div>-->
          <!--                                </li>-->
          <!--                            </ul>-->
          <!--                        </template>-->
          <!--                    </van-cell>-->
          <van-cell center>
            <template>
              <a class="bnt-warp" round @click="tell"><van-icon name="phone-circle-o" /> 联系客服</a>
              <div class="bnt-warp txt-right">
                <a v-if="orderFlag" @click="appeal()"><van-icon name="info-o" /> {{orderDetail.iArbitState >0?'申诉补充':'我要申诉'}}</a>
                <a style="margin-left:26px;color:#FF5763" @click="$router.push('/appeal/list?orderId='+orderId+'')" v-if="orderDetail.iArbitState>0">申诉历史</a>
              </div>
            </template>
          </van-cell>
          <!-- <a v-if="orderFlag" class="appeal-right" @click="appeal()"><van-icon name="info-o" />{{}}</a> -->
        </div>
        <!--订单按钮状态-->
        <div class="buy-foot-bnt">

                    <span v-if="orderDetail.iOrderState ===1 ">
                      <span v-if="isTrading && orderDetail.iOrderType === 1">
                        <van-button class="bnt" :class="canStyle?'wval':''" flat @click="cancelOrder" round>取消接单</van-button>
                      </span>
                        <van-button class="bnt" round @click="pay" type="info" v-if="orderDetail.iOrderType === 1">我已支付,申请放币</van-button>
                      <!--                        <div v-else-if="orderDetail.iMerchantOrder === 2">-->
                      <!--                            <van-button style="width:60%" class="bnt" round disabled>等待买家支付</van-button>-->
                      <!--                        </div>-->
                      <!--                        <van-button style="width:60%" class="bnt" color="#2EBC85" round v-else @click="drawal" type="info">我已收款,给买家放币</van-button>-->
                    </span>
          <span v-if="orderDetail.iOrderState ===2">
                        <van-button round v-if="orderDetail.iOrderType === 1" disabled>已支付，等待卖家放币</van-button>
                        <van-button style="width:60%" color="#2EBC85" round v-else @click="drawal" type="info">我已收款,给买家放币</van-button>
                    </span>
          <span v-if="orderDetail.iOrderState ===3 && orderDetail.iArbitState !== 10">
                        <van-button style="width:60%" color="#2EBC85" v-if="orderDetail.iOrderType === 2" round @click="drawal" type="info">我已收款,给买家放币</van-button>
                    </span>
          <span v-if="(orderDetail.iOrderState ===1 || orderDetail.iOrderState ===4 || orderDetail.iOrderState ===12) && orderDetail.iOrderType === 2">
                        <van-button @click="drawal" round type="info" color="#2EBC85" style="width:60%" >我已收款,给买家放币</van-button>
                    </span>

        </div>
      </div>
    </section>
    <van-popup v-model="visible"
               closeable
               round
               title="安全验证"
               :close-on-click-overlay ="closeOnClickOverlay"
               close-icon-position="top-right"
               position="bottom"
    >
      <div class="aq-title">安全验证</div>
      <div style="font-size: 12px;margin-left: 16px;margin-right: 16px;color: #FF5763;">请确认您已经收到了与对方实名一致的账户转账，一旦放币完成，资产将直接转至对方账户，无法退回！</div>
      <van-field style="margin-top: 20px"
                 v-model="password"
                 center
                 clearable
                 required
                 label="资金密码"
                 type="password"
                 :maxlength="20"
                 placeholder="请输入资金密码"
      >
      </van-field>
      <div style="margin: 36px 16px; 0 16px">
        <van-button round block type="info"  @click="handleOk">
          放币确认
        </van-button>
      </div>
    </van-popup>
    <tell-model ref="tellModel"></tell-model>
    <van-image-preview v-model="codeShow" :start-position="0" :images="images" @change="onChange">
      <!-- <template v-slot:index>第{{ index }}页</template> -->
    </van-image-preview>
    <van-action-sheet
        v-model="show"
        cancel-text="取消"
        close-on-click-action
        :actions="actions"
        @select="takePhoto"
    />
  </div>
</template>

<script>
import {getOrderDetail, getShareOrderDetail,confirmPaid,confirmShareOrder, getPayDictionary,releaseCoin,releaseShareOrder,cancelOTCOrder,getAdsReward,cancelShareOrder,dropShareOrder} from '@/api/otc'
import {mapActions} from 'vuex'
import {ossUrl, getSignedUrl} from '@/api/oss'
import moment from 'dayjs'
import TellModel from "@/components/tell"
import LoadingModal from "@/components/LoadingModal/index";
const file_url = process.env.VUE_APP_FILE_API_URL
export default {
  name: "Orderdetail",
  components: {
    TellModel,LoadingModal
  },
  data() {
    return {
      ossUrl,
      closeOnClickOverlay:false,
      visible:false,
      orderId:'',
      password:'',
      orderDetail:{},
      payModeList:[],
      fixed:true,
      payDictionarys:[],
      isPublisher:null,
      overtime:'',
      isOverTime:null,
      active: 0,
      adsReward:0,
      merchantFee:0,
      getOrderDetailInterval:null,
      iCurrentTime:0,
      iExpireTime:0,
      orderDetailFlag: false,
      codeShow: false,
      images: [],
      // sProof:'/user/20210322/f4c2f1dce9722c0cf681cab9c1751793.png',
      sProof:null,
      sProofPath:null,
      showPhotoType: 1,
      show: false,
      showPicker: false,
      ssk: '',
      actions: [
        {name: '从相册选择'},
        {name: '拍照'},
      ],
      upImgLoading: false,
    };
  },
  computed:{
    isTrading(){
      if(this.orderDetail){
        switch (this.orderDetail.iOrderState) {
          case 1: case 2: case 3: case 4:
            return true
          default :
            return  false
        }
      }
    },
    // orderType(){
    //     if(this.orderDetail){
    //         if(!this.isPublisher && this.orderDetail.iOrderType === 2)return 'buy'
    //         if(!this.isPublisher && this.orderDetail.iOrderType === 1)return 'sell'
    //         if(this.isPublisher && this.orderDetail.iOrderType === 2)return 'sell'
    //         if(this.isPublisher && this.orderDetail.iOrderType === 1)return 'buy'
    //     }else{
    //         return ''
    //     }
    // },
    styleObj(){
      let style = {type:'',hedStyle:''},orderDetail = this.orderDetail;
      if(orderDetail){
        if(orderDetail.iOrderType == 2){
          style.type = 'sell-box'
        }else{
          style.type = 'buy-box'
        }
        // 订单
        if(orderDetail.iOrderState ===11 || orderDetail.iOrderState ===12){
          style.hedStyle = 'order-cancel-close'
        }
      }
      return style
    },
    canStyle(){
      if(this.orderDetail){
        return this.orderDetail.iOrderState===3||this.orderDetail.iOrderState===4?true:false
      }
    },
    orderFlag(){
      if(this.orderDetail){
        switch (this.orderDetail.iOrderState) {
          case 2: case 3: case 4: case 10: case 11: case 12:
            return true
          default :
            return  false
        }
      }
    }
  },
  created(){
    this.ssk = this.$ls.get('SSK')
    this.orderId = this.$route.query.id
    getShareOrderDetail({sOrderId:this.orderId})
        .then(res=>{
          var that = this;
          this.orderDetail = res.data.stOrderData;
          this.iCurrentTime = res.data.iCurrentTime;
          this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime;
          this.payModeList = this.orderDetail.iMerchantOrder ===1 ? res.data.vUsedList : res.data.vAdsModeList;
          if(this.orderDetail.sProof && this.orderDetail.sProof !==''){
            getSignedUrl(this.orderDetail.sProof).then(res => that.sProofPath = res);
          }
          // this.sProofPath = this.orderDetail.sProof
          // this.isPublisher = res.data.iIsPublisher === 1 ? true : false
          this.isPublisher =  true
          this.overTimeInterval()

          if(this.orderDetail.iMerchantOrder === 1 ){
            getAdsReward({iIdle:0})
                .then(res=>{
                  // debugger
                  this.adsReward = Number(res.data.sBasicReward)
                })
          }
          // if(this.orderDetail.iMerchantOrder === 1 && !this.isPublisher){
          //     getMerchantFee({iIdle:0})
          //         .then(res=>{
          //         this.merchantFee = Number(res.data.sBasicFee)
          //         })
          // }
          this.getOrderDetailInterval = setInterval(()=>{
            if(this.isTrading){
              getShareOrderDetail({sOrderId:this.orderId})
                  .then(res=>{
                    // debugger
                    this.orderDetail = res.data.stOrderData
                    this.iCurrentTime = res.data.iCurrentTime
                    this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                    this.payModeList = this.orderDetail.iMerchantOrder ===1 ? res.data.vUsedList : res.data.vAdsModeList
                    this.isPublisher = res.data.iIsPublisher === 1 ? true : false
                  })
            }
          },3000)
          this.orderDetailFlag = true
        }).catch((err) =>{
      console.log(err,'-----')
    })
    // getPayDictionary({iDictType:3})
    //     .then((res)=>{
    //     this.payDictionarys = res.data.vPayDictionary
    //     })
  },
  activated: function () {
    this.$setgoindex()
  },
  methods: {
    ...mapActions(['Login','GetUserExten','GetUserInfo']),
    moment,
    drawal(){
      this.visible = true
    },
    tell(){
      this.$refs.tellModel.init()
    },
    onClickLeft() {
      if (this.$route.query.goindex === 'true') {
        this.$router.push('/');
      } else {
        this.$router.back(-1);
      }
    },
    overTimeInterval(){
      if(this.iExpireTime <= 0){
        this.isOverTime = true
        getShareOrderDetail({sOrderId:this.orderId})
            .then(res=>{
              this.orderDetail = res.data.stOrderData
              this.iCurrentTime = res.data.iCurrentTime
              this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
            })
        return
      }
      // debugger
      let m = parseInt(this.iExpireTime/60000)
      let s = (parseInt((this.iExpireTime%60000)/1000)).toString()
      if(s.length === 1)s ='0' + s
      this.overtime =  m + ' : ' + s
      this.iExpireTime -=1000
      setTimeout(()=>this.overTimeInterval(),1000)
    },
    initState(state){
      switch (state) {
        case 1:
          return '未支付'
        case 2:
          return '已支付 等待放币'
        case 3:
          return '申诉中'
        case 4:
          return '放币超时'
        case 10:
          return '已完成'
        case 11:
          return '已取消'
        case 12:
          return '支付超时取消'
      }
    },
    showTrading(){
      if(this.orderDetail){
        switch (this.orderDetail.iOrderState) {
          case 1:
            return '请支付'
          case 2:
            return '订单已支付'
          case 3:
            return '订单申诉中'
          case 4:
            return '已完成'
          case 5:
            return '不在交易中，不能查看支付方式'
          case 6:
            return '不在交易中，不能查看支付方式'
          default :
            return  ''
        }
      }
    },
    pay(){
      if(!this.sProof || this.sProof ===''){
        this.$toast('未上传转账凭证！')
        return
      }
      this.$dialog.confirm({
        title: '确认支付',
        message: '确认支付后将通知卖家放币，请您确认已经按照要求支付了相应的款项',
        messageAlign: 'left',
        confirmButtonText:'我已确认',
        confirmButtonColor: '#3D7EFF',
        cancelButtonText:'我再想想'
      }).then(() =>{
        confirmShareOrder({
          sOrderId:this.orderId,
          sProof:this.sProof
        })
            .then(res=>{
              this.$message.success('支付成功！')
              getShareOrderDetail({sOrderId:this.orderId})
                  .then(res=>{
                    // debugger
                    this.orderDetail = res.data.stOrderData
                    this.iCurrentTime = res.data.iCurrentTime
                    this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                    let m = parseInt(this.iExpireTime/60000)
                    let s = (parseInt((this.iExpireTime%60000)/1000)).toString()
                    if(s.length === 1)s ='0' + s
                    this.overtime =  m + ' : ' + s
                    this.payModeList = this.orderDetail.iMerchantOrder ===1 ? res.data.vUsedList : res.data.vAdsModeList
                    this.isPublisher = res.data.iIsPublisher === 1 ? true : false
                  })
            })
      }).catch(() =>{

      })
    },
    cancelOrder(){
      this.$dialog.confirm({
        title: '取消接单',
        message: '取消后将不会退款，如果已经付款请不要取消订单',
        messageAlign: 'left',
        confirmButtonText:'确认取消',
        cancelButtonText:'我再想想'
      })
          .then(() => {
            dropShareOrder({sOrderId:this.orderId})
                .then(res=>{
                  this.$toast('取消成功！')
                  this.$router.go(-1)
                })
          })
          .catch(() => {
          });
    },
    handleOk(){
      if(!this.password || this.password ===''){
        this.$toast('请输入资金密码')
      }else{
        this.loading = true
        const params = {
          sOrderId:this.orderId,
          sDrawalPassword:this.password
        }
        releaseShareOrder(params)
            .then(res=>{
              this.visible = false
              this.$toast('提交成功！')
              getShareOrderDetail({sOrderId:this.orderId})
                  .then(res=>{
                    // debugger
                    this.orderDetail = res.data.stOrderData
                    this.iCurrentTime = res.data.iCurrentTime
                    this.iExpireTime = this.orderDetail.iExpireTime - this.iCurrentTime
                    this.payModeList = this.orderDetail.iMerchantOrder ===1 ? res.data.vUsedList : res.data.vAdsModeList
                    this.isPublisher = res.data.iIsPublisher === 1 ? true : false
                  })
            })
            .finally(()=>{
              this.loading = false
            })
      }
    },
    appeal(){
      let iArbitState = this.orderDetail.iArbitState >0?2:1
      this.$router.push('/appeal?orderId='+this.orderId+'&iOrderType='+this.orderDetail.iOrderType+'&iType='+iArbitState+'')
    },
    preview(arr,index){
      this.codeShow = true
      this.images = [arr]
    },
    onChange(index) {
      this.index = index;
    },
    showTakePhoto(type) {
      this.show = true;
      this.showPhotoType = type;
    },
    takePhoto(action, index) {
      var that = this;

      function setOptions(srcType) {
        var options = {
          // Some common settings are 20, 50, and 100
          quality: 80,
          destinationType: Camera.DestinationType.FILE_URI,
          // In this app, dynamically set the picture source, Camera or photo gallery
          sourceType: srcType,
          encodingType: Camera.EncodingType.JPEG,
          mediaType: Camera.MediaType.PICTURE,
          correctOrientation: true  //Corrects Android orientation quirks
        }
        return options;
      }

      if (index === 0) {
        let srcType = Camera.PictureSourceType.PHOTOLIBRARY;//选照片
        let options = setOptions(srcType);
        navigator.camera.getPicture(function cameraSuccess(imageUri) {
          that.fileUpload(imageUri)

        }, function cameraError(error) {
          console.debug("Unable to obtain picture: " + error, "app");

        }, options);
      }
      if (index === 1) {
        let srcType = Camera.PictureSourceType.CAMERA;//拍照
        let options = setOptions(srcType);
        navigator.camera.getPicture(function cameraSuccess(imageUri) {
          that.fileUpload(imageUri)

        }, function cameraError(error) {
          console.debug("Unable to obtain picture: " + error, "app");

        }, options);
      }
    },

    // 使用cordova FileUpload上传图片
    fileUpload: function (imageURI) {
      function getCaption(obj, state) {
        var index = obj.lastIndexOf("?");
        if (index > 0) {
          if (state == 0) {
            obj = obj.substring(0, index);
          } else {
            obj = obj.substring(index + 1, obj.length);
          }
          return obj;
        } else {
          return obj;
        }
      }

      imageURI = getCaption(imageURI, 0)

      let that = this
      that.upImgLoading = true
      let FileUploadOptions = window.FileUploadOptions
      let FileTransfer = window.FileTransfer
      let options = new FileUploadOptions()
      options.fileKey = 'file'
      let path = imageURI.substr(imageURI.lastIndexOf('/') + 1);
      options.fileName = path
      options.mimeType = 'image/jpeg'
      let headers = {'Authorization': ' ' + that.ssk}
      options.headers = headers;
      let ft = new FileTransfer()
      ft.upload(imageURI, encodeURI(this.ossUrl.PrivateUpload), async function (data) {

        function wait(ms) {
          return new Promise(resolve => setTimeout(() => resolve(), ms));
        };

        let resString = data.response
        let resObject = JSON.parse(resString) // 字符串转对象
        if (resObject.iRspCode === 0) {
          let vUrlPath = resObject.data.vUrlPath[0];
          getSignedUrl(vUrlPath).then(res => that.sProofPath = res)
          that.sProof = resObject.data.vUrlPath[0];
          that.upImgLoading = false
          //alert(that.sFamilyRegister)
        } else {
          //that.$toast(resObject.msg)
          that.$toast.fail('上传出错稍后重试！')
          that.upImgLoading = false
        }
      }, function (err) {
        console.log(err)
        // alert(JSON.stringify(err))
        that.upImgLoading = false
        that.$toast.fail('上传出错稍后重试！')
      }, options)
    },
  },
  beforeDestroy() {
    clearInterval(this.getOrderDetailInterval)
  },
  watch: {
    upImgLoading(v) {
      if (v) {
        console.log('22', v)
        document.body.style.overflow = 'hidden'
        document.body.removeEventListener('touchmove', this.stopTouch, {capture: true})
      } else {
        document.body.removeEventListener('touchmove', this.stopTouch, {capture: true})
        document.body.style = ''
      }
    },
  }
}
</script>

