<template>
    <div>
        <van-action-sheet
            v-model="show"
            title="联系客服"
            close-on-click-action
            @cancel="onCancel"
        >
            <template>
                <van-cell :title="tell.sChannel+'：'+tell.sValue" v-for="(tell,index) in actions" :key="index" />
            </template>
        </van-action-sheet>
    </div>
</template>

<script>
    import {getCustomServant} from "@/api/udc";
    export default {
        name: 'user-menu',
        data(){
            return{
                show: false,
                actions: []
            }
        },
        created(){
            
        },
        methods:{
            onCancel() {
                console.log('取消');
            },
            init(){
                this.show = true
                getCustomServant({iIdle:0})
                .then(res=>{
                    let data = res.data.vList
                    this.actions = res.data.vList
                })
            }
        }
    };

</script>

<style lang="scss" scoped>
    /deep/ .van-cell__left-icon{
        font-size: .42rem;
        margin-right: .16rem;
        .van-icon__image{
            vertical-align: inherit;
            width: 0.32rem;
            height: 0.32rem;
        }
    }
</style>
