/**
 * 新增修改完成调用 modalFormOk方法 编辑弹框组件ref定义为modalForm
 * 高级查询按钮调用 superQuery方法  高级查询组件ref定义为superQueryModal
 * data中url定义 list为查询列表  delete为删除单条记录  deleteBatch为批量删除
 */
import { filterObj } from '@/utils/util';
import { postAction } from '@/api/manage'
import { mapState } from 'vuex'
import { Notify } from 'vant';

export const GpayListMixin = {
	data() {
		return {
			//token header
			// tokenHeader: {'X-Access-Token': Vue.ls.get(ACCESS_TOKEN)},
			/* 查询条件-请不要在queryParam中声明非字符串值的属性 */
			//设置返回数据result中用户列表数据的字段名
			resultName: '',
			queryParam: {},
			/* 数据源 */
			dataSource: [],
			/* 分页参数 */
			ipagination: {
				current: 0,
				pageSize: 10,
				// pageSizeOptions: ['10', '20', '30', '50', '100'],//移动端没有这个
				showTotal: (total, range) => {
					return range[0] + "-" + range[1] + " 共" + total + "条"
				},
				showQuickJumper: true,
				showSizeChanger: true,
				total: 0
			},
			/* 排序参数 */
			isorter: {
				column: 'iCreateTime',
				order: 'desc',
			},
			/* 筛选参数 */
			filters: {},
			refreshing: false,
			/* table加载状态 */
			loading: false,
			finished: false,
			/* 所有是否加载 */
			loadedall: false,
			/* table选中keys*/
			selectedRowKeys: [],
			/* table选中records*/
			selectionRows: [],
			/* 查询折叠 */
			toggleSearchStatus: false,
			/* 高级查询条件生效状态 */
			superQueryFlag: false,
			/* 高级查询条件 */
			superQueryParams: '',
			/** 高级查询拼接方式 */
			superQueryMatchType: 'and',
			/** 数据请求错误 */
			error: false,
			/** 数据加载状态 */
			dataEmpty: false
		}
	},
	computed: {
		...mapState(["isLoading"]),
	},
	created() {
		if (!this.disableMixinCreated) {
			console.log('执行混入')
		}
	},
	methods: {
		loadData() {
			if (!this.url.list) {
				this.$toast.fail("请设置url.list属性!")
				return
			}
			if (this.loadedall) {
				console.log('已经加载到最后了')
				return
			}
			//加载数据 若传入参数1则加载第一页的内容
			this.ipagination.current = this.ipagination.current + 1;
			console.log('loadDataloadData')
			let params = this.getQueryParams();//查询条件
			postAction(this.url.list, params).then((res) => {
				if (res.iRspCode === 0) {
					console.log('loadDataloadData------1')
					let data = res.data[this.resultName]
					if (data && data.length > 0) {
						console.log('loadDataloadData------2')

						this.dataSource = this.dataSource.concat(res.data[this.resultName])

						// 特殊情况
						if (this.dataSource.length < this.ipagination.pageSize) {
							console.log('loadDataloadData------5---数据加载完成')
							this.loadedall = true;
						}
					} else {
						console.log('loadDataloadData------5---数据加载完成')
						this.loadedall = true;
					}
				} else {
					this.error = false
					//this.$toast.fail(res.message)
				}
			}).catch(err => {
				console.log(err)
				this.error = false
				this.loading = false;
				this.refreshing = false;
				this.loadedall = true;
			}).finally(() => {
				this.loading = false;
				this.refreshing = false;
				console.log('finally')
			})
		},
		refreshData() {
			if(window.networkFlag&&window.networkFlag==2){
				this.loadedall = true; 
				this.refreshing = false; 
				this.loading = false;
				//Notify({ type: 'warning', message: '当前无网络，请检查网络连接' });
				return
			}
			this.ipagination.current = 0;
			// 清空列表数据
			this.loadedall = false;
			this.dataSource = []
			// 重新加载数据
			// 将 loading 设置为 true，表示处于加载状态
			this.loading = true;
			this.refreshing = false
			this.loadData();
		},
		initParam() { },
		getQueryParams() {
			//获取查询条件
			let sqp = {}
			if (this.superQueryParams) {
				sqp['superQueryParams'] = encodeURI(this.superQueryParams)
				sqp['superQueryMatchType'] = this.superQueryMatchType
			}
			let param = Object.assign(sqp, this.queryParam, this.isorter, this.filters);
			param.iPageNo = this.ipagination.current - 1;
			param.iPageSize = this.ipagination.pageSize;
			param.sOrder = param.order;
			param.sColumn = param.column;
			delete param.order
			delete param.column
			return filterObj(param);
		},
	}
}
