<template>
    <div>
        <van-nav-bar
            title="佣金记录"
            left-text=""
            class="header_title"
            @click-left="onClickLeft"
            safe-area-inset-top
            placeholder
            left-arrow
            :fixed="true"
        />
        
        <div class="ads-list-cont">
            <!-- <van-tabs v-model="sToken">
                <van-tab v-for="token in tokenNames" :key="token.sToken" :title="token.sToken" :name="token.sToken"></van-tab>
            </van-tabs> -->
            <van-dropdown-menu :active-color="'#3D7EFF'">
<!--                <van-dropdown-item @change="droSToken" v-model="sToken" :options="tokenNames" />-->
                <van-dropdown-item @change="droType" v-model="iBusiType" :options="depositList" />
                <van-dropdown-item :title="date" @open="show = true">
                    <!-- <van-cell title="选择日期区间" :value="date" @click="show = true" /> -->
                </van-dropdown-item>
            </van-dropdown-menu>
            <van-calendar v-model="show" :show-mark="false" :color="'#3D7EFF'" :min-date="new Date('2021-01-01')" :default-date="[new Date('2021-01-01')]" type="range" @confirm="onConfirm" />
            <van-empty description="您还没有财务记录" :image="require('../../assets/img/empty-image-default.png')" v-if="dataSource.length==0&&loadedall" />
            <van-pull-refresh v-else v-model="refreshing" style="min-height: 66vh;margin-top: 20px;" @refresh="refreshData">
                <van-list
                    v-model="loading"
                    :finished="loadedall"
                    finished-text="没有更多了"
                    @load="loadData"
                    :border="true"
                >
                        
                    <div class="warp-box">
                        <div class="ads-list" v-for="(item,index) in dataSource" :key="index">
                            <div class="info-cont gflex mbmt">
                                <div style="width:60px">
                                    <div class="box__title">币种:</div>
                                </div>
                                <div class="right-cont color-r">{{ item.sToken }}</div>
                            </div>
                            <div class="info-cont gflex mbmt">
                                <div style="width:60px">
                                    <div class="box__title">流水类型: </div>
                                </div>
                                <div class="right-cont">{{ibus(item.iBusiType)}}</div>
                            </div>
                            <div class="info-cont gflex mbmt">
                                <div style="width:60px">
                                    <div class="box__title">资产总额: </div>
                                </div>
                                <div class="right-cont">{{ item | amountZ}}</div>
                            </div>
                            <div class="info-cont gflex mbmt">
                                <div style="width:60px">
                                    <div class="box__title">变化金额: </div>
                                </div>
                                <div class="right-cont" :class="styles(item)?'red':'green'">{{ initChangeVal(item) }}</div>
                            </div>
                            <div class="info-cont gflex mbmt">
                                <div style="width:60px">
                                    <div class="box__title">记录时间: </div>
                                </div>
                                <div class="right-cont">{{moment(item.iCreateTime).format('YYYY-MM-DD HH:mm:ss')}}</div>
                            </div>
                        </div>
                    </div>
                </van-list>
            </van-pull-refresh>
        </div>
        <add-modal ref="add-modal" @aplOK="setOK" />
    </div>
</template>

<script>
    import {udcUrl} from '@/api/udc'
    import {getMerchantTokens} from '@/api/wallet'
    import {mapGetters} from 'vuex'
    import moment from 'dayjs'
    import {GpayListMixin} from '@/mixins/GpayListMixin'
    import AddModal from "@/components/wallet/addModal";

    export default {
        name: "dealRecords",
        mixins: [GpayListMixin],
        components: {
            AddModal
        },
        data() {
            return {
                moment,
                myAdsList: [],
                myAdsParam: {},
                payDictionarys: [],
                iSideType: 1,
                iBlockType: 1,
                activeSCoinCode: 'USDT',
                resultName: 'vList',
                iAcctType:0,
                depositList:[{
                    text: '全部类型',
                    value: 0
                },
                // {
                //   text: '追加保证金',
                //   value: 101
                // },
                // {
                //   text: '解冻保证金',
                //   value: 102
                // },
                // {
                //     text: '充值',
                //     value: 1
                // },
                  {
                    text: '提取佣金',
                    value: 2
                },
                //   {
                //     text: '提现订单',
                //     value: 3
                // },{
                //     text: '充值订单',
                //     value: 4
                // },
                  {
                    text: '佣金发放',
                    value: 103
                }
                ],
                queryParam: {
                    "iPageNo":0,
                    "iPageSize":10,
                    iAcctType:1,
                    // "sToken":"",
                    // "iBusiType":0,
                    // "iCreateTime_Begin":'',
                    // "iCreateTime_End":''
                },
                url: {
                    list: udcUrl.GetUserJournalList,
                },
                sToken: 0,
                tokenNames:[],
                iBusiType: 0,
                date: '时间筛选',
                show: false,
            };
        },
        created() {
            //查询数据
            //this.initParam();
            //this.refreshData();
            // getMerchantTokens({iIdle:0})
            // .then(res=>{
            //     this.tokenNames = [{ text: '全部', value: 0 }]
            //     this.tokens = res.data.vTokens
            //     this.tokens.forEach(token =>{
            //         if(this.tokenNames.findIndex(t=> t.text === token.sToken)<0) {
            //             console.log(token)
            //             //this.tokenNames.push(token)
            //             this.tokenNames.push({ text: token.sToken, value: token.sToken })
            //         }
            //     })
            // })
        },
        methods: {
            setOK(){
                console.log(12)
                this.queryParam = {
                    iPageNo: 0,
                    iPageSize: 10,
                },
                this.refreshData();
            },
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            ibus(id){
                let data = this.depositList.find((i) => i.value ==id)
                return data&&data.text
            },
            styles(record){
                // if(id==102|| id==2|| id==4){
                //   return true
                // }
                const availableCode = [1,3,103]
                const val = availableCode.includes(record.iBusiType) ? record.iAvailableChange :record.iFrozenChange
                if(val.indexOf('-') > -1){
                    return true
                }
            },
            initChangeVal(record){
                const availableCode = [1,3,103]
                const val = availableCode.includes(record.iBusiType) ? record.iAvailableChange :record.iFrozenChange
                return Number(val)
            },
            droSToken(v){
                console.log(v)
                this.queryParam.sToken = v==0?'':v;
                this.refreshData();
            },
            droType(v){
                console.log(v)
                this.queryParam.iBusiType = v==0?'':v;
                this.refreshData();
            },
            formatDate(date) {
                return `${date.getMonth() + 1}-${date.getDate()}`;
            },
            onConfirm(date) {
                const [start, end] = date;
                this.show = false;
                this.date = `${this.formatDate(start)} ~ ${this.formatDate(end)}`;
                this.queryParam.iCreateTime_Begin = moment(start).startOf('day').valueOf()
                this.queryParam.iCreateTime_End = moment(end).endOf('day').valueOf()
                this.refreshData();
                console.log(date)
            },
        },
        filters:{
            amountZ(item){
                let zn = parseFloat(item.iAvailableAmount) + parseFloat(item.iFrozenAmount)
                return zn.toFixed(8)
            }
        }
    }
</script>

<style lang="less" scoped>
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__right .van-nav-bar__text{
            color: @gpay-color3D7EFF;
            background: #ffffff;
            border-radius: 20px;
            padding: 1px 6px;
            font-size: 12px;
        }
    }
    .ads-list{
        background: #fff;
        position: relative;
        padding-bottom: 8px;
        margin-bottom: 10px;
    }
    .ads-list::after {
        position: absolute;
        box-sizing: border-box;
        content: ' ';
        pointer-events: none;
        right: 16px;
        bottom: 0;
        left: 16px;
        border-bottom: 1px solid #ebedf0;
        -webkit-transform: scaleY(.5);
        transform: scaleY(.5);
    }
    .info-top,.info-cont{
        padding: 0 .28rem;
    }
    .border-top{
        //border-top: 1px solid #e4e5e6;
        padding-top: 12px;
    }
    .border-bm{
        //border-bottom: 1px solid #e4e5e6;
        padding-bottom: 12px;
        margin-bottom: .25rem;
        position: relative;
    }
    .gflex{
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        // -webkit-box-align: center;
        // -webkit-align-items: center;
        // align-items: center;
    }
    .box__hd{
        margin-right: 16px;
        width: 42px;
        height: 42px;
        background: #FFFFFF;
        border-radius: 50%;
        .avatar{
            width: 100%;
            border-radius: 50%;
        }
    }
    .box__bd{
        -webkit-box-flex: 1;
        -webkit-flex: 1;
        flex: 1;
        min-width: 0;
    }
    .box__dec{
        font-size: 14px;
        line-height: 1.4;
        padding: 0;
        margin: 0;
        padding-top: 4px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
    }
    .info-cont{
        .box__title{
            font-size: 12px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #919DAF;
            i{
                color: @gpay-color3D7EFF;
                font-size: .3rem;
                font-style: normal;
                font-family: PingFangSC-Regular;
                margin-left: .26rem;
            }
            span{
                color: #212833;
                font-size: 12px;
                margin-left: .26rem;
            }
        }
        .mc-bnt{
            min-width: 120px;
            background: @gpay-bg3D7EFF;
            border-radius: 24px;
            padding: 3px 18px;
            font-size: 14px;
            font-family: PingFang SC;
            font-weight: 400;
            color: #FFFFFF;
            line-height: 36px;
        }
        .pay-type{
            width: 61px;
            img{
                width: 20px;
                margin-right: 8px;
            }
        }
    }
    .mbmt{
        margin-bottom: 6px;
    }
    .qtright{
        float: right;
        margin-right: 20px;
    }
    /deep/ .van-tabs__nav{
        background: #F3F5FD;
    }
    /deep/.van-tabs__line{
        display: none;
    }
    // /deep/ .van-tabs--line .van-tabs__wrap{
    //     padding-top: .3rem;
    //     height: auto;
    // }
    /deep/ .van-tab--active{
        color: @gpay-color3D7EFF;
    }
    .ads-list-cont{
        //background: #F3F5FD;
        //padding-top: .3rem;
        padding-bottom: 1.1rem;
        top: .9rem;
        position: relative;
    }
    .ads-two-mun{
        color: #212833;
        padding-top: 2px;
        background: #F3F5FD;
    }
    /deep/ .ads-two-mun{
        .van-tab{
            //color: #FEFEFE;
            color: #212833;
            font-size: .24rem;
        }
        .van-tab--disabled{
            color: #c8c9cc;
        }
    }
    /deep/.ads-two-mun .van-tab--active{
        span{
            font-family: PingFang SC;
            font-weight: 400;
            color: #fff;
            background: @gpay-bg3D7EFF;
            border-radius: 15px;
            padding: 1px 14px;
        }
    }
    /deep/ .ads-nav-name .van-tab{
        font-size: .3rem;
        line-height: normal;
    }
    .green{
        color: #4caf50;
    }
    .green::before{
        content: "";
    }
    .red{
        color: red;
    }
    .red::before{
        content: "";
    }
    .right-cont{
        flex:1;
        font-size: 12px;
        text-align: right;
    }
    .color-r{
        color: @gpay-color3D7EFF;
    }
</style>
