<template>
    <div class="app-gapy-user">
        <router-view/>
        <van-nav-bar
            title="忘记密码"
            left-text=""
            class="hed"
            :border="false"
            left-arrow
            @click-left="onClickLeft"
        />
        <!-- <van-row class="flex-wrapper" type="flex" justify="end">
            <van-col class="flex-right">
                忘记密码
            </van-col>
        </van-row> -->
        <div class="login-form-box fort-box">
            <van-cell-group :border="false" class="mlr">
                <div v-if="step==1">
                    <div class="label-text">请输入您的登录账号</div>
                    <van-cell>
                        <van-field
                            class="input-style"
                            v-model="validateForm.username"
                            name="username"
                            placeholder="请输入手机号码"
                            clearable
                            required
                            size="large"
                            :maxlength="11"
                            autocomplete="off"
                            @input="phone"
                            :error-message='errermsg'
                        />
                    </van-cell>
                </div>
                <div v-if="step==2">
                    <div class="label-text">请输入您收到的验证码</div>
                    <van-cell>
                        <van-field
                            class="input-style"
                            v-model="validateForm.code"
                            name="code"
                            placeholder="验证码"
                            clearable
                            required
                            :maxlength="6"
                            size="large"
                            autocomplete="off"
                        />
                    </van-cell>
                </div>
                <div v-if="step==3">
                    <div class="label-text">重置你的密码</div>
                    <van-cell>
                        <van-field
                            class="input-style"
                            v-model="validateForm.password"
                            type ="password"
                            name="password"
                            placeholder="密码"
                            clearable
                            required
                            :maxlength="30"
                            size="large"
                            autocomplete="off"
                        />
                    </van-cell>
                </div>
                <div style="margin: 16px;">
                    <van-button round block type="info" :disabled="errermsg !==''" @click="next(step)">
                        {{ step < 3?'下一步':'重置密码'}}
                    </van-button>
                </div>
            </van-cell-group>
        </div>
        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi = 'iVerifyBusi'
            @ok="applyVerify"
            :need-cellphone="needCellphone"
            :need-email="needEmail"
            :need-google="needGoogle"
            :need-type="false"
            :email-val="sEmail"
            :phone-val="sCellphone"
            area-val="86"
        />
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import {getSMSVerifyCode,retrievePassword,resetPassword} from '@/api/auth'
    import SafeVerifyModal from "@/components/SafeVerifyModal"
    import {regPwd,isMobile} from "@/utils/validate"

    export default {
        name: "userForget",
        components: {
            SafeVerifyModal
        },
        data() {
            return {
                username:'',
                step:1,
                validateForm: {
                    username: '',
                    password: '',
                    code:'',
                    isAgree: false
                },
                iVerifyBusi:null,
                needCellphone:2,
                needEmail:2,
                needGoogle:2,
                values:{},
                sCellphone:'',
                sEmail:'',
                areaCode:'86',
                errermsg: null
            };
        },
        mounted(){
                // this.$refs.safeVerifyModal.visible = true
                // this.needGoogle = 1;
                // this.needEmail = 1;
                // this.needCellphone = 1;
        },
        methods: {
            ...mapActions(['Logout']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            phone(val){
                if(!isMobile(this.validateForm.username)){
                    this.errermsg = '请正确输入正确格式手机号'
                }else{
                    this.errermsg =''
                }
            },
            next(stp){
                const params = {
                    iSource:2,
                    iRetrieveType:1,
                    sCellphone:this.validateForm.username
                }
                if(this.errermsg!="") return
                //if(!isMobile(this.validateForm.username)){
                    //this.$toast("请正确输入正确格式手机号");
                //}
                // if(this.validateForm.username==null||this.validateForm.username==''||this.validateForm.username==undefined){
                //     this.$toast("请正确输入正确格式手机号");
                //     return;
                // }else if(this.validateForm.length < 11){
                //     this.$toast("请正确输入手机号");
                //     return;
                // }
                else{
                    if(stp == 1){
                        this.retrievePwd(params)
                    }
                }
                if(stp == 3){
                    if(!regPwd(this.validateForm.password)){
                        this.$toast("密码需包含数字和大小写字母中至少两种字符的8-10位字符");
                        return
                    }
                    params.sGoogleVerifyCode = this.values.sGoogleVerifyCode
                    params.sSMSVerifyCode = this.values.sSMSVerifyCode
                    params.sEmailVerifyCode = this.values.sEmailVerifyCode
                    params.sPassword = this.validateForm.password
                    params.sCellphone = this.validateForm.username
                    console.log(this.values)
                    this.resetPassword(params)
                }
                //this.step = this.step+1;
            },
            retrievePwd(params){
                retrievePassword(params)
                .then((res)=>{
                    console.log(res)
                    this.needGoogle = res.data.bSwitchGoogleVerify?1:0;
                    this.needEmail = res.data.bSwitchEmailVerify?1:0;
                    this.needCellphone = res.data.bSwitchCellphoneVerify?1:0;
                    this.sCellphone = res.data.sCellphone
                    this.sEmail = res.data.sEmail
                    console.log(this.sCellphone,this.sEmail)
                    this.iVerifyBusi = 4
                    this.$refs.safeVerifyModal.visible = true
                })
            },
            resetPassword(params){
                resetPassword(params)
                    .then((res)=>{
                    this.$message.success('恭喜您，密码重置成功！')
                    this.Logout()
                    })
            },
            applyVerify(codes){// 回调
                this.$refs.safeVerifyModal.visible = false
                this.editPwd = true
                this.values = codes
                this.values.sGoogleVerifyCode = !this.values.sGoogleVerifyCode?'':this.values.sGoogleVerifyCode
                this.step = 3
            },
        },
    }
</script>

<style lang="less" scoped>
    @import url('../../assets/style/userStyle.less');
    .flex-wrapper {
        width: 100%;
        margin-top: 8px;
    }
    /deep/ .van-nav-bar{
        background: #3c7cf9;
        .van-nav-bar__title{
            color: #fff;
        }
    }
    .login-form-box.fort-box{
        margin-top: .46rem;
    }
    /deep/ .van-nav-bar__text{
        color: #fff;
    }
</style>